import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit, ViewEncapsulation } from '@angular/core';

import { SmartComponent } from '../../../../common/cdk/smart-component';

import { CompositionWarehouse } from '../../../composition/domain-api/composition.warehouse';
import { CellTemplateWithContext } from '../../../composition/domain-api/read/definition/cell-template-with-context';
import { CompositionCommandDispatcher } from '../../../composition/domain-api/composition.command-dispatcher';
import { CompositionId } from '../../../composition/domain/composition.id';


@Component({
	selector: 'gui-structure-column-manager',
	template: `

		<ol>
			<li *ngFor="let column of columns"
				(click)="toggleColumn(column)">

				<gui-checkbox [checked]="column.isEnabled()"
							  [disabled]="enabledColumnsCount === 1 && column.isEnabled()">

					<ng-container
							*ngTemplateOutlet="column.viewTemplate;
											context: column.context">
					</ng-container>
				</gui-checkbox>

			</li>
		</ol>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	host: {
		'[class.gui-structure-column-manager]': 'true'
	}
})
export class StructureColumnManagerComponent extends SmartComponent implements OnInit {

	columns: Array<CellTemplateWithContext>;

	enabledColumnsCount: number;

	constructor(private changeDetectorRef: ChangeDetectorRef,
				private compositionId: CompositionId,
				private compositionCommandService: CompositionCommandDispatcher,
				private compositionReadModelService: CompositionWarehouse) {
		super();
	}

	ngOnInit() {

		this.compositionReadModelService
			.onAllColumns(this.compositionId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((columns: Array<CellTemplateWithContext>) => {
				this.columns = columns;
				this.enabledColumnsCount = this.columns
											   .map((c) => +c.isEnabled())
											   .reduce((accumulator, currentValue) => accumulator + currentValue);

				this.changeDetectorRef.detectChanges();
			});
	}

	toggleColumn(column: CellTemplateWithContext): void {
		event.stopPropagation();
		if (column.isEnabled()) {
			this.compositionCommandService.disableColumn(column.getColumnDefinitionId(), this.compositionId);
		} else {
			this.compositionCommandService.enableColumn(column.getColumnDefinitionId(), this.compositionId);
		}
	}
}
