import { DomainEvent } from '@generic-ui/hermes';

import { SchemaId } from '../schema.id';
import { SchemaTheme } from './schema-theme';

export class SchemaThemeSetEvent extends DomainEvent<SchemaId> {

	constructor(schemaId: SchemaId,
				private readonly theme: SchemaTheme) {
		super(schemaId, 'SchemaThemeSetEvent');
	}

	getTheme(): SchemaTheme {
		return this.theme;
	}
}
