import { Command } from '@generic-ui/hermes';

import { StructureId } from '../../../structure.id';
import { CompositionId } from '../../../../../composition/domain/composition.id';
import { ColumnConfig } from '../../../../../composition/domain/column/column.config';


export class InitFieldsCommand extends Command {

	constructor(structureId: StructureId,
				private readonly compositionId: CompositionId,
				private readonly columns: Array<ColumnConfig>) {
		super(structureId, 'InitFieldsCommand');
	}

	getCompositionId(): CompositionId {
		return this.compositionId;
	}

	getColumns(): Array<ColumnConfig> {
		return this.columns;
	}

}
