import { DomainEvent } from '@generic-ui/hermes';
import { CompositionId } from '../../../../../composition/domain/composition.id';
import { FieldId } from '../../field/data-type/field.id';
import { StructureId } from '../../../structure.id';

export class SortOrderSetEvent extends DomainEvent<StructureId> {

	constructor(aggregateId: StructureId,
				private readonly compositionId: CompositionId,
				private readonly directions: Array<{ fieldId: FieldId, direction: boolean }>) {
		super(aggregateId, 'SortOrderSetEvent');
	}

	getCompositionId(): CompositionId {
		return this.compositionId;
	}

	getDirections(): any {
		return this.directions;
	}
}
