import { Injectable, Type } from '@angular/core';

import { AggregateEvent, CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { ChangePagesizeCommand } from './change-pagesize.command';
import { PageChangedEvent } from '../page/page-changed.event';
import { PagesizeChangedEvent } from './pagesize-changed.event';
import { StructureAggregate } from '../../../../lib/structure/domain/structure/structure.aggregate';
import { StructureId } from '../../../../lib/structure/domain/structure.id';


@Injectable()
export class ChangePagesizeCommandHandler implements CommandHandler<StructureAggregate, ChangePagesizeCommand> {

	constructor(protected domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<ChangePagesizeCommand> {
		return ChangePagesizeCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: ChangePagesizeCommand): void {

		const aggregateEvents = aggregate.getEvents();

		(aggregateEvents as any).forEach((event: AggregateEvent<StructureId>) => {
			(event as any).aggregateId = aggregate.getId();
		});

		this.publishAggregateEvents(aggregateEvents, command);
	}

	handleAggregate(structureAggregate: StructureAggregate, command: ChangePagesizeCommand): void {

		const pageSize = command.getPageSize();

		structureAggregate.changePageSize(pageSize);
	}

	private publishAggregateEvents(events: ReadonlyArray<AggregateEvent<StructureId>>, command: ChangePagesizeCommand): void {

		for (let event of events) {
			this.publishAggregateEvent(event, command);
		}
	}

	private publishAggregateEvent(event: AggregateEvent<StructureId>, command: ChangePagesizeCommand): void {

		switch (event.getType()) {
			case 'PageChangedAggregateEvent':

				const pageChangedEvent = new PageChangedEvent(command.getAggregateId());

				this.domainEventPublisher.publish(pageChangedEvent);

				break;

			case 'PagesizeChangedAggregateEvent':

				const pagesizeChangedEvent = new PagesizeChangedEvent(command.getAggregateId());

				this.domainEventPublisher.publish(pagesizeChangedEvent);

				break;

			default:
				break;
		}
	}
}
