import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';

import { DomainEvent, DomainEventBus } from '@generic-ui/hermes';

import { StructureId } from '../../../domain/structure.id';
import { EditemItemValues } from './editem-item.values';
import { StructureSourceItemEditedEvent } from '../../../domain/source/origin/edit/structure.source-item-edited.event';


@Injectable()
export class SourceEventService {

	constructor(private readonly domainEventBus: DomainEventBus) {
	}

	onSourceEdited(structureId: StructureId): Observable<EditemItemValues> {

		return this.domainEventBus
				   .ofEvent(
					   StructureSourceItemEditedEvent as any
				   )
				   .pipe(
					   filter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === structureId.toString()),
					   map((event: StructureSourceItemEditedEvent) => {

						   const afterItem = event.getAfterItem().getData(),
							   beforeItem = event.getBeforeItem().getData();

						   return new EditemItemValues(afterItem, beforeItem);
					   })
				   );
	}
}
