import { FieldId } from './field.id';
import { DataType } from './data-type';
import { OriginItemEntity } from '../../../source/origin/origin-item-entity';
import { FieldMatcherType } from '../matcher/field.matcher.type';
import { FieldMatchers } from '../matcher/field.matchers';

export abstract class BaseField {

	private readonly id: FieldId;

	// TODO
	private readonly accessor: string | any; //(rawData: any) => any;

	private readonly dataType: DataType;

	private readonly accessorMethod: (entity: OriginItemEntity) => any;

	private readonly matchers: FieldMatchers;

	protected constructor(id: FieldId,
						  accessor: any,
						  dataType: DataType,
						  matchers: FieldMatchers) {
		this.id = id;
		this.accessor = accessor;
		this.dataType = dataType;
		this.matchers = matchers;

		if (typeof accessor === 'string') {
			this.accessorMethod = (entity: OriginItemEntity) => entity.getData()[accessor];
		} else if (typeof accessor === 'function') {
			this.accessorMethod = (entity: OriginItemEntity) => accessor(entity.getData());
		}

	}

	getId(): FieldId {
		return this.id;
	}

	getKey(): string {
		return this.getId().getId();
	}

	getDataType(): DataType {
		return this.dataType;
	}

	getAccessor(): string {
		return this.accessor;
	}

	getAccessorMethod(): (entity: OriginItemEntity) => any {
		return this.accessorMethod;
	}

	getMatchers(): FieldMatchers {
		return this.matchers;
	}

	getSearchAccessorMethod(): (entity: OriginItemEntity) => any {

		let matcher: (i: any) => any;

		this.matchers
			.getSearchMatcher()
			.ifPresent((m: FieldMatcherType) => {
				matcher = m;
			});

		if (matcher) {
			return (entity) => {
				const val = this.accessorMethod(entity);
				return matcher(val);
			};
		} else {
			return (entity) => {
				return this.accessorMethod(entity);
			};
		}
	}

	getValue(entity: OriginItemEntity): any {
		return this.accessorMethod(entity);
	}

	getSortValue(entity: OriginItemEntity): any {
		let value = this.accessorMethod(entity);

		this.matchers
			.getSortMatcher()
			.ifPresent((matcher: FieldMatcherType) => {
				value = matcher(value);
			});

		return value;
	}

	getSearchValue(entity: OriginItemEntity): any {
		let value = this.accessorMethod(entity);

		this.matchers
			.getSearchMatcher()
			.ifPresent((matcher: FieldMatcherType) => {
				value = matcher(value);
			});

		return value;
	}


}
