import { Injectable } from '@angular/core';

import { Override } from '../../../../../common/cdk/decorators';

import { PagingCommandDispatcher } from '../../../../../structure/paging/domain-api/paging.command-dispatcher';
import { StructureId } from '../../../domain/structure.id';
import { PagingManagerFactory } from '../../../../../structure/paging/domain/paging.manager-factory';
import { PagingDispatcher } from '../../../../../structure/paging/domain/paging.dispatcher';
import { PagingConfig } from '../../../../../structure/paging/domain-api/paging-config';


@Injectable()
export class LocalPagingCommandDispatcher extends PagingCommandDispatcher {

	constructor(private structureId: StructureId,
				private aggregateFactory: PagingManagerFactory,
				pagingDispatcher: PagingDispatcher) {
		super(pagingDispatcher);
	}

	setDefaultPaging(): void {

		const defaultPaggingAggregate = this.aggregateFactory.createDefault();

		super.setPaging({
			page: defaultPaggingAggregate.getPage(),
			pageSize: defaultPaggingAggregate.getPageSize(),
			pageSizes: defaultPaggingAggregate.getPageSizes()
		}, this.structureId);
	}

	@Override
	setPaging(paging: PagingConfig): void {
		super.setPaging(paging, this.structureId);
	}

	@Override
	changePageSize(pageSize: number): void {
		super.changePageSize(pageSize, this.structureId);
	}

	@Override
	nextPage(): void {
		super.nextPage(this.structureId);
	}

	@Override
	prevPage(): void {
		super.prevPage(this.structureId);
	}

}
