import { Injectable, Type } from '@angular/core';

import { AggregateEvent, CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { CompositionAggregateRepository } from '../../composition.aggregate-repository';
import { CompositionEventConverter } from '../../composition.event-converter';
import { SetCompositionContainerWidthCommand } from './set-composition-container-width.command';
import { CompositionAggregate } from '../../composition.aggregate';
import { CompositionId } from '../../composition.id';


@Injectable()
export class SetCompositionContainerWidthCommandHandler implements CommandHandler<CompositionAggregate, SetCompositionContainerWidthCommand> {

	constructor(private readonly compositionAggregateRepository: CompositionAggregateRepository,
				protected domainEventPublisher: DomainEventPublisher,
				private compositionEventConverter: CompositionEventConverter) {
	}

	forCommand(): Type<SetCompositionContainerWidthCommand> {
		return SetCompositionContainerWidthCommand;
	}

	publishDomainEvents(aggregate: CompositionAggregate, command: SetCompositionContainerWidthCommand): void {
		this.publishAggregateEvents(aggregate.getEvents() as Array<AggregateEvent<CompositionId>>);
	}

	handleAggregate(composition: CompositionAggregate, command: SetCompositionContainerWidthCommand): void {

		const width = command.getWidth();

		composition.setContainerWidth(width);
	}

	private publishAggregateEvents(aggregateEvents: Array<AggregateEvent<CompositionId>>): void {

		const domainEvents = this.compositionEventConverter.convert(aggregateEvents);

		this.domainEventPublisher.publish(domainEvents);
	}

}
