import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { SetRowHeightCommand } from './set-row-height.command';
import { StructureAggregate } from '../../structure.aggregate';
import { RowHeightSetEvent } from './row-height-set.event';


@Injectable()
export class SetRowHeightCommandHandler implements CommandHandler<StructureAggregate, SetRowHeightCommand> {

	constructor(private readonly domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<SetRowHeightCommand> {
		return SetRowHeightCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: SetRowHeightCommand): void {
		this.domainEventPublisher.publish(new RowHeightSetEvent(command.getAggregateId()));
	}

	handleAggregate(structure: StructureAggregate, command: SetRowHeightCommand): void {

		const rowHeight = command.getRowHeight();

		const formation = structure.getVerticalFormation();

		formation.setRowHeight(rowHeight);
	}

}
