import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	Inject,
	NgZone,
	OnDestroy,
	OnInit,
	Optional,
	Renderer2,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';
import { fromEvent, Subject } from 'rxjs';
import { distinctUntilChanged, filter, map, takeUntil, auditTime } from 'rxjs/operators';

import { ResizeDetector } from '@generic-ui/fabric';

import { SmartComponent } from '../../../../common/cdk/smart-component';
import { CellTemplateWithAccessor } from '../../../composition/domain-api/read/definition/cell-template-with-accessor';
import { SchemaRowColoring } from '../../../../schema/domain/coloring/schema-row-coloring';
import { StructureId } from '../../domain/structure.id';
import { StructureCommandDispatcher } from '../../domain-api/structure.command-dispatcher';
import { StructureWarehouse } from '../../domain-api/structure.warehouse';
import { StructureSourceWarehouse } from '../../domain-api/source/structure-source.warehouse';
import { CompositionCommandDispatcher } from '../../../composition/domain-api/composition.command-dispatcher';
import { CompositionWarehouse } from '../../../composition/domain-api/composition.warehouse';
import { StructureFormationWarehouse } from '../../domain-api/formation/structure-formation.warehouse';
import { structureParentComponent } from '../structure-parent-component';
import { StructureReadModelRoot } from '../../domain-api/read/structure.read-model-root';
import { ItemEntity } from '../../domain/source/item.entity';
import { StructureVerticalFormationWarehouse } from '../../domain-api/vertical-formation/structure-vertical-formation.warehouse';


@Component({
	selector: 'gui-structure-container',
	templateUrl: `structure-container.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureContainerComponent extends SmartComponent implements OnInit, AfterViewInit, OnDestroy {

	@ViewChild('sourceCollection', { static: false })
	sourceCollectionRef: ElementRef;

	columns: Array<CellTemplateWithAccessor> = [];

	source: Array<ItemEntity> = [];

	height: number;

	rowColoring: SchemaRowColoring;

	private autoResizeWidthEnabled: boolean = false;

	private scrollObservation$ = new Subject();

	constructor(private readonly elementRef: ElementRef,
				private readonly renderer: Renderer2,
				private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly ngZone: NgZone,
				private readonly structureId: StructureId,
				private readonly structureCommandService: StructureCommandDispatcher,
				private readonly structureReadModelWarehouse: StructureWarehouse,
				private readonly structureVerticalFormationWarehouse: StructureVerticalFormationWarehouse,
				private readonly structureSourceWarehouse: StructureSourceWarehouse,
				private readonly compositionCommandService: CompositionCommandDispatcher,
				private readonly compositionWarehouse: CompositionWarehouse,
				private readonly structureFormationWarehouse: StructureFormationWarehouse,
				private readonly resizeDetector: ResizeDetector,
				@Optional() @Inject(structureParentComponent) private readonly structureParent: any) {
		super();
	}

	ngOnInit() {

		this.structureVerticalFormationWarehouse
			.onContainerHeight(this.structureId)
			.pipe(this.takeUntil())
			.subscribe((containerHeight: number) => {
				this.setContainerHeight(containerHeight);
			});

		this.structureSourceWarehouse
			.onEntities()
			.pipe(
				this.takeUntil()
			)
			.subscribe((source: Array<ItemEntity>) => {
				this.source = source;
				this.changeDetectorRef.detectChanges();
			});

		this.compositionWarehouse
			.onTemplateColumns()
			.pipe(this.takeUntil())
			.subscribe((columns: Array<CellTemplateWithAccessor>) => {
				this.columns = columns;

				this.changeDetectorRef.detectChanges();
			});

		this.compositionWarehouse
			.onResizeWidth()
			.pipe(this.takeUntil())
			.subscribe((enabled: boolean) => {
				this.autoResizeWidthEnabled = enabled;
			});
	}

	ngAfterViewInit() {

		// turn on when structure is ready
		if (this.structureParent) {
			this.resizeDetector
				.observe(this.structureParent.getElementRef().nativeElement)
				.pipe(
					filter(() => this.autoResizeWidthEnabled),
					map((element: HTMLElement) => element.offsetWidth),
					distinctUntilChanged(),
					this.takeUntil()
				)
				.subscribe((width: number) => {
					this.recalculateContainer(width);
				});
		}

		this.compositionWarehouse
			.onContainerWidth()
			.pipe(
				this.takeUntil()
			)
			.subscribe((width: number) => {
				this.renderer.setStyle(this.sourceCollectionRef.nativeElement, 'width', width + 'px');
			});

		this.structureVerticalFormationWarehouse
			.onVerticalScrollEnabled(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((enabled: boolean) => {
				if (enabled) {
					this.enableScrollObservation();
				} else {
					this.disableScrollObservation();
				}
			});

		this.structureReadModelWarehouse
			.onStructure()
			.pipe(
				filter((str: StructureReadModelRoot) => {
					return str.isVerticalScrollEnabled();
				}),
				this.takeUntil()
			)
			.subscribe((structure: StructureReadModelRoot) => {

				const topMargin = structure.getTopMargin(),
					sourceHeight = structure.getSourceHeight();

				this.setSourceHeight(topMargin, sourceHeight);
			});
	}

	ngOnDestroy() {
		super.ngOnDestroy();
		this.resizeDetector.destroy(this.elementRef.nativeElement);
	}

	private setContainerHeight(height: number): void {
		this.height = height;
		this.renderer.setStyle(this.elementRef.nativeElement, 'height', this.height + 'px');
	}

	private setSourceHeight(topMargin: number, sourceHeight: number): void {
		this.renderer.setStyle(this.sourceCollectionRef.nativeElement, 'padding-top', topMargin + 'px');
		this.renderer.setStyle(this.sourceCollectionRef.nativeElement, 'height', sourceHeight + 'px');
	}

	private recalculateContainer(width: number): void {
		if (this.autoResizeWidthEnabled) {
			this.compositionCommandService.setContainerWidth(width);
		}
	}

	private enableScrollObservation(): void {
		this.ngZone
			.runOutsideAngular(() => {

				fromEvent(this.elementRef.nativeElement, 'scroll')
					.pipe(
						// auditTime(10),
						takeUntil(this.scrollObservation$),
						this.takeUntil()
					)
					.subscribe((event: any) => {

						const scrollTop = event.target.scrollTop;

						this.structureCommandService.setScrollPosition(scrollTop);
					});
			});
	}

	private disableScrollObservation(): void {
		this.scrollObservation$.next();
		this.scrollObservation$.complete();
	}

}
