import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { map } from 'rxjs/operators';

import { StructureId } from '../../../domain/structure.id';
import { structureGlobalId } from '../../structure.global-id';


@Injectable()
export class StructureSearchPlaceholderArchive {

	private static readonly PLACEHOLDER = 'Search ...';

	private readonly idToPlaceholder = new Map<string, string>();

	private readonly idToPlaceholder$ = new ReplaySubject<Map<string, string>>();

	next(value: string, structureId: StructureId = structureGlobalId): void {
		this.idToPlaceholder.set(structureId.getId(), value);
		this.idToPlaceholder$.next(this.idToPlaceholder);
	}

	onValue(structureId: StructureId = structureGlobalId): Observable<string> {
		return this.idToPlaceholder$
				   .asObservable()
				   .pipe(
					   map((idToHighlight: Map<string, string>) => {
						   return idToHighlight.get(structureId.getId()) || StructureSearchPlaceholderArchive.PLACEHOLDER;
					   })
				   );
	}

}
