import { RandomStringGenerator } from '@generic-ui/hermes';

import { StructureSorter } from './structure.sorter';
import { SortingConfig } from '../../../domain-api/sorting-config';
import { SorterId } from './sorter.id';
import { FieldId } from '../field/data-type/field.id';
import { OriginItemEntity } from '../../source/origin/origin-item-entity';
import { SortOrder } from '../../../../composition/domain/column/sort/sort-order';
import { TypeField } from '../field/data-type/type.field';


export class SorterCollection {

	private enabled: boolean;

	private multi: boolean;

	private readonly sorters = new Map<string, StructureSorter>();

	constructor(enabled: boolean = false,
				multi: boolean = false) {
		this.enabled = enabled;
		this.multi = multi;
	}

	setConfig(config: SortingConfig): void {

		if (config && config.enabled !== undefined && config.enabled !== null) {
			this.enabled = config.enabled;
		}

		if (config && config.multiSorting !== undefined && config.multiSorting !== null) {
			this.multi = config.multiSorting;
			this.sorters.clear();
		}

	}

	toggle(field: TypeField): void {

		const fieldId = field.getId(),
			sorter = this.sorters.get(fieldId.getId());

		if (sorter) {
			if (sorter.hasDirection()) {
				sorter.changeDirection();
			} else {
				this.delete(fieldId);
			}
		} else {
			this.add(field);
		}

	}

	setSortOrder(field: TypeField, sortOrder: SortOrder): void {

		const fieldId = field.getId(),
			sorter = this.sorters.get(fieldId.getId());

		if (sortOrder === SortOrder.NONE) {
			this.delete(fieldId);
		} else if (sortOrder === SortOrder.ASC || sortOrder === SortOrder.DESC) {

			this.delete(fieldId);

			const direction = sortOrder === SortOrder.ASC;

			this.add(field, direction);
		}
	}

	add(field: TypeField, direction = true): void {
		this.addSorter(field.getId(), new StructureSorter(new SorterId(RandomStringGenerator.generate()), field, direction));
	}

	addSorter(fieldId: FieldId, sorter: StructureSorter): void {

		if (!this.multi) {
			this.sorters.clear();
		}

		this.sorters.set(fieldId.getId(), sorter);
	}

	delete(fieldId: FieldId): void {
		this.sorters.delete(fieldId.getId());
	}

	update(): void {
	}

	// TODO Remove the loop from sorter.sort
	sort(entities: Array<OriginItemEntity>): Array<OriginItemEntity> {
		const sorters = this.getAll();

		let sortedEntities = [...entities];

		for (let sorter of sorters) {
			sortedEntities = sorter.sort(sortedEntities);
		}

		return sortedEntities;
	}

	getAll(): Array<StructureSorter> {

		if (!this.enabled) {
			return [];
		}

		return Array.from(this.sorters)
					.map((arr) => arr[1])
					.sort((sorterOne: StructureSorter, sorterTwo: StructureSorter) => {
						return sorterOne.getRank() - sorterTwo.getRank();
					})
					.reverse();
	}
}
