import { FilterConfig } from '../../../domain-api/filter/filter-config';
import { SearchConfig } from '../../../domain-api/search/search-config';
import { QuickFiltersConfig } from '../../../domain-api/filter/quick-filters.config';

export class FilterSettings {

	private filteringEnabled: boolean = false;

	private searchEnabled: boolean = false;

	private quickFiltersEnabled: boolean = false;

	constructor(filteringEnabled: boolean = false,
				searchEnabled: boolean = false,
				quickFiltersEnabled: boolean = false) {
		this.filteringEnabled = filteringEnabled;
		this.searchEnabled = searchEnabled;
		this.quickFiltersEnabled = quickFiltersEnabled;
	}

	isFilteringEnabled(): boolean {
		return this.filteringEnabled;
	}

	isQuickFilteringEnabled(): boolean {
		return this.quickFiltersEnabled;
	}

	isSearchingEnabled(): boolean {
		return this.searchEnabled;
	}

	setFilterConfig(config: FilterConfig): void {
		if (config && config.enabled !== undefined && config.enabled !== null) {
			this.filteringEnabled = config.enabled;
		}
	}

	setSearchingConfig(config: SearchConfig): void {
		if (config && config.enabled !== undefined && config.enabled !== null) {
			this.searchEnabled = config.enabled;
		}
	}

	setQuickFiltersConfig(config: QuickFiltersConfig): void {
		if (config && config.enabled !== undefined && config.enabled !== null) {
			this.quickFiltersEnabled = config.enabled;
		}
	}

}
