import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { StructureAggregateRepository } from '../../structure-aggregate.repository';
import { StructureToggleFilterCommand } from './structure.toggle-filter.command';
import { FieldId } from '../../field/data-type/field.id';
import { StructureFilterToggledEvent } from './structure.filter-toggled.event';
import { StructureAggregate } from '../../structure.aggregate';


@Injectable()
export class StructureToggleFilterCommandHandler implements CommandHandler<StructureAggregate, StructureToggleFilterCommand> {

	constructor(private readonly structureAggregateRepository: StructureAggregateRepository,
				private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<StructureToggleFilterCommand> {
		return StructureToggleFilterCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: StructureToggleFilterCommand): void {
		this.domainEventPublisher.publish(new StructureFilterToggledEvent(command.getAggregateId()));
	}

	handleAggregate(aggregate: StructureAggregate, command: StructureToggleFilterCommand): void {

		const fieldId: FieldId = command.getFieldId(),
			externalFieldId: string = command.getExternalFilterId(),
			filterValue = command.getFilterValue();

		aggregate.toggleFilter(fieldId, externalFieldId, filterValue);
	}

}
