export class StructureInfoPanelConfig {

	private readonly enabled?: boolean = false;
	private readonly infoDialog?: boolean = true;
	private readonly columnsManager?: boolean = true;
	private readonly sourceSize?: boolean = true;

	constructor(enabled?: boolean,
				infoDialog?: boolean,
				columnsManager?: boolean,
				sourceSize?: boolean) {

		if (this.isNotUndefinedOrNull(enabled)) {
			this.enabled = enabled;
		}

		if (this.isNotUndefinedOrNull(infoDialog)) {
			this.infoDialog = infoDialog;
		}

		if (this.isNotUndefinedOrNull(columnsManager)) {
			this.columnsManager = columnsManager;
		}

		if (this.isNotUndefinedOrNull(sourceSize)) {
			this.sourceSize = sourceSize;
		}

	}

	isEnabled(): boolean {
		return this.enabled;
	}

	isInfoDialogEnabled(): boolean {
		return this.infoDialog;
	}

	isColumnsManagerEnabled(): boolean {
		return this.columnsManager;
	}

	isSourceSizeEnabled(): boolean {
		return this.sourceSize;
	}

	private isNotUndefinedOrNull(config: boolean): boolean {
		return config !== undefined && config !== null;
	}

}
