import { EventEmitter, Input, OnChanges, Output, SimpleChanges } from '@angular/core';

import {
	GuiColumn,
	GuiColumnMenu,
	GuiFiltering, GuiFooterPanel, GuiInfoPanel,
	GuiPaging,
	GuiQuickFilters,
	GuiRowColoring,
	GuiRowDetail,
	GuiSearching,
	GuiSorting,
	GuiSummaries,
	GuiTheme, GuiTitlePanel
} from '../../domain-api/grid.api';
import { ColumnConfig } from '../../../../lib/composition/domain/column/column.config';

import { GridColumnConverter } from './column/grid.column.converter';
import { GridThemeConverter } from './theme/grid.theme.converter';
import { GridRowColoringConverter } from './coloring/grid.row-coloring.converter';
import { ColumnMenuConfig } from '../../../../lib/structure/domain-api/column-menu-config';
import { GridColumnMenuConverter } from './column/menu/grid.column-menu.converter';
import { GridPagingConverter } from './paging/grid.paging.converter';
import { SchemaTheme } from '../../../../schema/domain/theme/schema-theme';
import { RowColoring } from '../../../../schema/domain-api/row-coloring';

export abstract class GridGateway implements OnChanges {

	/**
	 * INPUTS
	 */
	@Input()
	columnHeaderTop: boolean;

	@Input()
	columnHeaderBottom: boolean;

	@Input()
	maxHeight: number;

	@Input()
	width: number;

	@Input()
	rowHeight: number;

	@Input()
	autoResizeWidth: boolean;

	@Input()
	source: Array<any> = [];

	@Input()
	columns: Array<GuiColumn> = [];

	@Input()
	paging: boolean | GuiPaging;

	@Input()
	verticalGrid: boolean;

	@Input()
	horizontalGrid: boolean;

	@Input()
	theme: string | GuiTheme;

	@Input()
	rowColoring: string | GuiRowColoring;

	@Input()
	rowSelecting: boolean;

	@Input()
	loading: boolean;

	@Input()
	virtualScroll: boolean;

	@Input()
	sorting: boolean | GuiSorting;

	@Input()
	searching: boolean | GuiSearching;

	@Input()
	titlePanel: GuiTitlePanel;

	@Input()
	footerPanel: GuiFooterPanel;

	/**
	 * @experimental
	 */
	@Input()
	filtering: boolean | GuiFiltering;

	/**
	 * @experimental
	 */
	@Input()
	quickFilters: boolean | GuiQuickFilters;

	/**
	 * @experimental
	 */
	@Input()
	editMode: boolean;

	/**
	 * @experimental
	 */
	@Input()
	cellEditing: boolean;

	@Input()
	infoPanel: boolean | GuiInfoPanel;

	/**
	 * @experimental
	 */
	@Input()
	summaries: GuiSummaries;

	@Input()
	columnMenu: GuiColumnMenu;

	@Input()
	rowDetail: GuiRowDetail;


	/**
	 * OUTPUTS
	 */
	@Output()
	pageChanged: EventEmitter<number> = new EventEmitter<number>();

	@Output()
	pageSizeChanged: EventEmitter<number> = new EventEmitter<number>();

	@Output()
	itemsSelected: EventEmitter<any> = new EventEmitter();

	@Output()
	columnsChanged: EventEmitter<void> = new EventEmitter();

	@Output()
	containerWidthChanged: EventEmitter<number> = new EventEmitter();

	@Output()
	sourceEdited: EventEmitter<{ after: any, before: any }> = new EventEmitter();

	@Output()
	cellEditEntered: EventEmitter<void> = new EventEmitter();

	@Output()
	cellEditCanceled: EventEmitter<void> = new EventEmitter();

	@Output()
	cellEditSubmitted: EventEmitter<void> = new EventEmitter();

	@Output()
	searchPhraseChanged: EventEmitter<string> = new EventEmitter();

	@Output()
	themeChanged: EventEmitter<GuiTheme> = new EventEmitter();

	@Output()
	horizontalGridChanged: EventEmitter<boolean> = new EventEmitter();

	@Output()
	verticalGridChanged: EventEmitter<boolean> = new EventEmitter();

	@Output()
	rowColoringChanged: EventEmitter<GuiRowColoring> = new EventEmitter();


	/** @internal */
	columnsConfig: Array<ColumnConfig>;

	themeConfig: any; // SchemaTheme;

	rowColoringConfig: any; // RowColoring;

	columnMenuConfig: ColumnMenuConfig;

	private readonly gridColumnConverter: GridColumnConverter = new GridColumnConverter();

	private readonly gridThemeConverter: GridThemeConverter = new GridThemeConverter();

	private readonly gridRowColoringConverter: GridRowColoringConverter = new GridRowColoringConverter();

	private readonly gridColumnMenuConverter: GridColumnMenuConverter = new GridColumnMenuConverter();

	private readonly gridPagingConverter: GridPagingConverter = new GridPagingConverter();

	protected constructor() {
	}

	ngOnChanges(changes: SimpleChanges): void {

		if (changes.columns !== undefined && changes.columns.currentValue !== undefined) {
			this.columnsConfig = this.gridColumnConverter.convert(this.columns);
		}

		if (changes.theme !== undefined && changes.theme.currentValue !== undefined) {
			this.themeConfig = this.gridThemeConverter.convert(this.theme);
		}

		if (changes.rowColoring !== undefined && changes.rowColoring.currentValue !== undefined) {
			this.rowColoringConfig = this.gridRowColoringConverter.convert(this.rowColoring);
		}

		if (changes.columnMenu !== undefined && changes.columnMenu.currentValue !== undefined) {
			this.columnMenuConfig = this.gridColumnMenuConverter.convert(this.columnMenu);
		}

		// TODO
		if (changes.paging !== undefined && changes.paging.currentValue !== undefined) {

			if (typeof this.paging !== 'boolean') {
				this.paging = this.gridPagingConverter.convert(this.paging);
			}
		}
	}

	onPageChange(page: number): void {
		this.pageChanged.emit(page);
	}

	onPageSizeChange(pageSize: number): void {
		this.pageSizeChanged.emit(pageSize);
	}

	onItemSelect(item: any): void {
		this.itemsSelected.emit(item);
	}

	onColumnsChange(): void {
		this.columnsChanged.emit();
	}

	onContainerWidthChange(containerWidth: number): void {
		this.containerWidthChanged.emit(containerWidth);
	}

	onSourceEdit(value: any): void {
		this.sourceEdited.emit(value);
	}

	onCellEditEnter(): void {
		this.cellEditEntered.emit();
	}

	onCellEditSubmit(): void {
		this.cellEditSubmitted.emit();
	}

	onCellEditCancel(): void {
		this.cellEditCanceled.emit();
	}

	onSearchPhrase(value: string): void {
		this.searchPhraseChanged.emit(value);
	}

	onTheme(value: SchemaTheme): void {
		this.themeChanged.emit(this.gridThemeConverter.convertToGuiTheme(value));
	}

	onHorizontalGrid(value: boolean): void {
		this.horizontalGridChanged.emit(value);
	}

	onVerticalGrid(value: boolean): void {
		this.verticalGridChanged.emit(value);
	}

	onRowColoring(value: RowColoring): void {
		this.rowColoringChanged.emit(this.gridRowColoringConverter.convertToGuiRowColoring(value));
	}


}
