import { RandomStringGenerator } from '@generic-ui/hermes';


export class ItemEntity {

	private readonly id: string;

	private readonly data: any;

	private readonly position: number;

	private readonly version: number = 0;

	constructor(data: any,
				position: number,
				id?: string,
				version: number = 0) {
		this.data = data;
		this.position = position;
		this.version = version;
		if (!id) {
			this.id = RandomStringGenerator.generate();
		} else {
			this.id = id;
		}
	}

	getData(): any {
		return this.data;
	}

	getUiId(): string {
		return 'gui' + this.id.replace(/-/g, '');
	}

	getId(): string {
		return this.id;
	}

	getVersion(): number {
		return this.version;
	}

	getPosition(): number {
		return this.position;
	}

	isEven(): boolean {
		return this.position % 2 === 0;
	}

	isOdd(): boolean {
		return this.position % 2 === 1;
	}

	equals(entity: ItemEntity): boolean {
		return this.id === entity.id && this.getVersion() === entity.getVersion();
	}

	equalsById(id: string): boolean {
		return this.id === id;
	}

}
