import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { CompositionId } from '../../composition.id';
import { CompositionResizeWidthSetEvent } from './composition-resize-width-set.event';


export class CompositionResizeWidthSetAggregateEvent extends AggregateEvent<CompositionId> {

	constructor(compositionId: CompositionId) {
		super(compositionId, 'CompositionResizeWidthSetAggregateEvent');
	}

	toDomainEvent(): DomainEvent<CompositionId> {
		return new CompositionResizeWidthSetEvent(this.getAggregateId());
	}
}
