import { Injectable, Type } from '@angular/core';

import { AggregateRepository, CreateAggregateCommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { CompositionAggregateRepository } from '../composition.aggregate-repository';
import { CreateCompositionCommand } from './create-composition.command';
import { CompositionCreatedEvent } from './composition-created.event';
import { CompositionAggregate } from '../composition.aggregate';
import { CompositionId } from '../composition.id';


@Injectable()
export class CreateCompositionCommandHandler implements CreateAggregateCommandHandler<CompositionAggregate, CreateCompositionCommand> {

	constructor(private readonly compositionAggregateRepository: CompositionAggregateRepository,
				private readonly domainEventPublisher: DomainEventPublisher) {
	}

	getAggregateRepository(): AggregateRepository<CompositionId, CompositionAggregate> {
		return this.compositionAggregateRepository;
	}

	forCommand(): Type<CreateCompositionCommand> {
		return CreateCompositionCommand;
	}

	publishDomainEvents(aggregate: CompositionAggregate, command: CreateCompositionCommand): void {
		this.domainEventPublisher.publish(new CompositionCreatedEvent(command.getAggregateId()));
	}
}
