import { AggregateEvent, AggregateId, DomainEvent } from '@generic-ui/hermes';

import { OriginItemEntity } from '../origin-item-entity';
import { StructureId } from '../../../structure.id';
import { StructureSourceItemEditedEvent } from './structure.source-item-edited.event';


export class StructureSourceItemEditedAggregateEvent extends AggregateEvent<StructureId> {

	constructor(aggregateId: AggregateId,
				private readonly beforeItem: OriginItemEntity,
				private readonly afterItem: OriginItemEntity) {
		super(aggregateId, 'StructureSourceItemEditedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new StructureSourceItemEditedEvent(this.getAggregateId(), this.beforeItem, this.afterItem);
	}

	getBeforeItem(): OriginItemEntity {
		return this.beforeItem;
	}

	getAfterItem(): OriginItemEntity {
		return this.afterItem;
	}
}
