import { ChangeDetectionStrategy, Component, ElementRef, ViewChild, ViewEncapsulation } from '@angular/core';

import { EditCommunicationComponent } from '../edit-communication.component';


@Component({
	selector: 'gui-boolean-edit',
	template: `

		<span class="gui-cell-boolean">
			<gui-checkbox #checkbox
						  [checked]="value"
						  [name]="filterFieldName"
						  (changed)="toggle($event)">
			</gui-checkbox>
		</span>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class BooleanEditTemplateComponent extends EditCommunicationComponent<boolean> {

	@ViewChild('checkbox', { read: ElementRef, static: true })
	checkboxRef: ElementRef;

	readonly filterFieldName = 'booleanEdit';

	constructor() {
		super();
	}

	toggle(changed: boolean): void {
		this.valueChanges.emit(changed);
		this.submit();
	}

}
