import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { StructureSourceDomainEventPublisher } from '../../../source/structure.source.domain-event.publisher';
import { StructureSetSummariesEnabledCommand } from './structure.set-summaries-enabled.command';
import { StructureSummariesEnabledSetEvent } from './structure.summaries-enabled-set.event';
import { StructureAggregate } from '../../structure.aggregate';


@Injectable()
export class StructureSetSummariesEnabledCommandHandler implements CommandHandler<StructureAggregate, StructureSetSummariesEnabledCommand> {

	constructor(private readonly structureSourceDomainEventPublisher: StructureSourceDomainEventPublisher,
				private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<StructureSetSummariesEnabledCommand> {
		return StructureSetSummariesEnabledCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: StructureSetSummariesEnabledCommand): void {

		const enabled = command.isEnabled(),
			aggregateEvents = aggregate.getEvents();

		this.domainEventPublisher.publish(new StructureSummariesEnabledSetEvent(command.getAggregateId(), enabled));
		this.structureSourceDomainEventPublisher.publish(aggregateEvents);
	}

	handleAggregate(structure: StructureAggregate, command: StructureSetSummariesEnabledCommand): void {

		const enabled = command.isEnabled();

		structure.setSummariesEnabled(enabled);
	}

}
