import { ChangeDetectionStrategy, Component, ViewEncapsulation } from '@angular/core';
import { FormBuilder } from '@angular/forms';

import { Override } from '../../../../../../common/cdk/decorators';

import { InputEditTemplateComponent } from './input-edit-template.component';


@Component({
	selector: 'gui-string-edit',
	template: `

		<form [formGroup]="filterForm" >
			<input #input [formControlName]="filterFieldName" type="type" class="gui-input" />
		</form>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StringEditTemplateComponent extends InputEditTemplateComponent<string> {

	constructor(fb: FormBuilder) {
		super(fb);
	}

	@Override
	focusField(inputElement: any): void {

		if (this.focus) {
			inputElement.focus();
			inputElement.setSelectionRange(0, inputElement.value.length);
		}
	}
}
