import { AggregateEvent, AggregateId, DomainEvent } from '@generic-ui/hermes';

import { OriginItemEntity } from './origin-item-entity';
import { StructureId } from '../../structure.id';
import { OriginSetEvent } from './set-origin/origin-set.event';

export class StructureOriginChangedAggregateEvent extends AggregateEvent<StructureId> {

	constructor(aggregateId: AggregateId,
				private readonly origin: Array<OriginItemEntity>) {
		super(aggregateId, 'StructureOriginChangedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new OriginSetEvent(this.getAggregateId(), this.origin);
	}

	getOrigin(): Array<OriginItemEntity> {
		return this.origin;
	}
}
