import { ReadModelObject } from '@generic-ui/hermes';

@ReadModelObject
export class Paging {

	constructor(private readonly enabled: boolean,
				private readonly page: number,
				private readonly pageSize: number,
				private readonly pageSizes: Array<number>,
				private readonly pagerTop: boolean,
				private readonly pagerBottom: boolean,
				private readonly isNextDisabled: boolean,
				private readonly isPrevDisabled: boolean,
				private readonly start: number,
				private readonly end: number,
				private readonly sourceSize: number) {
	}

	isEnabled(): boolean {
		return this.enabled;
	}

	getPage(): number {
		return this.page;
	}

	getPageSize(): number {
		return this.pageSize;
	}

	getPageSizes(): Array<number> {
		return this.pageSizes;
	}

	isPagerTop(): boolean {
		return this.pagerTop;
	}

	isPagerBottom(): boolean {
		return this.pagerBottom;
	}

	isNextPageDisabled(): boolean {
		return this.isNextDisabled;
	}

	isPrevPageDisabled(): boolean {
		return this.isPrevDisabled;
	}

	getStart(): number {
		return this.start;
	}

	getEnd(): number {
		return this.end;
	}

	getSourceSize(): number {
		return this.sourceSize;
	}

	calculateVisiblePages(currentPage: number, numberOfVisiblePages: number, page: number): boolean {
		return currentPage - numberOfVisiblePages < page && page < currentPage + numberOfVisiblePages;
	}

	sample(source: Array<any>): Array<any> {

		let start = this.getStart();

		if (start !== 0) {
			start -= 1;
		}

		return source.slice(start, this.getEnd());
	}

}
