import { ItemEntity } from '../../../../domain/source/item.entity';

export class SelectedRowsList {

	constructor(private readonly rowIds: Array<string>) {
	}

	isRowSelected(entity: ItemEntity): boolean {
		return !!(this.rowIds.find((id) => entity.equalsById(id)));
	}

	getIds(): Array<string> {
		return this.rowIds;
	}
}
