import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { distinctUntilChanged, filter, map } from 'rxjs/operators';

import { DomainEvent, DomainEventBus } from '@generic-ui/hermes';

import { Reactive } from '../../../../common/cdk/reactive';
import { StructureId } from '../../domain/structure.id';
import { VerticalFormationReadModel } from './vertical-formation.read-model';
import { StructureCreatedEvent } from '../../domain/structure/create-structure/structure-created.event';
import { VerticalScrollEnabledSetEvent } from '../../domain/structure/vertical-formation/set-enabled/vertical-scroll-enabled-set.event';
import { InMemoryStructureReadStore } from '../../infrastructure/in-memory/feature-api/in-memory-structure.read-store';
import { RowHeightSetEvent } from '../../domain/structure/vertical-formation/set-row-height/row-height-set.event';
import { StructureReadModelRoot } from '../read/structure.read-model-root';
import { SchemaThemeSetEvent } from '../../../../schema/domain/theme/schema-theme-set.event';
import { StructureHeightSetEvent } from '../../domain/structure/vertical-formation/container/set-height/structure-height-set.event';
import { OriginSetEvent } from '../../domain/source/origin/set-origin/origin-set.event';
import { ScrollPositionSetEvent } from '../../domain/structure/vertical-formation/scroll-position/scroll-position-set.event';
import { RowHeightSetBasedOnThemeEvent } from '../../domain/structure/vertical-formation/set-row-height-theme/row-height-set-based-on-theme.event';
import { StructureSearchPhraseSetDomainEvent } from '../../domain/structure/search/set-phrase/structure.search-phrase-set.domain-event';


@Injectable()
export class VerticalFormationRepository extends Reactive {

	private readonly verticalFormation = new Map<string, VerticalFormationReadModel>();

	private readonly verticalFormation$ = new ReplaySubject<Map<string, VerticalFormationReadModel>>();

	constructor(private readonly domainEventBus: DomainEventBus,
				private inMemoryStructureReadStore: InMemoryStructureReadStore) {
		super();

		this.domainEventBus
			.ofEvent(
				StructureCreatedEvent,
				VerticalScrollEnabledSetEvent,
				OriginSetEvent as any,
				StructureHeightSetEvent,
				RowHeightSetEvent,
				SchemaThemeSetEvent as any, // TODO
				RowHeightSetBasedOnThemeEvent,
				ScrollPositionSetEvent,
				StructureSearchPhraseSetDomainEvent
			)
			.pipe(this.takeUntil())
			.subscribe((event: DomainEvent<StructureId>) => {

				const aggregateId = event.getAggregateId();

				const optStructure = this.inMemoryStructureReadStore.getById(aggregateId);

				optStructure.ifPresent((structureReadModelRoot: StructureReadModelRoot) => {

					const verticalFormation = structureReadModelRoot.getVerticalFormation(),
						structureReadModelRootId = structureReadModelRoot.getId();

					this.next(aggregateId, verticalFormation);
				});

			});
	}

	onVerticalScrollEnabled(structureId: StructureId): Observable<boolean> {

		return this.onVerticalFormation(structureId)
				   .pipe(
					   map((v: VerticalFormationReadModel) => v.isEnabled()),
					   distinctUntilChanged()
				   );
	}

	onRowHeight(structureId: StructureId): Observable<number> {

		return this.onVerticalFormation(structureId)
				   .pipe(
					   map((v: VerticalFormationReadModel) => v.getRowHeight())
				   );
	}

	onContainerHeight(structureId: StructureId): Observable<number> {

		return this.onVerticalFormation(structureId)
				   .pipe(
					   map((v: VerticalFormationReadModel) => v.getViewPortHeight())
				   );
	}

	onTopMargin(structureId: StructureId): Observable<number> {

		return this.onVerticalFormation(structureId)
				   .pipe(
					   map((v: VerticalFormationReadModel) => v.getTopMargin())
				   );
	}

	private onVerticalFormation(structureId: StructureId): Observable<VerticalFormationReadModel> {

		return this.verticalFormation$
				   .asObservable()
				   .pipe(
					   filter((idToVerticalFormation: Map<string, VerticalFormationReadModel>) => {

						   const key = structureId.getId();

						   return idToVerticalFormation.has(key);
					   }),
					   map((idToVerticalFormation: Map<string, VerticalFormationReadModel>) => idToVerticalFormation.get(structureId.getId()))
				   );
	}

	private next(structureId: StructureId, verticalFormation: VerticalFormationReadModel): void {
		this.verticalFormation.set(structureId.toString(), verticalFormation);
		this.verticalFormation$.next(this.verticalFormation);
	}


}
