import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { StructureAggregateRepository } from '../../structure-aggregate.repository';
import { StructureSetConfigQuickFilterCommand } from './structure.set-config-quick-filter.command';
import { StructureConfigQuickFilterSetEvent } from './structure.config-quick-filter-set.event';
import { StructureAggregate } from '../../structure.aggregate';


@Injectable()
export class StructureSetConfigQuickFilterCommandHandler implements CommandHandler<StructureAggregate, StructureSetConfigQuickFilterCommand> {

	constructor(private readonly structureAggregateRepository: StructureAggregateRepository,
				private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<StructureSetConfigQuickFilterCommand> {
		return StructureSetConfigQuickFilterCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: StructureSetConfigQuickFilterCommand): void {

		const structureId = command.getAggregateId(),
			quickFiltersConfig = command.getConfig();

		this.domainEventPublisher.publish(new StructureConfigQuickFilterSetEvent(structureId, quickFiltersConfig.enabled));
	}

	handleAggregate(aggregate: StructureAggregate, command: StructureSetConfigQuickFilterCommand): void {

		const quickFiltersConfig = command.getConfig();

		aggregate.setQuickFiltersConfig(quickFiltersConfig);
	}

}
