import { Injectable } from '@angular/core';

import { DomainEventHandler } from '@generic-ui/hermes';

import { StructureSearchingRepository } from './structure.searching.repository';

import { StructureConfigSearchingSetEvent } from '../../../domain/structure/search/config/structure.config-searching-set.event';
import { StructureId } from '../../../domain/structure.id';


@Injectable()
export class StructureConfigSearchingSetEventHandler extends DomainEventHandler<StructureId, StructureConfigSearchingSetEvent> {

	constructor(private structureSearchingRepository: StructureSearchingRepository) {
		super();
	}

	handle(event: StructureConfigSearchingSetEvent): void {

		if (event.ofMessageType('StructureConfigSearchingSetEvent')) {
			this.structureSearchingRepository.setEnabled(event.isEnabled(), event.getAggregateId());
		}

	}

}
