import { Type } from '@angular/core';

import { AggregateEvent, AggregateRoot } from '@generic-ui/hermes';

import { PagingManager } from '../../../../structure/paging/domain/paging.manager';
import { SourceManager } from '../source/source.manager';
import { VerticalFormation } from './vertical-formation/vertical-formation';
import { SummariesManager } from './summaries/summaries.manager';
import { SorterCollection } from './sort/sorter.collection';
import { FilterManager } from './filter/filter.manager';
import { FieldCollection } from './field/field.collection';
import { FormationAggregate } from '../formation/formation-aggregate';
import { StructureId } from '../structure.id';
import { StructureEditSourceItemParams } from '../source/origin/edit/structure.edit-source-item.params';
import { PagingConfig } from '../../../../structure/paging/domain-api/paging-config';
import { OriginItemEntity } from '../source/origin/origin-item-entity';
import { ColumnConfig } from '../../../composition/domain/column/column.config';
import { SortingConfig } from '../../domain-api/sorting-config';
import { FieldId } from './field/data-type/field.id';
import { StructureSorter } from './sort/structure.sorter';
import { FilterConfig } from '../../domain-api/filter/filter-config';
import { QuickFiltersConfig } from '../../domain-api/filter/quick-filters.config';
import { SearchConfig } from '../../domain-api/search/search-config';
import { SearchManager } from './search/search.manager';
import { StructurePreparedEntitiesSetAggregateEvent } from '../source/prepared/structure.prepared-entities-set.aggregate-event';
import { SortOrder } from '../../../composition/domain/column/sort/sort-order';
import { FieldsInitedAggregateEvent } from './field/init/fields-inited.aggregate-event';
import { SchemaTheme } from '../../../../schema/domain/theme/schema-theme';
import { SchemaAggregate } from '../../../../schema/domain/schema.aggregate';
import { StructureCreatedAggregateEvent } from './create-structure/structure-created.aggregate-event';


export class StructureAggregate extends AggregateRoot<StructureId> {

	private formationAggregate: FormationAggregate;

	private pagingManager: PagingManager;

	private sourceManager: SourceManager;

	private sorterCollection: SorterCollection;

	private filterManager: FilterManager;

	private searchManager: SearchManager;

	private verticalFormation: VerticalFormation;

	private fieldCollection: FieldCollection;

	private summariesManager: SummariesManager;

	constructor(structureId: StructureId,
				pagingAggregate: PagingManager,
				formationAggregate: FormationAggregate,
				sourceManager: SourceManager,
				verticalFormation: VerticalFormation,
				summariesManager: SummariesManager,
				sorterCollection: SorterCollection,
				filterCollection: FilterManager,
				searchManager: SearchManager,
				fieldCollection: FieldCollection) {
		super(structureId, 'StructureAggregate');
		this.pagingManager = pagingAggregate;
		this.formationAggregate = formationAggregate;
		this.sourceManager = sourceManager;
		this.verticalFormation = verticalFormation;
		this.summariesManager = summariesManager;
		this.sorterCollection = sorterCollection;
		this.filterManager = filterCollection;
		this.searchManager = searchManager;
		this.fieldCollection = fieldCollection;
	}

	createEvent(): Type<AggregateEvent<StructureId>> {
		return StructureCreatedAggregateEvent;
	}

	init(): void {
		this.initTheme();
	}

	setVerticalFormationEnabled(enabled: boolean): void {
		this.verticalFormation.setEnabled(enabled);
		this.calculateSource();
	}

	getVerticalFormation(): VerticalFormation {
		return this.verticalFormation;
	}

	setSummariesEnabled(enabled: boolean): ReadonlyArray<AggregateEvent<StructureId>> {

		this.summariesManager.setEnabled(enabled);

		this.calculateSource();

		return this.getEvents();
	}

	setOrigin(items: Array<any>): ReadonlyArray<AggregateEvent<StructureId>> {

		this.addEvent(
			this.sourceManager.setOrigin(items, this.getId())
		);

		this.calculateSource();

		return this.getEvents();
	}

	editItem(editParams: StructureEditSourceItemParams): ReadonlyArray<AggregateEvent<StructureId>> {

		const itemId = editParams.getItemId(),
			fieldId = editParams.getColumnFieldId(),
			field = this.fieldCollection.getField(fieldId as any), // as FieldId
			value = editParams.getValue();

		const editItemEvents = this.sourceManager.editOriginItem(itemId, value, field, this.getId());

		editItemEvents.forEach((e) => this.addEvent(e));

		this.calculateSource();

		return this.getEvents();
	}

	setHeight(height: number): void {

		this.verticalFormation.setViewportHeight(height);

		this.calculateSourceBasedOnVirtualScroll();
	}

	setTheme(theme: SchemaTheme): void {

		// #1707 Move to SchemeAggregate
		if (theme === SchemaTheme.MATERIAL) {
			this.getVerticalFormation().setRowHeight(52);
		}

		if (theme === SchemaTheme.GENERIC) {
			this.getVerticalFormation().setRowHeight(42);
		}

		if (theme === SchemaTheme.FABRIC) {
			this.getVerticalFormation().setRowHeight(36);
		}

		if (theme === SchemaTheme.LIGHT) {
			this.getVerticalFormation().setRowHeight(56);
		}

		if (theme === SchemaTheme.DARK) {
			this.getVerticalFormation().setRowHeight(38);
		}
	}

	setFormation(formation: FormationAggregate): void {
		this.formationAggregate = formation;
	}

	setScrollPosition(position: number): void {
		this.verticalFormation.setScrollPosition(position);
		this.calculateSourceBasedOnVirtualScroll();
	}

	getFormation(): FormationAggregate {
		return this.formationAggregate;
	}

	getPaging(): PagingManager {
		return this.pagingManager;
	}

	changePaging(config: PagingConfig) {
		this.pagingManager.change(config);
		this.calculateSource();
	}

	setPaging(paging: PagingManager): void {
		this.pagingManager = paging;
		this.calculateSource();
	}

	nextPage(): void {

		this.pagingManager.nextPage();

		this.calculateSource();
	}

	prevPage(): void {
		this.pagingManager.prevPage();
		this.calculateSource();
	}

	changePageSize(pageSize: number): ReadonlyArray<AggregateEvent<StructureId>> {

		const pagingEvents = this.pagingManager.changePageSize(pageSize);

		// TODO
		pagingEvents.forEach((event) => {
			this.addEvent(event);
		});

		this.calculateSource();

		return this.getEvents();
	}

	getEntities(): Array<OriginItemEntity> {
		return this.sourceManager.getSlicedEntities();
	}

	getSource(): SourceManager {
		return this.sourceManager;
	}

	createFields(columns: Array<ColumnConfig>): ReadonlyArray<AggregateEvent<StructureId>> {
		this.fieldCollection.initFields(columns);

		const typeFields = this.fieldCollection.getAllFields();

		this.addEvent(
			new FieldsInitedAggregateEvent(this.getId(), columns, typeFields)
		);

		return this.getEvents();
	}

	setSortingConfig(config: SortingConfig): void {
		this.sorterCollection.setConfig(config);
	}

	toggleSort(fieldId: FieldId): Array<StructureSorter> {

		const field = this.fieldCollection.getField(fieldId);

		this.sorterCollection.toggle(field);

		this.calculateSource();

		return this.sorterCollection.getAll();
	}

	setSortOrder(fieldId: FieldId, sortOrder: SortOrder): Array<StructureSorter> {

		const field = this.fieldCollection.getField(fieldId);

		this.sorterCollection.setSortOrder(field, sortOrder);

		this.calculateSource();

		return this.sorterCollection.getAll();
	}

	setFilterConfig(config: FilterConfig): void {
		this.filterManager.getSettings().setFilterConfig(config);
	}

	setSearchingConfig(config: SearchConfig): void {
		this.filterManager.getSettings().setSearchingConfig(config);
	}

	setQuickFiltersConfig(config: QuickFiltersConfig): void {
		this.filterManager.getSettings().setQuickFiltersConfig(config);
	}

	toggleFilter(fieldId: FieldId, externalFilterId: string, filterValue: string): Array<AggregateEvent<StructureId>> {

		const field = this.fieldCollection.getField(fieldId);

		// Field not found
		// TODO throw aggregate event
		if (field === undefined) {
			return [];
		}

		this.filterManager.addFilter(field, externalFilterId, filterValue);

		this.calculateSource();

		return [];
	}

	addSearchPhrase(phrase: string): Array<AggregateEvent<StructureId>> {

		const fields = this.fieldCollection.getAllFields();

		this.searchManager.addSearchPhrase(fields, phrase);

		this.calculateSource();

		return [];
	}

	removeSearchPhrase(): Array<AggregateEvent<StructureId>> {

		this.searchManager.removeSearchFilters();

		this.calculateSource();

		return [];
	}

	private calculateSource(): void {

		if (!this.sourceManager.getOrigin() || !this.pagingManager) {
			return;
		}

		// TODO Refactor
		this.sourceManager.setEntities(this.sourceManager.getOrigin());

		// filtering
		this.sourceManager.setEntities(this.filterManager.filter(this.sourceManager.getEntities()));

		// search
		const searchedEntities = this.searchManager.search(this.sourceManager.getEntities());
		this.sourceManager.setEntities(searchedEntities);

		// sorting
		const ents = this.sorterCollection.sort(this.sourceManager.getEntities());
		this.sourceManager.setEntities(ents);

		// calculate filteredEntities
		this.sourceManager.setPreparedEntities();

		this.pagingManager.setSourceSize(this.sourceManager.getPreparedEntities().length);

		this.addEvent(new StructurePreparedEntitiesSetAggregateEvent(this.getId(), ents));

		// summaries
		const summariesEvents = this.summariesManager.calculate(
			this.fieldCollection.getAllFields(),
			this.sourceManager.getEntities()
		);

		summariesEvents.forEach((ae: AggregateEvent<StructureId>) => {
			this.addEvent(ae);
		});

		// paging
		this.sourceManager.setEntities(this.pagingManager.sample(this.sourceManager.getEntities()));

		this.verticalFormation.setSourceSize(this.sourceManager.getEntities().length);

		this.calculateSourceBasedOnVirtualScroll();
	}

	private calculateSourceBasedOnVirtualScroll(): void {

		if (this.verticalFormation.isEnabled()) {

			const range = this.verticalFormation.getRange();

			this.sourceManager.setSlicedEntities(
				this.sourceManager.getEntities().slice(range.getStart(), range.getEnd())
			);
		} else {
			this.sourceManager.setSlicedEntities(
				this.sourceManager.getEntities()
			);
		}
	}

	private initTheme() {

		// TODO
		const theme = SchemaAggregate.DEFAULT_THEME;

		this.setTheme(theme);
	}

}
