import { Injectable } from '@angular/core';

import { PagingDispatcher } from '../domain/paging.dispatcher';
import { StructureId } from '../../../lib/structure/domain/structure.id';
import { structureGlobalId } from '../../../lib/structure/domain-api/structure.global-id';
import { PagingConfig } from './paging-config';


@Injectable()
export class PagingCommandDispatcher {

	constructor(private pagingDispatcher: PagingDispatcher) {
	}

	enable(structureId: StructureId = structureGlobalId): void {
		this.pagingDispatcher.setPaging(structureId, { enabled: true });
	}

	disable(structureId: StructureId = structureGlobalId): void {
		this.pagingDispatcher.setPaging(structureId, { enabled: false });
	}

	setPaging(paging: PagingConfig, structureId: StructureId = structureGlobalId): void {
		this.pagingDispatcher.setPaging(structureId, paging);
	}

	changePageSize(pageSize: number, structureId: StructureId = structureGlobalId): void {
		this.pagingDispatcher.changePageSize(structureId, pageSize);
	}

	nextPage(structureId: StructureId = structureGlobalId): void {
		this.pagingDispatcher.nextPage(structureId);
	}

	prevPage(structureId: StructureId = structureGlobalId): void {
		this.pagingDispatcher.prevPage(structureId);
	}

	goToPage(pageNumber: number, currentPage: number) {

		if (currentPage < pageNumber) {
			while (currentPage !== pageNumber) {
				this.nextPage();
				currentPage += 1;
			}
		} else {
			while (currentPage !== pageNumber) {
				this.prevPage();
				currentPage -= 1;
			}
		}
	}

	changePagerTop(enabled: boolean, structureId: StructureId = structureGlobalId): void {
		this.pagingDispatcher.setPaging(structureId, { pagerTop: enabled });
	}

	changePagerBottom(enabled: boolean, structureId: StructureId = structureGlobalId): void {
		this.pagingDispatcher.setPaging(structureId, { pagerBottom: enabled });
	}
}
