import { ChangeDetectionStrategy, Component, EventEmitter, Output, ViewEncapsulation } from '@angular/core';

import { SmartComponent } from '../../../../../common/cdk/smart-component';

import { CompositionWarehouse } from '../../../../composition/domain-api/composition.warehouse';
import { CellTemplateWithContext } from '../../../../composition/domain-api/read/definition/cell-template-with-context';

@Component({
	selector: 'gui-structure-column-selector',
	template: `

		<gui-select
				[options]="options"
				(optionChanged)="onSelectChange($event)">
		</gui-select>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureColumnSelectorComponent extends SmartComponent {

	@Output()
	columnSelected = new EventEmitter<string>();

	columns: Array<CellTemplateWithContext> = [];

	options: Array<string> = [];

	constructor(private readonly compositionWarehouse: CompositionWarehouse) {
		super();
	}

	ngOnInit() {
		this.compositionWarehouse
			.onHeaderColumns()
			.pipe(this.takeUntil())
			.subscribe((columns: Array<CellTemplateWithContext>) => {
				this.columns = columns;

				this.options = columns.map((column: CellTemplateWithContext) => column.getColumnDefinitionId().toString());
			});
	}

	onSelectChange(columnId: string): void {
		this.columnSelected.emit(columnId);
	}
}
