import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { distinctUntilChanged, map } from 'rxjs/operators';

import { Reactive } from '../../../../../common/cdk/reactive';

import { StructureId } from '../../../domain/structure.id';
import { SummariesManager } from '../../../domain/structure/summaries/summaries.manager';


@Injectable()
export class StructureSummariesRepository extends Reactive {

	private enabled = new Map<string, boolean>();

	private enabled$ = new ReplaySubject<Map<string, boolean>>();

	constructor() {
		super();
	}

	init(structureId: StructureId): void {
		this.setEnabled(SummariesManager.DEFAULT_ENABLED, structureId);
	}

	setEnabled(enabled: boolean, structureId: StructureId): void {
		this.next(enabled, structureId);
	}

	onSummariesEnabled(structureId: StructureId): Observable<boolean> {

		return this.enabled$
				   .asObservable()
				   .pipe(
					   this.takeUntil(),
					   map((map: Map<string, boolean>) => {
						   return map.get(structureId.toString());
					   }),
					   distinctUntilChanged()
				   );
	}

	private next(enabled: boolean, structureId: StructureId): void {
		this.enabled.set(structureId.toString(), enabled);
		this.enabled$.next(this.enabled);
	}

}
