import { StructureFilter } from './structure.filter';
import { FilterId } from './filter.id';
import { OriginItemEntity } from '../../source/origin/origin-item-entity';
import { FilterSettings } from './filter.settings';
import { TypeField } from '../field/data-type/type.field';


export class FilterManager {

	private readonly filterSettings = new FilterSettings();

	private readonly filters = new Map<string, StructureFilter>();

	getAll(): Array<StructureFilter> {
		return Array.from(this.filters)
					.map((arr) => arr[1]);
	}

	getSettings(): FilterSettings {
		return this.filterSettings;
	}

	addFilter(field: TypeField, externalFilterId: string, filterValue: string): void {

		const filterId = new FilterId(field, externalFilterId),
			filter = new StructureFilter(filterId, field, filterValue);

		if (filterValue === '') {
			this.filters.delete(filterId.getId());
		} else {
			this.filters.set(filterId.getId(), filter);
		}
	}

	filter(entities: Array<OriginItemEntity>): Array<OriginItemEntity> {
		const filters = this.getAll();

		let filteredEntities = [...entities];

		for (let filter of filters) {
			filteredEntities = filter.filter(filteredEntities);
		}

		return filteredEntities;
	}

}
