import { ColumnConfig } from '../column.config';
import { ColumnField } from '../field/column-field';

export class ColumnParams {

	constructor(private readonly column: ColumnConfig,
				private readonly field: ColumnField) {
	}

	getColumn(): ColumnConfig {
		return this.column;
	}

	getField(): ColumnField {
		return this.field;
	}

}
