import { AggregateEvent } from '@generic-ui/hermes';

import { ActiveColumnEntity } from './active-column.entity';
import { ColumnWidthCollection } from '../column-width/column-width-collection';
import { ColumnConfig } from '../column.config';
import { ChangeSortParams } from '../sort/change-sort.params';
import { SortOrder } from '../sort/sort-order';
import { CompositionId } from '../../composition.id';
import { CompositionChangeSortStatusAggregateEvent } from '../sort/composition-change-sort-status.aggregate-event';
import { ColumnId } from '../column.id';
import { ColumnWidth } from '../column-width/column-width';

export class ActiveColumnContainer {

	private readonly MIN_COLUMN_WIDTH = 50;

	private columns: Array<ActiveColumnEntity>;

	/**
	 * Columns taken from user config.
	 * This should not be modified and it should be used as a reference point.
	 */
	private columnWidthCollection: ColumnWidthCollection = new ColumnWidthCollection(this.MIN_COLUMN_WIDTH);

	constructor(private compositionId: CompositionId,
				columns: Array<ActiveColumnEntity> = [],
				gridWidth?: number,
				columnConfigs?: Array<ColumnConfig>) {
		this.columns = columns;

		if (gridWidth && columnConfigs) {
			this.columnWidthCollection = new ColumnWidthCollection(this.MIN_COLUMN_WIDTH, columnConfigs, gridWidth);
		}
	}

	getColumnWidths(): Array<ColumnWidth> {
		return this.columnWidthCollection.getColumnWidths();
	}

	// REMOVE
	getColumns(): Array<ActiveColumnEntity> {
		return this.columns;
	}

	setWidth(width: number): void {
		this.columnWidthCollection.setWidth(width);
	}

	addColumn(column: ActiveColumnEntity, index: number): void {
		this.columns.splice(index, 0, column);
	}

	removeColumn(index: number): void {
		this.columns.splice(index, 1);
	}

	setColumns(columns: Array<ActiveColumnEntity>, columnConfigs: Array<ColumnConfig>) {

		this.columns = columns;

		this.columnWidthCollection.setColumns(columnConfigs);

		const updatedWidths = this.columnWidthCollection.getWidths();

		this.columns.forEach((column: ActiveColumnEntity, index: number) => {
			column.setWidth(updatedWidths[index]);
		});

		// TODO
		//this.addEvent(new ColumnsSetAggregateEvent(this.getId(), columns));
	}

	changeSort(sortParams: Array<ChangeSortParams>): AggregateEvent<CompositionId> {

		this.columns.forEach((column: ActiveColumnEntity) => {
			column.setSortStatus(SortOrder.NONE);
		});

		for (let param of sortParams) {

			const fieldId = param.fieldId,
				direction = param.direction,
				sortStatus = direction ? SortOrder.ASC : SortOrder.DESC;

			const columns = this.columns.filter((column: ActiveColumnEntity) => {
				return column.getField().getId().getId() === fieldId.getId();
			});

			if (columns.length > 0) {
				columns.forEach((column: ActiveColumnEntity) => {

					column.setSortStatus(sortStatus);
				});
			}
		}

		return new CompositionChangeSortStatusAggregateEvent(this.getCompositionId(), this.columns);
	}

	moveLeft(columnId: ColumnId): void {

		const index = this.findColumnIndex(columnId);

		this.move(index, index - 1);
	}

	moveRight(columnId: ColumnId): void {

		const index = this.findColumnIndex(columnId);

		this.move(index, index + 1);
	}

	getMinColumnWidth(): number {
		return this.MIN_COLUMN_WIDTH;
	}

	private move(fromIndex: number, toIndex: number): void {

		if (!this.validateMoveIndex(fromIndex) || !this.validateMoveIndex(toIndex)) {
			return;
		}

		const column = this.columns[fromIndex];

		this.columns[fromIndex] = this.columns[toIndex];
		this.columns[toIndex] = column;
	}

	private validateMoveIndex(index: number): boolean {
		return index >= 0;
	}

	private getCompositionId(): CompositionId {
		return this.compositionId;
	}

	private findColumnIndex(columnId: ColumnId): number {
		return this.columns.findIndex(c => c.getId().equals(columnId));
	}

}
