import { FieldFactory } from './data-type/field.factory';
import { FieldId } from './data-type/field.id';

import { ColumnConfig } from '../../../../composition/domain/column/column.config';
import { TypeField } from './data-type/type.field';


export class FieldCollection {

	private readonly fields = new Map<string, TypeField>();

	constructor(private readonly fieldFactory: FieldFactory) {
	}

	getField(fieldId: FieldId): TypeField {

		if (fieldId === null) {
			return this.getAllFields()[0];
		}

		return this.fields.get(fieldId.getId());
	}

	getAllFieldIds(): Array<string> {
		return Array.from(this.fields.keys());
	}

	getAllFields(): Array<TypeField> {
		return Array.from(this.fields.values());
	}

	initFields(columns: Array<ColumnConfig>): void {

		if (!columns) {
			return;
		}

		columns.forEach((column: ColumnConfig) => {

			const field = this.fieldFactory.create(column);

			const typeField = new TypeField(field);

			this.addField(typeField);
		});
	}

	addFields(fields: Array<TypeField>): void {
		for (let field of fields) {
			this.addField(field);
		}
	}

	private addField(field: TypeField): void {
		this.fields.set(field.getId().toString(), field);
	}
}
