import { Component, Inject, OnInit, ChangeDetectionStrategy, ViewEncapsulation } from '@angular/core';
import { SafeHtml, DomSanitizer } from '@angular/platform-browser';

import { structureRowDetailViewItem, structureRowDetailViewTemplate } from './structure.row-detail-view.tokens';


@Component({
	template: `
		
		<div [innerHTML]="safeHTML"></div>
		
	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureRowDetailViewComponent implements OnInit {

	selectedRowValues: Array<any>;

	safeHTML: SafeHtml;

	constructor(@Inject(structureRowDetailViewItem) private item: any,
				@Inject(structureRowDetailViewTemplate) private template: any,
				private readonly sanitizer: DomSanitizer) {

		this.safeHTML = this.sanitizer.bypassSecurityTrustHtml(this.template(this.item));
	}

	ngOnInit(): void {
		this.selectedRowValues = this.item;
	}

}
