import { Injectable } from '@angular/core';

import { DomainEventHandler } from '@generic-ui/hermes';

import { StructureSearchPhraseRepository } from './structure.search-phrase.repository';
import { StructureSearchPhraseSetDomainEvent } from '../../../domain/structure/search/set-phrase/structure.search-phrase-set.domain-event';
import { StructureId } from '../../../domain/structure.id';


@Injectable()
export class StructureSearchPhraseSetEventHandler extends DomainEventHandler<StructureId, StructureSearchPhraseSetDomainEvent> {

	constructor(private structureSearchPhraseRepository: StructureSearchPhraseRepository) {
		super();
	}

	handle(event: StructureSearchPhraseSetDomainEvent): void {

		if (event.ofMessageType('StructureSearchPhraseSetDomainEvent')) {
			this.structureSearchPhraseRepository.setPhrase(event.getPhrase(), event.getAggregateId());
		}

	}

}
