import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { SetSourceLoadingEvent } from './set-source-loading.event';
import { SourceSetLoadingCommand } from './source-set-loading.command';
import { StructureAggregate } from '../../structure/structure.aggregate';


@Injectable()
export class SourceSetLoadingCommandHandler implements CommandHandler<StructureAggregate, SourceSetLoadingCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<SourceSetLoadingCommand> {
		return SourceSetLoadingCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: SourceSetLoadingCommand): void {
		this.domainEventPublisher.publish(new SetSourceLoadingEvent(command.getAggregateId()));
	}

	handleAggregate(aggregate: StructureAggregate, command: SourceSetLoadingCommand): void {

		const enabled = command.isEnabled(),
			source = aggregate.getSource();

		source.setLoading(enabled);
	}

}
