import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';
import { StructureId } from '../../../../lib/structure/domain/structure.id';
import { PageChangedEvent } from './page-changed.event';


export class PageChangedAggregateEvent extends AggregateEvent<StructureId> {

	constructor(structureId: StructureId) {
		super(structureId, 'PageChangedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new PageChangedEvent(this.getAggregateId());
	}

}
