import { CellView } from './cell-view';
import { DataType } from '../../../structure/domain/structure/field/data-type/data-type';
import { CellValue } from '../../domain-api/read/definition/cell-value';

export class ViewEntity {

	public static fromDataType(dataType: DataType): ViewEntity {

		if (dataType === DataType.DATE) {
			return new ViewEntity(CellView.DATE);
		} else {
			return new ViewEntity(CellView.TEXT);
		}

	}

	private readonly view: CellView;

	private readonly templateFunction?: (entity: any) => string = (cellValue: any) => cellValue;

	constructor(cellView: CellView | any) {

		if (typeof cellView === 'function') {
			this.view = CellView.FUNCTION;
			this.templateFunction = cellView;
		} else {
			this.view = cellView;
		}
	}

	getCellView(): CellView {
		return this.view;
	}

	getTemplateFunction(): any {
		return this.templateFunction;
	}

}
