import { Command } from '@generic-ui/hermes';

import { SchemaRowColoring } from './schema-row-coloring';
import { SchemaId } from '../schema.id';


export class SetRowColoringCommand extends Command {

	constructor(schemaId: SchemaId,
				private readonly coloring: SchemaRowColoring) {
		super(schemaId, 'SetRowColoringCommand');
	}

	getColoring(): SchemaRowColoring {
		return this.coloring;
	}

}
