import { AggregateEvent, AggregateId, DomainEvent } from '@generic-ui/hermes';

import { OriginItemEntity } from '../origin/origin-item-entity';
import { StructureId } from '../../structure.id';
import { StructurePreparedEntitiesSetEvent } from './structure.prepared-entities-set.event';


export class StructurePreparedEntitiesSetAggregateEvent extends AggregateEvent<StructureId> {

	constructor(aggregateId: AggregateId,
				private readonly preparedItems: Array<OriginItemEntity>) {
		super(aggregateId, 'StructurePreparedEntitiesSetAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new StructurePreparedEntitiesSetEvent(this.getAggregateId(), this.preparedItems);
	}

	getPreparedItems(): Array<OriginItemEntity> {
		return this.preparedItems;
	}

}
