import { AggregateEvent, AggregateId, DomainEvent } from '@generic-ui/hermes';

import { CompositionId } from '../../composition.id';
import { CompositionChangeSortStatusEvent } from './composition-change-sort-status.event';
import { ActiveColumnEntity } from '../active/active-column.entity';


export class CompositionChangeSortStatusAggregateEvent extends AggregateEvent<CompositionId> {

	constructor(aggregateId: AggregateId,
				private readonly activeColumns: Array<ActiveColumnEntity>) {
		super(aggregateId, 'CompositionChangeSortStatusAggregateEvent');
	}

	toDomainEvent(): DomainEvent<CompositionId> {
		return new CompositionChangeSortStatusEvent(this.getAggregateId(), this.activeColumns);
	}

}
