import { GenericBuilder } from '../../../common/cdk/generic-builder';
import { PagingDefinition } from '../../../structure/paging/feature/paging-definition';

export class StructureDefinitionBuilder extends GenericBuilder<StructureDefinition> {

	private border: boolean = true;

	constructor(private headerEnabled: boolean,
				private bottomPaging: PagingDefinition,
				private topPaging: PagingDefinition) {
		super();
	}

	protected buildObject(): StructureDefinition {
		return new StructureDefinition(
			this.headerEnabled,
			this.bottomPaging,
			this.topPaging,
			this.border
		);
	}

	withHeader(enabled: boolean): StructureDefinitionBuilder {
		this.headerEnabled = enabled;
		return this;
	}

	withBottomPaging(paging: PagingDefinition): StructureDefinitionBuilder {
		this.bottomPaging = paging;
		return this;
	}

	withTopPaging(paging: PagingDefinition): StructureDefinitionBuilder {
		this.topPaging = paging;
		return this;
	}

	withBorder(enabled: boolean): StructureDefinitionBuilder {
		this.border = enabled;
		return this;
	}

}

export class StructureDefinitionDefaultBuilder extends StructureDefinitionBuilder {

	private static readonly defaultHeaderEnabled: boolean = true;
	private static readonly defaultBottomPaging: PagingDefinition = new PagingDefinition.DefaultBuilder().build();
	private static readonly defaultTopPaging: PagingDefinition = new PagingDefinition.DefaultBuilder().build();

	constructor() {
		super(
			StructureDefinitionDefaultBuilder.defaultHeaderEnabled,
			StructureDefinitionDefaultBuilder.defaultBottomPaging,
			StructureDefinitionDefaultBuilder.defaultTopPaging
		);
	}
}

export class StructureDefinition {

	private readonly headerEnabled: boolean;

	private readonly bottomPaging: PagingDefinition;

	private readonly topPaging: PagingDefinition;

	private readonly border: boolean;

	constructor(headerEnabled: boolean,
				bottomPaging: PagingDefinition,
				topPaging: PagingDefinition,
				border: boolean) {
		this.headerEnabled = headerEnabled;
		this.bottomPaging = bottomPaging;
		this.topPaging = topPaging;
		this.border = border;
	}

	static Builder = StructureDefinitionBuilder;

	static DefaultBuilder = StructureDefinitionDefaultBuilder;

	isHeaderEnabled(): boolean {
		return this.headerEnabled;
	}

	isBorderEnabled(): boolean {
		return this.border;
	}

	getBottomPaging(): PagingDefinition {
		return this.bottomPaging;
	}

	getTopPaging(): PagingDefinition {
		return this.topPaging;
	}

}
