import { DomainEvent } from '@generic-ui/hermes';

import { SchemaId } from '../../schema.id';

export class SchemaVerticalGridSetEvent extends DomainEvent<SchemaId> {

	constructor(schemaId: SchemaId,
				private readonly verticalGrid: boolean) {
		super(schemaId, 'SchemaVerticalGridSetEvent');
	}

	getVerticalGrid(): boolean {
		return this.verticalGrid;
	}
}
