import { SorterId } from './sorter.id';
import { OriginItemEntity } from '../../source/origin/origin-item-entity';
import { TypeField } from '../field/data-type/type.field';

export class StructureSorter {

	private readonly sorterId: SorterId;

	private columnId: string;

	private field: TypeField;

	private rank: number = 1;

	private direction: boolean;

	constructor(id: SorterId,
				field: TypeField,
				direction: boolean = true) {
		this.sorterId = id;
		this.field = field;
		this.direction = direction;
	}

	getId(): SorterId {
		return this.sorterId;
	}

	getRank(): number {
		return this.rank;
	}

	getField(): TypeField {
		return this.field;
	}

	hasDirection(): boolean {
		return this.direction;
	}

	changeDirection(): void {
		this.direction = !this.direction;
	}

	setDirection(direction: boolean): void {
		this.direction = direction;
	}

	sort(entities: Array<OriginItemEntity>): Array<OriginItemEntity> {

		if (entities.length === 0) {
			return entities;
		}

		return entities.sort((entityOne: OriginItemEntity, entityTwo: OriginItemEntity) => {
			return this.field.sort(entityOne, entityTwo, this.direction);
		});
	}

}
