import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { CompositionId } from '../../composition.id';
import { CompositionContainerWidthSetEvent } from './composition-container-width-set.event';


export class CompositionContainerWidthSetAggregateEvent extends AggregateEvent<CompositionId> {

	constructor(compositionId: CompositionId,
				public readonly containerWidth: number) {
		super(compositionId, 'CompositionContainerWidthSetAggregateEvent');
	}

	toDomainEvent(): DomainEvent<CompositionId> {
		return new CompositionContainerWidthSetEvent(this.getAggregateId(), this.containerWidth);
	}
}
