import { Command } from '@generic-ui/hermes';

import { CompositionId } from '../../composition.id';
import { ColumnId } from '../column.id';

export class CompositionSetColumnEnabledCommand extends Command {

	constructor(readonly compositionId: CompositionId,
				private readonly columnId: ColumnId,
				private readonly enabled: boolean) {
		super(compositionId, 'CompositionSetColumnEnabledCommand');
	}

	getColumnId(): ColumnId {
		return this.columnId;
	}

	isEnabled(): boolean {
		return this.enabled;
	}

}
