import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { distinctUntilChanged, map } from 'rxjs/operators';

import { OriginItemEntity } from '../../../domain/source/origin/origin-item-entity';
import { StructureId } from '../../../domain/structure.id';


@Injectable()
export class StructurePreparedItemsRepository {

	private origin = new Map<string, Array<any>>();

	private origin$ = new ReplaySubject<Map<string, Array<any>>>();

	setItems(origin: Array<OriginItemEntity>, structureId: StructureId): void {
		this.origin.set(structureId.toString(), origin);
		this.origin$.next(this.origin);
	}

	onPreparedItems(structureId: StructureId): Observable<Array<OriginItemEntity>> {

		return this.origin$
				   .asObservable()
				   .pipe(
					   map((map: Map<string, Array<any>>) => {
						   return map.get(structureId.toString());
					   }),
					   distinctUntilChanged()
				   );
	}
}
