import { AggregateEvent, AggregateId, DomainEvent } from '@generic-ui/hermes';

import { CompositionId } from '../../composition.id';
import { CompositionWidthSetEvent } from './composition-width-set.event';

export class CompositionWidthSetAggregateEvent extends AggregateEvent<CompositionId> {

	constructor(aggregateId: AggregateId) {
		super(aggregateId, 'CompositionWidthSetAggregateEvent');
	}

	toDomainEvent(): DomainEvent<CompositionId> {
		return new CompositionWidthSetEvent(this.getAggregateId());
	}

}
