import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Inject, ViewEncapsulation } from '@angular/core';

import { SmartComponent } from '../../../../common/cdk/smart-component';
import { StructureReadModelRoot } from '../../domain-api/read/structure.read-model-root';
import { Paging } from '../../../../structure/paging/domain-api/read/paging';
import { StructureDefinition } from '../structure-definition';
import { StructureId } from '../../domain/structure.id';
import { StructureWarehouse } from '../../domain-api/structure.warehouse';
import { PagingWarehouse } from '../../../../structure/paging/domain-api/paging.warehouse';
import { StructureInfoPanelArchive } from '../panel/info/structure.info-panel.archive';
import { StructureSummariesArchive } from '../panel/summaries/structure.summaries.archive';
import { StructureFilterWarehouse } from '../../domain-api/filter/structure-filter.warehouse';
import { StructureSearchWarehouse } from '../../domain-api/search/structure-search.warehouse';
import { STRUCTURE_CSS_CLASS_NAME } from '../../domain-api/structure-tokens';
import { StructureSummariesPanelConfig } from '../panel/summaries/structure.summaries-panel.config';
import { StructureHeaderTopEnabledArchive } from '../header/structure-header-top-enabled.archive';
import { StructureHeaderBottomEnabledArchive } from '../header/structure-header-bottom-enabled.archive';
import { StructureTitlePanelConfigArchive } from '../panel/title-panel/structure.title-panel.config-archive';
import { StructureFooterPanelConfigArchive } from '../panel/footer-panel/structure.footer-panel.config-archive';
import { TitlePanelConfig } from '../panel/title-panel/title-panel.config';
import { FooterPanelConfig } from '../panel/footer-panel/footer-panel.config';
import { StructureInfoPanelConfig } from '../panel/info/structure.info-panel.config';


@Component({
	selector: 'gui-structure-blueprint',
	templateUrl: `structure.blueprint.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureBlueprintComponent extends SmartComponent {

	structure: StructureReadModelRoot;

	searchEnabled: boolean = false;

	topSummariesPanelEnabled: boolean = false;

	bottomSummariesPanelEnabled: boolean = false;

	quickFiltersEnabled: boolean = false;

	infoPanelEnabled: boolean = false;

	readonly contentCssClass: string;

	readonly headerCssClass: string;

	topHeaderEnabled: boolean;

	bottomHeaderEnabled: boolean;

	private pagingReadModel: Paging;

	titlePanelEnabled: boolean;

	footerPanelEnabled: boolean;

	items: Array<any> = [];

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly structureDefinition: StructureDefinition,
				private readonly structureId: StructureId,
				private readonly structureReadModelService: StructureWarehouse,
				private readonly structureInfoPanelEnabledArchive: StructureInfoPanelArchive,
				private readonly pagingReadModelService: PagingWarehouse,
				private readonly structureSummariesArchive: StructureSummariesArchive,
				private readonly structureFilterReadModelRepository: StructureFilterWarehouse,
				private readonly structureSearchReadModelRepository: StructureSearchWarehouse,
				private readonly structureHeaderTopEnabledArchive: StructureHeaderTopEnabledArchive,
				private readonly structureHeaderBottomEnabledArchive: StructureHeaderBottomEnabledArchive,
				private readonly structureTitlePanelConfigArchive: StructureTitlePanelConfigArchive,
				private readonly structureFooterPanelConfigArchive: StructureFooterPanelConfigArchive,
				@Inject(STRUCTURE_CSS_CLASS_NAME) private className: string) {
		super();

		this.headerCssClass = `gui-${this.className}-header`;
		this.contentCssClass = `gui-${this.className}-content`;
	}

	ngOnInit() {

		this.structureHeaderTopEnabledArchive
			.onValue()
			.pipe(this.takeUntil())
			.subscribe((topHeaderEnabled: boolean) => {
				this.topHeaderEnabled = topHeaderEnabled;
				this.changeDetectorRef.detectChanges();
			});

		this.structureHeaderBottomEnabledArchive
			.onValue()
			.pipe(this.takeUntil())
			.subscribe((bottomHeaderEnabled: boolean) => {
				this.bottomHeaderEnabled = bottomHeaderEnabled;
				this.changeDetectorRef.detectChanges();
			});

		this.structureReadModelService
			.onStructure()
			.pipe(this.takeUntil())
			.subscribe((structure: StructureReadModelRoot) => {
				this.structure = structure;
				this.items = structure.getEntities();
				this.changeDetectorRef.detectChanges();
			});

		this.pagingReadModelService
			.onPaging()
			.pipe(this.takeUntil())
			.subscribe((paging: Paging) => {
				this.pagingReadModel = paging;
				this.changeDetectorRef.detectChanges();
			});

		this.structureSearchReadModelRepository
			.onSearchEnabled(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((enabled: boolean) => {
				this.searchEnabled = enabled;
				this.changeDetectorRef.detectChanges();
			});


		this.structureFilterReadModelRepository
			.onQuickFiltersEnabled(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((enabled: boolean) => {
				this.quickFiltersEnabled = enabled;
				this.changeDetectorRef.detectChanges();
			});

		this.structureSummariesArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((config: StructureSummariesPanelConfig) => {
				this.topSummariesPanelEnabled = config.itTopEnabled();
				this.bottomSummariesPanelEnabled = config.itBottomEnabled();
				this.changeDetectorRef.detectChanges();
			});

		this.structureInfoPanelEnabledArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((infoPanel: StructureInfoPanelConfig) => {
				this.infoPanelEnabled = infoPanel.isEnabled();
				this.changeDetectorRef.detectChanges();
			});

		this.structureTitlePanelConfigArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((titlePanel: TitlePanelConfig) => {
				this.titlePanelEnabled = titlePanel.enabled;
				this.changeDetectorRef.detectChanges();
			});

		this.structureFooterPanelConfigArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((footerPanel: FooterPanelConfig) => {
				this.footerPanelEnabled = footerPanel.enabled;
				this.changeDetectorRef.detectChanges();
			});
	}

	getHeaderTopClasses(): string {
		return this.headerCssClass + ' gui-header-top';
	}

	getHeaderBottomClasses(): string {
		return this.headerCssClass + ' gui-header-bottom';
	}

	isColumnHeaderTopEnabled(): boolean {
		return this.structureDefinition.isHeaderEnabled() && this.topHeaderEnabled;
	}

	isColumnHeaderBottomEnabled(): boolean {
		return this.structureDefinition.isHeaderEnabled() && this.bottomHeaderEnabled;
	}

	isPagingTopEnabled(): boolean {
		return this.structureDefinition.getTopPaging().isEnabled() && this.pagingReadModel && this.pagingReadModel.isPagerTop();
	}

	isPagingBottomEnabled(): boolean {
		return this.structureDefinition.getBottomPaging().isEnabled() && this.pagingReadModel && this.pagingReadModel.isPagerBottom();
	}

	isSourceEmpty(): boolean {
		return this.structure && this.structure.getEntities().length === 0;
	}

}
