export class StructureSummariesPanelConfig {

	private readonly top: boolean = false;

	private readonly bottom: boolean = true;

	constructor(top?: boolean,
				bottom?: boolean) {

		if (top !== undefined && top !== null) {
			this.top = top;
		}

		if (bottom !== undefined && bottom !== null) {
			this.bottom = bottom;
		}

	}

	itTopEnabled(): boolean {
		return this.top;
	}

	itBottomEnabled(): boolean {
		return this.bottom;
	}

}
