import { AggregateEvent, AggregateId, DomainEvent } from '@generic-ui/hermes';

import { ActiveColumnEntity } from '../active/active-column.entity';
import { CompositionId } from '../../composition.id';
import { CompositionColumnSetEnabledEvent } from './composition.column-set-enabled.event';

export class CompositionColumnSetEnabledAggregateEvent extends AggregateEvent<CompositionId> {

	constructor(aggregateId: AggregateId,
				private readonly activeColumns: Array<ActiveColumnEntity>) {
		super(aggregateId, 'CompositionColumnSetEnabledAggregateEvent');
	}

	toDomainEvent(): DomainEvent<CompositionId> {
		return new CompositionColumnSetEnabledEvent(this.getAggregateId());
	}

	getActiveColumns(): Array<ActiveColumnEntity> {
		return this.activeColumns;
	}

}
