import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { take } from 'rxjs/operators';

import { schemaGlobalId } from './read/schema.global-id';
import { SchemaTheme } from '../domain/theme/schema-theme';
import { SchemaReadModelRootId } from './read/schema.read-model-root-id';
import { SchemaCssClass } from './css-classes/schema-css-class';
import { SchemaThemeRepository } from './theme/schema.theme.repository';
import { SchemaCssClassesRepository } from './css-classes/schema.css-classes.repository';


@Injectable()
export class SchemaWarehouse {

	constructor(private readonly schemaCssClassesRepository: SchemaCssClassesRepository,
				private readonly schemaThemeRepository: SchemaThemeRepository) {
	}

	onSingleTheme(schemaId: SchemaReadModelRootId = schemaGlobalId): Observable<SchemaTheme> {
		return this.schemaThemeRepository
				   .on(schemaId.toAggregateId())
				   .pipe(
					   take(1)
				   );
	}

	onCssClasses(schemaId: SchemaReadModelRootId = schemaGlobalId): Observable<SchemaCssClass> {
		return this.schemaCssClassesRepository
				   .on(schemaId.toAggregateId());
	}

}
