import { Injectable } from '@angular/core';

import { RandomStringGenerator } from '@generic-ui/hermes';

import { ColumnParams } from './set-columns/column.params';
import { ColumnEntity } from './column.entity';
import { ColumnConfig } from './column.config';
import { ColumnField } from './field/column-field';
import { ViewEntity } from './view.entity';
import { ColumnId } from './column.id';
import { DataType } from '../../../structure/domain/structure/field/data-type/data-type';
import { ColumnPresentation } from './presentation/column.presentation';
import { ColumnPresentationConverter } from './presentation/column.presentation.converter';


@Injectable()
export class ColumnEntityFactory {

	constructor(private readonly columnPresentationConverter: ColumnPresentationConverter) {
	}

	create(params: ColumnParams): ColumnEntity;
	create(params: Array<ColumnParams>): Array<ColumnEntity>;
	create(data: ColumnParams | Array<ColumnParams>): ColumnEntity | Array<ColumnEntity> {

		if (Array.isArray(data)) {
			return this.createColumns(data);
		} else {
			return this.createColumn(data);
		}
	}

	private createColumn(params: ColumnParams): ColumnEntity {

		let columnConfig: ColumnConfig = params.getColumn(),
			field: ColumnField = params.getField(),
			width = this.convertWidth(columnConfig.width) || undefined,
			view,
			enabled,
			dataType = field.getDataType();

		if (columnConfig.view !== undefined) {
			view = new ViewEntity(columnConfig.view);
		}

		if (columnConfig.enabled !== undefined) {
			enabled = columnConfig.enabled;
		} else {
			enabled = true;
		}

		const presentation = this.getPresentation(dataType);

		let columnEntity = new ColumnEntity(
			new ColumnId(RandomStringGenerator.generate()),
			field,
			columnConfig,
			enabled,
			presentation,
			undefined,
			columnConfig.align,
			view,
			width
		);

		if (columnConfig.header !== undefined) {
			columnEntity.setHeader(columnConfig.header);
		}

		return columnEntity;
	}

	private createColumns(columns: Array<ColumnParams>): Array<ColumnEntity> {

		let columnEntities = [] as Array<ColumnEntity>;

		columns.forEach((column: ColumnParams) => {
			columnEntities.push(this.createColumn(column));
		});

		return columnEntities;
	}

	private convertWidth(width: string | number): number {
		return +width;
	}

	private getPresentation(dataType: DataType): ColumnPresentation {

		return this.columnPresentationConverter.convert(dataType);
	}
}
