import { Injectable } from '@angular/core';

import { CommandDispatcher } from '@generic-ui/hermes';

import { StructureSearchDispatcher } from '../../../domain/structure/search/structure.search.dispatcher';
import { StructureId } from '../../../domain/structure.id';
import { SearchConfig } from '../../../domain-api/search/search-config';
import { Override } from '../../../../../common/cdk/decorators';
import { StructureSearchHighlightArchive } from '../../../domain-api/search/highlight/structure.search-highlight.archive';
import { StructureSearchPlaceholderArchive } from '../../../domain-api/search/placeholder/structure.search-placeholder.archive';


@Injectable()
export class LocalStructureSearchCommandDispatcher extends StructureSearchDispatcher {

	constructor(private structureId: StructureId,
				commandDispatcher: CommandDispatcher,
				structureSearchHighlightArchive: StructureSearchHighlightArchive,
				structureSearchPlaceholderArchive: StructureSearchPlaceholderArchive) {
		super(commandDispatcher, structureSearchHighlightArchive, structureSearchPlaceholderArchive);
	}

	@Override
	setSearchingConfig(config: SearchConfig): void {
		super.setSearchingConfig(config, this.structureId);
	}

	@Override
	search(phrase: string): void {
		super.search(phrase, this.structureId);
	}
}
