import { ColumnId } from './column.id';
import { ColumnField } from './field/column-field';
import { ColumnConfig, HeaderTemplate } from './column.config';
import { SortOrder } from './sort/sort-order';
import { ViewEntity } from './view.entity';
import { ColumnAlign } from './column-align';
import { AbstractColumnEntity } from './abstract-column.entity';
import { ColumnPresentation } from './presentation/column.presentation';


export class ColumnEntity extends AbstractColumnEntity {

	sortable?: boolean;

	private sortStatus = SortOrder.NONE;

	private enabled: boolean = true;

	constructor(columnId: ColumnId,
				columnField: ColumnField,
				columnConfig: ColumnConfig,
				enabled: boolean,
				presentation: ColumnPresentation,
				header: string | HeaderTemplate,
				align: ColumnAlign,
				view: ViewEntity,
				width: number) {
		super(
			columnId,
			columnField,
			columnConfig,
			presentation,
			header,
			align,
			view,
			width
		);

		this.enabled = enabled;

		if (view === undefined) {
			this.view = new ViewEntity(this.presentation.getDefaultView());
		}

		if (align === undefined) {
			this.align = this.presentation.getDefaultAlign(this.view);
		}
	}

	isEnabled(): boolean {
		return this.enabled;
	}

	setEnabled(enabled: boolean): void {
		this.enabled = enabled;
	}

	getSortStatus(): SortOrder {
		return this.sortStatus;
	}

	setSortStatus(status: SortOrder): void {
		this.sortStatus = status;
	}

}
