import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';
import { InitFieldsCommand } from './init-fields.command';
import { FieldsInitedEvent } from './fields-inited.event';
import { StructureAggregate } from '../../structure.aggregate';
import { FieldsInitedAggregateEvent } from './fields-inited.aggregate-event';


@Injectable()
export class InitFieldsCommandHandler implements CommandHandler<StructureAggregate, InitFieldsCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<InitFieldsCommand> {
		return InitFieldsCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: InitFieldsCommand): void {

		const compositionId = command.getCompositionId(),
			columns = command.getColumns();

		const aggregateEvents = aggregate.getEvents();

		const fieldTypes = (aggregateEvents[0] as FieldsInitedAggregateEvent).getFields();

		this.domainEventPublisher.publish(new FieldsInitedEvent(command.getAggregateId(), compositionId, columns, fieldTypes));
	}

	handleAggregate(structure: StructureAggregate, command: InitFieldsCommand): void {

		const columns = command.getColumns();

		structure.createFields(columns);
	}


}
