import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { StructureAggregateRepository } from '../../structure/structure-aggregate.repository';
import { ToggleSelectedRowCommand } from './toggle-selected-row.command';
import { SelectedRowToggledEvent } from './selected-row-toggled.event';
import { StructureAggregate } from '../../structure/structure.aggregate';


@Injectable()
export class ToggleSelectedRowCommandHandler implements CommandHandler<StructureAggregate, ToggleSelectedRowCommand> {

	constructor(private structureAggregateRepository: StructureAggregateRepository,
				private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<ToggleSelectedRowCommand> {
		return ToggleSelectedRowCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: ToggleSelectedRowCommand): void {
		this.domainEventPublisher.publish(new SelectedRowToggledEvent(command.getAggregateId()));
	}

	handleAggregate(aggregate: StructureAggregate, command: ToggleSelectedRowCommand): void {

		const formation = aggregate.getFormation(),
			selectedRow = command.getSelectedRow();

		formation.toggleRow(selectedRow);
	}

}
