import { ColumnFieldId } from './column-field.id';
import { ColumnFieldAccessor } from './column-field-accessor';

import { DataType } from '../../../../structure/domain/structure/field/data-type/data-type';
import { OriginItemEntity } from '../../../../structure/domain/source/origin/origin-item-entity';


export class ColumnField {

	private readonly fieldId: ColumnFieldId;

	private readonly accessor: ColumnFieldAccessor;

	private readonly searchAccessor: ColumnFieldAccessor;

	private readonly dataType: DataType;

	constructor(fieldId: ColumnFieldId,
				accessor: ColumnFieldAccessor,
				dataType: DataType,
				searchAccessor: ColumnFieldAccessor) {
		this.fieldId = fieldId;
		this.accessor = accessor;
		this.dataType = dataType;
		this.searchAccessor = searchAccessor;
	}

	getId(): ColumnFieldId {
		return this.fieldId;
	}

	getValue(entity: OriginItemEntity): any {
		return this.accessor(entity);
	}

	getAccessor(): ColumnFieldAccessor {
		return this.accessor;
	}

	getSearchAccessor(): ColumnFieldAccessor {
		return this.searchAccessor;
	}

	getDataType(): DataType {
		return this.dataType;
	}

}
