import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map, switchMap, take } from 'rxjs/operators';

import { DomainEvent, DomainEventBus } from '@generic-ui/hermes';

import { StructureWarehouse } from '../structure.warehouse';
import { StructureId } from '../../domain/structure.id';
import { SelectedRowToggledEvent } from '../../domain/formation/toggle/selected-row-toggled.event';
import { StructureReadModelRoot } from '../read/structure.read-model-root';
import { ItemEntity } from '../../domain/source/item.entity';


@Injectable()
export class FormationEventService {

	constructor(private readonly domainEventBus: DomainEventBus,
				private readonly structureWarehouse: StructureWarehouse) {
	}

	onItemSelected(structureId: StructureId): Observable<Array<any>> {

		return this.domainEventBus
				   .ofEvent(
					   SelectedRowToggledEvent
				   )
				   .pipe(
					   filter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === structureId.toString()),
					   switchMap((event: DomainEvent<StructureId>) => {

						   return this.structureWarehouse
									  .onStructure(structureId)
									  .pipe(
										  take(1),
										  map((str: StructureReadModelRoot) => str.getSelectedEntities()),
										  map((entities: Array<ItemEntity>) => entities.map(ent => ent.getData()))
									  );
					   })
				   );
	}

}
