import { AggregateId, ReadModelRootId } from '@generic-ui/hermes';
import { CompositionReadModelRootId } from '../domain-api/read/composition.read-model-root-id';

export class CompositionId extends AggregateId {

	constructor(id: string) {
		super(id);
	}

	toReadModelRootId(): ReadModelRootId {
		return new CompositionReadModelRootId(this.getId());
	}

}
