import { ReadModelRoot } from '@generic-ui/hermes';

import { ColumnDefinition } from './definition/column-definition';
import { CellTemplateWithContext } from './definition/cell-template-with-context';
import { CellTemplateWithAccessor } from './definition/cell-template-with-accessor';
import { CompositionReadModelRootId } from './composition.read-model-root-id';


export class CompositionReadModeRoot extends ReadModelRoot<CompositionReadModelRootId> {

	constructor(uid: CompositionReadModelRootId,
				private readonly ready: boolean,
				private readonly allColumns: Array<ColumnDefinition>,
				private readonly activeColumns: Array<ColumnDefinition>,
				private readonly width: number,
				private readonly resizeWidth: boolean) {
		super(uid);
	}

	getActiveColumns(): Array<ColumnDefinition> {
		return this.activeColumns;
	}

	getAllColumns(): Array<ColumnDefinition> {
		return this.allColumns;
	}

	getActiveHeaderColumns(): Array<CellTemplateWithContext> {
		return this.getActiveColumns()
				   .map((column: ColumnDefinition, index: number) => column.getHeaderCellTemplateWithContext(index));
	}

	getHeaderColumns(): Array<CellTemplateWithContext> {
		return this.getAllColumns()
				   .map((column: ColumnDefinition, index: number) => column.getHeaderCellTemplateWithContext(index));
	}

	getTemplateColumns(): Array<CellTemplateWithAccessor> {
		return this.getActiveColumns()
				   .map((column: ColumnDefinition) => column.getContentCellTemplateWithAccessor());
	}

	getWidth(): number {
		return this.width;
	}

	getContainerWidth(): number {
		return +this.width - 2;
	}

	isReady(): boolean {
		return this.ready;
	}

	isResizeWidthEnabled(): boolean {
		return this.resizeWidth;
	}

	equals(composition: CompositionReadModeRoot): boolean {
		return this.width === composition.width &&
			this.resizeWidth === composition.resizeWidth &&
			this.equalsByColumns(composition.allColumns);
	}

	equalsByColumns(columns: Array<ColumnDefinition>): boolean {
		return this.allColumns.length === columns.length;
	}

}
