import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { ColumnConfig } from '../../../../../composition/domain/column/column.config';
import { StructureId } from '../../../structure.id';
import { TypeField } from '../data-type/type.field';
import { FieldsInitedEvent } from './fields-inited.event';


export class FieldsInitedAggregateEvent extends AggregateEvent<StructureId> {

	constructor(structureId: StructureId,
				private readonly columns: Array<ColumnConfig>, // TODO merge
				private readonly fields: Array<TypeField>) {
		super(structureId, 'FieldsInitedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {

		throw new Error('COMPOSITION ID IS MISSING');

		return new FieldsInitedEvent(
			this.getAggregateId(),
			null,
			this.columns,
			this.fields
		);
	}

	getColumns(): Array<ColumnConfig> {
		return this.columns;
	}

	getFields(): Array<TypeField> {
		return this.fields;
	}
}
