import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { SchemaId } from '../../schema.id';
import { SchemaHorizontalGridSetEvent } from '../horizontal/schema-horizontal-grid-set.event';
import { SchemaVerticalGridSetEvent } from './schema-vertical-grid-set.event';

export class SchemaVerticalGridSetAggregateEvent extends AggregateEvent<SchemaId> {

	constructor(schemaId: SchemaId,
				private readonly verticalGrid: boolean) {
		super(schemaId, 'SchemaHorizontalGridSetEvent');
	}

	toDomainEvent(): DomainEvent<SchemaId> {
		return new SchemaVerticalGridSetEvent(this.getAggregateId(), this.verticalGrid);
	}
}
