import { Injectable, TemplateRef } from '@angular/core';

import { ViewTemplateRepository } from '../view/view-template.repository';
import { EditTemplateRepository } from '../edit/edit-template.repository';
import { ColumnEntity } from '../../../domain/column/column.entity';
import { ColumnDefinition } from './column-definition';
import { CellView } from '../../../domain/column/cell-view';
import { DataType } from '../../../../structure/domain/structure/field/data-type/data-type';
import { ActiveColumnEntity } from '../../../domain/column/active/active-column.entity';
import { ColumnDefinitionId } from './column-definition-id';


@Injectable()
export class ColumnDefinitionFactory {

	constructor(private viewTemplateRepository: ViewTemplateRepository,
				private editTemplateRepository: EditTemplateRepository) {
	}

	create(column: ColumnEntity): ColumnDefinition;
	create(column: ActiveColumnEntity): ColumnDefinition;
	create(column: ColumnEntity | ActiveColumnEntity): ColumnDefinition {

		if (column instanceof ColumnEntity) {
			return this.createFromColumnEntity(column);
		} else {
			return this.createFromActiveColumnEntity(column);
		}
	}

	private createFromColumnEntity(column: ColumnEntity): ColumnDefinition {

		let columnDef = new ColumnDefinition(
			column.getField(),
			new ColumnDefinitionId(column.getId().toString()),
			column.isEnabled(),
			column.getDataType(),
			column.getView(),
			column.getAlign(),
			column.getHeader(),
			column.isCellEditingEnabled(),
			column.getSortStatus(),
			column.getSortingEnabled()
		);

		columnDef.cellTemplate = this.findViewTemplate(column.getCellView());
		columnDef.headerTemplate = this.findViewTemplate(CellView.TEXT);

		columnDef.setTemplateFunction(column.getTemplateFunction());

		columnDef.editTemplate = this.findEditTemplate(column.getDataType());

		columnDef.width = +column.getWidth();

		return columnDef;
	}

	private createFromActiveColumnEntity(activeColumn: ActiveColumnEntity): ColumnDefinition {

		let columnDef = new ColumnDefinition(
			activeColumn.getField(),
			new ColumnDefinitionId(activeColumn.getId().toString()),
			true, // remove,
			activeColumn.getDataType(),
			activeColumn.getView(),
			activeColumn.getAlign(),
			activeColumn.getHeader(),
			activeColumn.isCellEditingEnabled(),
			activeColumn.getSortStatus(),
			activeColumn.getSortingEnabled()
		);

		columnDef.cellTemplate = this.findViewTemplate(activeColumn.getCellView());
		columnDef.headerTemplate = this.findViewTemplate(CellView.TEXT);

		columnDef.setTemplateFunction(activeColumn.getTemplateFunction());

		columnDef.editTemplate = this.findEditTemplate(activeColumn.getDataType());

		columnDef.width = +activeColumn.getWidth();

		return columnDef;
	}

	createColumns(columns: Array<ColumnEntity>): Array<ColumnDefinition> {
		return columns.map((column: ColumnEntity) => this.create(column));
	}

	private findViewTemplate(view: CellView): TemplateRef<any> {

		if (view === CellView.FUNCTION) {
			return this.viewTemplateRepository.findTemplate(CellView.FUNCTION);
		} else {
			return this.viewTemplateRepository.findTemplate(view);
		}
	}

	private findEditTemplate(dataType: DataType): TemplateRef<any> {
		return this.editTemplateRepository.findTemplate(dataType);
	}

}
