import { Command } from '@generic-ui/hermes';

import { CompositionId } from '../../composition.id';

import { ColumnParams } from './column.params';


export class SetColumnsCommand extends Command {

	constructor(readonly compositionId: CompositionId,
				private readonly columns: Array<ColumnParams>) {
		super(compositionId, 'SetColumnsCommand');
	}

	getParams(): Array<ColumnParams> {
		return this.columns;
	}

}
