import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { CompositionId } from '../composition.id';
import { CompositionCreatedEvent } from './composition-created.event';

export class CompositionCreatedAggregateEvent extends AggregateEvent<CompositionId> {

	constructor(compositionId: CompositionId) {
		super(compositionId, 'SchemaCreatedEvent');
	}

	toDomainEvent(): DomainEvent<CompositionId> {
		return new CompositionCreatedEvent(this.getAggregateId());
	}
}
