import { ColumnField } from '../field/column-field';
import { ColumnId } from '../column.id';
import { ColumnConfig, HeaderTemplate } from '../column.config';
import { ViewEntity } from '../view.entity';
import { ColumnAlign } from '../column-align';
import { ColumnEntity } from '../column.entity';
import { ColumnPresentation } from '../presentation/column.presentation';


export class ActiveColumnEntity extends ColumnEntity {

	static fromEntity(entity: ColumnEntity): ActiveColumnEntity {
		return new ActiveColumnEntity(
			entity.getId(),
			entity.getField(),
			entity.getHeader(),
			entity.getAlign(),
			entity.getView(),
			entity.getColumnConfig(),
			entity.getPresentation()
		);
	}

	private constructor(columnId: ColumnId,
						columnField: ColumnField,
						header: string | HeaderTemplate,
						align: ColumnAlign,
						view: ViewEntity,
						config: ColumnConfig,
						presentation: ColumnPresentation) {
		super(
			columnId,
			columnField,
			config,
			true,
			presentation,
			header,
			align,
			view,
			undefined
		);
	}

}
