export type FieldAccessor = (element?: any) => any;
export type ViewTemplateMethod = (cellValue: any) => string;

export interface GuiColumn {

	field?: string | FieldAccessor;

	type?: string | GuiDataType;

	view?: string | GuiCellView | ViewTemplateMethod;

	header?: string;

	width?: string | number;

	enabled?: boolean;

	align?: string | GuiColumnAlign;

	summaries?: GuiColumnSummaries;

	sorting?: GuiColumnSorting;

	cellEditing?: GuiColumnCellEditing;

	matcher?: (item: any) => any;
}

export enum GuiDataType {
	UNKNOWN,
	NUMBER,
	STRING,
	BOOLEAN,
	DATE,
	CUSTOM
}

export enum GuiCellView {
	TEXT,
	CHIP,
	LINK,
	IMAGE,
	BOLD,
	ITALIC,
	CHECKBOX,
	CUSTOM,
	BAR,
	PERCENTAGE_BAR,
	PERCENTAGE
}

export enum GuiNumberCellView {
	TEXT,
	BOLD,
	ITALIC,
	CHIP
}

export enum GuiStringCellView {
	TEXT,
	BOLD,
	ITALIC,
	CHIP
}

export enum GuiBooleanCellView {
	TEXT,
	BOLD,
	ITALIC,
	CHIP,
	CHECKBOX
}

export enum GuiDateCellView {
	TEXT,
	BOLD,
	ITALIC,
	CHIP
}

export interface GuiPaging {

	enabled?: boolean;

	page?: number;

	pageSize?: number;

	pageSizes?: Array<number>;

	pagerTop?: boolean;

	pagerBottom?: boolean;

	display?: string | GuiPagingDisplay;

}

export enum GuiTheme {
	FABRIC,
	MATERIAL,
	LIGHT,
	DARK,
	GENERIC
}

export enum GuiRowColoring {
	NONE,
	EVEN,
	ODD
}

export enum GuiSortingOrder {
	NONE,
	ASC,
	DESC
}

export interface GuiSorting {

	enabled?: boolean;

	/**
	 * @experimental
	 */
	multiSorting?: boolean;

}

export interface GuiFiltering {

	enabled?: boolean;

}

export interface GuiQuickFilters {

	enabled?: boolean;

}

export interface GuiSearching {

	enabled?: boolean;

	highlighting?: boolean;

	placeholder?: string;

	phrase?: string;

}

export interface GuiSummaries {

	enabled?: boolean;

	top?: boolean;

	bottom?: boolean;

}

export interface GuiColumnSummaries {

	enabled?: boolean;

	summariesTypes?: Array<any>;
}

export interface GuiColumnSorting {

	enabled?: boolean;

	matcher?: (item: any) => any;

	order?: GuiSortingOrder
}

export interface GuiColumnCellEditing {

	enabled?: boolean;

}

export interface GuiColumnMenu {

	enabled?: boolean;

	sort?: boolean;

	filter?: boolean;

	columnsManager?: boolean;

}

export enum GuiPagingDisplay {
	BASIC,
	ADVANCED
}

export enum GuiColumnAlign {
	RIGHT,
	CENTER,
	LEFT
}

export interface GuiRowDetail {

	enabled?: boolean;

	template?: (item: any) => string;
}

export interface GuiTitlePanel {
	enabled?: boolean;
	template?: string | ((title: any) => string);
}

export interface GuiFooterPanel {
	enabled?: boolean
	template?: string | ((title: any) => string);
}

export interface GuiInfoPanel {
	enabled?: boolean;
	infoDialog?: boolean;
	columnsManager?:boolean;
	sourceSize?: boolean
}
