import { ReadModelObject } from '@generic-ui/hermes';


@ReadModelObject
export class VerticalFormationReadModel {

	constructor(private readonly enabled: boolean,
				private readonly topMargin: number,
				private readonly sourceHeight: number,
				private readonly viewportHeight: number,
				private readonly rowHeight: number) {
	}

	isEnabled(): boolean {
		return this.enabled;
	}

	getTopMargin(): number {
		return this.topMargin; //  * this.rowHeight;
	}

	getSourceHeight(): number {
		return this.sourceHeight;
	}

	getViewPortHeight(): number {
		return this.viewportHeight;
	}

	getRowHeight(): number {
		return this.rowHeight;
	}

}
