import { Injectable } from '@angular/core';

import { InMemoryReadModelStore } from '@generic-ui/hermes';

import { CompositionReadModeRoot } from '../../../domain-api/read/composition.read-mode-root';
import { CompositionAggregate } from '../../../domain/composition.aggregate';
import { InMemoryCompositionStore } from '../in-memory.composition.store';
import { CompositionReadModelRootConverter } from '../../../domain-api/read/composition.read-model-root-converter';
import { CompositionId } from '../../../domain/composition.id';
import { CompositionReadModelRootId } from '../../../domain-api/read/composition.read-model-root-id';


@Injectable()
export class InMemoryCompositionReadStore extends InMemoryReadModelStore<CompositionReadModelRootId,
	CompositionReadModeRoot,
	CompositionId,
	CompositionAggregate> {

	constructor(private inMemoryCompositionStore: InMemoryCompositionStore,
				private compositionConverter: CompositionReadModelRootConverter) {
		super(inMemoryCompositionStore);
	}

	toReadModel(aggregate: CompositionAggregate): CompositionReadModeRoot {
		return this.compositionConverter.convert(aggregate);
	}

}
