import {
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	Input,
	OnChanges,
	SimpleChanges,
	ViewEncapsulation
} from '@angular/core';
import { timer } from 'rxjs';

import { SmartComponent } from '../../../../../common/cdk/smart-component';

import { CellTemplateWithAccessor } from '../../../../composition/domain-api/read/definition/cell-template-with-accessor';
import { StructureCellEditArchive } from '../../edit/structure.cell-edit.archive';
import { StructureCellEditStore } from '../../edit/structure.cell-edit.store';
import { SourceCommandDispatcher } from '../../../domain-api/source/source.command-dispatcher';
import { ChangedValueEmitter } from '../../../../composition/domain-api/read/edit/changed-value.emitter';
import { EditEventType } from '../../../../composition/domain-api/read/edit/state/edit.event-type';
import { StructureEditSourceItemParams } from '../../../domain/source/origin/edit/structure.edit-source-item.params';
import { StructureCellEditState } from '../../edit/structure.cell-edit.state';
import { ItemEntity } from '../../../domain/source/item.entity';
import { StructureCellEditCloseAllService } from '../../edit/structure.cell-edit-close-all.service';


@Component({
	selector: 'gui-structure-cell[entity][cell]',
	templateUrl: `structure.cell.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureCellComponent extends SmartComponent implements OnChanges {

	@Input()
	entity: ItemEntity;

	@Input()
	cell: CellTemplateWithAccessor;

	@Input()
	editMode: boolean;

	@Input()
	cellEditingEnabled: boolean;

	@Input()
	searchPhrase: string;

	inEditMode: boolean = false;

	editContext: any;

	valueChanges$: ChangedValueEmitter<any>;

	status$: ChangedValueEmitter<any>;

	actualValue: any;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly elementRef: ElementRef,
				private readonly structureCellEditArchive: StructureCellEditArchive,
				private readonly structureCellEditStore: StructureCellEditStore,
				private readonly cellEditCloseAllService: StructureCellEditCloseAllService,
				private readonly sourceCommandService: SourceCommandDispatcher) {
		super();
	}

	ngOnChanges(changes: SimpleChanges) {

		// if (changes.editMode !== undefined) {
		// 	if (this.editMode) {
		// 		this.cellEditingEnabled = true;
		// 		this.enterEditMode(false);
		// 	} else if (!this.editMode) {
		// 		this.exitEditMode();
		// 	}
		// }
		//
		// if (!this.editMode) {
		// 	this.exitEditMode();
		// }
	}

	ngAfterViewInit() {
		this.cellEditCloseAllService
			.onCloseAll()
			.pipe(
				this.takeUntil()
			)
			.subscribe(() => {
				this.exitEditMode();
			});
	}

	isCellEditingEnabled(): boolean {
		return this.cellEditingEnabled && this.cell.isCellEditingEnabled();
	}

	enterEditMode(forceCheck: boolean = true): void {

		if (!this.isCellEditingEnabled()) {
			return;
		}

		this.cellEditCloseAllService.closeAll();

		timer(0)
			.pipe(
				this.takeUntil()
			)
			.subscribe(() => {
				this.inEditMode = true;

				this.valueChanges$ = new ChangedValueEmitter();
				this.status$ = new ChangedValueEmitter<EditEventType>();

				this.editContext = {
					status: this.status$,
					valueChanges: this.valueChanges$,
					value: this.cell.getValue(this.entity).value,
					focus: forceCheck,
					parent: this.elementRef
				};

				this.observeFieldStatus();
				this.observeValueChanges();

				this.publishEditEnter();

				if (!forceCheck) {
					this.changeDetectorRef.markForCheck();
				} else {
					this.changeDetectorRef.detectChanges();
				}
			});

	}

	exitEditMode(): void {
		this.inEditMode = false;
		this.changeDetectorRef.detectChanges();
	}

	submitChangesAndExit(): void {

		const itemId = this.entity.getId(),
			updatedValue = this.actualValue,
			fieldId = this.cell.columnFieldId;

		this.sourceCommandService.editItem(new StructureEditSourceItemParams(itemId, fieldId, updatedValue));

		this.exitEditMode();
	}

	private observeFieldStatus(): void {

		this.status$
			.on()
			.pipe(
				this.takeUntil()
			)
			.subscribe((status: EditEventType) => {

				switch (status) {
					case EditEventType.SUBMIT:
						this.submitChangesAndExit();
						this.publishEditSubmit();
						break;

					case EditEventType.CANCEL:
						this.exitEditMode();
						this.publishEditCancel();
						break;

					default:
						break;
				}
			});
	}

	private observeValueChanges(): void {

		this.valueChanges$
			.on()
			.pipe(
				this.takeUntil()
			)
			.subscribe((value: any) => {
				this.actualValue = value;
			});
	}

	private publishEditState(state: StructureCellEditState): void {
		this.structureCellEditStore.next(state);
	}

	private publishEditEnter(): void {
		this.publishEditState(StructureCellEditState.ENTER);
	}

	private publishEditCancel(): void {
		this.publishEditState(StructureCellEditState.CANCEL);
	}

	private publishEditSubmit(): void {
		this.publishEditState(StructureCellEditState.SUBMIT);
	}
}
