import { Injectable } from '@angular/core';

import { AggregateEvent, DomainEventPublisher } from '@generic-ui/hermes';

import { StructureOriginChangedAggregateEvent } from './origin/structure.origin-changed.aggregate-event';
import { OriginSetEvent } from './origin/set-origin/origin-set.event';
import { StructureSourceItemEditedAggregateEvent } from './origin/edit/structure.source-item-edited.aggregate-event';
import { StructureSourceItemEditedEvent } from './origin/edit/structure.source-item-edited.event';
import {
	StructureSummariesChangedAggregateEvent,
	StructureSummariesChangedAggregateEventName
} from '../structure/summaries/structure.summaries-changed.aggregate-event';
import { StructureSummariesChangedEvent } from '../structure/summaries/structure.summaries-changed.event';
import { StructurePreparedEntitiesSetAggregateEvent } from './prepared/structure.prepared-entities-set.aggregate-event';
import { StructurePreparedEntitiesSetEvent } from './prepared/structure.prepared-entities-set.event';
import { StructureId } from '../structure.id';


@Injectable()
export class StructureSourceDomainEventPublisher {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	publish(events: ReadonlyArray<AggregateEvent<StructureId>>): void {

		events.forEach((event) => {
			this.publishEvent(event);
		});
	}

	private publishEvent(event: AggregateEvent<StructureId>): void {

		if (event.getType() === 'StructureOriginChangedAggregateEvent') {

			const structureOriginChangedAggregateEvent = (event as StructureOriginChangedAggregateEvent);

			const domainEvent = new OriginSetEvent(structureOriginChangedAggregateEvent.getAggregateId(), structureOriginChangedAggregateEvent.getOrigin());

			this.domainEventPublisher.publish(domainEvent);
		}

		if (event.getType() === 'StructureSourceItemEditedAggregateEvent') {

			const structureSourceItemEditedAggregateEvent = (event as StructureSourceItemEditedAggregateEvent);

			const domainEvent = new StructureSourceItemEditedEvent(
				structureSourceItemEditedAggregateEvent.getAggregateId(),
				structureSourceItemEditedAggregateEvent.getBeforeItem(),
				structureSourceItemEditedAggregateEvent.getAfterItem()
			);

			this.domainEventPublisher.publish(domainEvent);
		}

		if (event.getType() === StructureSummariesChangedAggregateEventName) {

			const structureSummariesChangedAggregateEvent = (event as StructureSummariesChangedAggregateEvent);

			const domainEvent = new StructureSummariesChangedEvent(structureSummariesChangedAggregateEvent.getAggregateId(), structureSummariesChangedAggregateEvent.getSummaries());

			this.domainEventPublisher.publish(domainEvent);

		}

		if (event.getType() === 'StructurePreparedEntitiesSetAggregateEvent') {

			const structurePreparedEntitiesSetAggregateEvent = (event as StructurePreparedEntitiesSetAggregateEvent);

			const domainEvent = new StructurePreparedEntitiesSetEvent(structurePreparedEntitiesSetAggregateEvent.getAggregateId(), structurePreparedEntitiesSetAggregateEvent.getPreparedItems());

			this.domainEventPublisher.publish(domainEvent);
		}
	}

}
