import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { DomainEventBus, EventRepository } from '@generic-ui/hermes';

import { SchemaThemeSetEvent } from '../domain/theme/schema-theme-set.event';
import { SchemaReadModelRootId } from './read/schema.read-model-root-id';
import { schemaGlobalId } from './read/schema.global-id';
import { SchemaTheme } from '../domain/theme/schema-theme';
import { SchemaHorizontalGridSetEvent } from '../domain/grid/horizontal/schema-horizontal-grid-set.event';
import { SchemaVerticalGridSetEvent } from '../domain/grid/vertical/schema-vertical-grid-set.event';
import { SchemaRowColoring } from '../domain/coloring/schema-row-coloring';
import { RowColoringSetEvent } from '../domain/coloring/row-coloring-set.event';
import { SchemaId } from '../domain/schema.id';


@Injectable()
export class SchemaEventRepository extends EventRepository<SchemaReadModelRootId, SchemaId> {

	constructor(domainEventBus: DomainEventBus) {
		super(domainEventBus);
	}

	onThemeChanged(schemaId: SchemaReadModelRootId = schemaGlobalId): Observable<SchemaTheme> {

		return this.onEvent(schemaId, SchemaThemeSetEvent)
				   .pipe(
					   map((event: SchemaThemeSetEvent) => event.getTheme())
				   );
	}

	onHorizontalGridChanged(schemaId: SchemaReadModelRootId = schemaGlobalId): Observable<boolean> {

		return this.onEvent(schemaId, SchemaHorizontalGridSetEvent)
				   .pipe(
					   map((event: SchemaHorizontalGridSetEvent) => event.getHorizontalGrid())
				   );
	}

	onVerticalGridChanged(schemaId: SchemaReadModelRootId = schemaGlobalId): Observable<boolean> {

		return this.onEvent(schemaId, SchemaVerticalGridSetEvent)
				   .pipe(
					   map((event: SchemaVerticalGridSetEvent) => event.getVerticalGrid())
				   );
	}

	onRowColoring(schemaId: SchemaReadModelRootId = schemaGlobalId): Observable<SchemaRowColoring> {

		return this.onEvent(schemaId, RowColoringSetEvent)
				   .pipe(
					   map((event: RowColoringSetEvent) => event.getRowColoring())
				   );
	}

}
