import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { SchemaId } from '../schema.id';
import { SchemaRowColoring } from './schema-row-coloring';
import { RowColoringSetEvent } from './row-coloring-set.event';


export class RowColoringSetAggregateEvent extends AggregateEvent<SchemaId> {

	constructor(schemaId: SchemaId,
				private readonly rowColoring: SchemaRowColoring) {
		super(schemaId, 'RowColoringSetEvent');
	}

	toDomainEvent(): DomainEvent<SchemaId> {
		return new RowColoringSetEvent(this.getAggregateId(), this.rowColoring);
	}

}
