import { Injectable } from '@angular/core';
import { zip } from 'rxjs';
import { map, take } from 'rxjs/operators';

import { Reactive } from '../../../../common/cdk/reactive';

import { SourceDispatcher } from '../../domain/source/source.dispatcher';
import { StructureFieldUiRepository } from '../field/structure.field.ui-repository';
import { StructureSourceWarehouse } from './structure-source.warehouse';
import { StructureId } from '../../domain/structure.id';
import { structureGlobalId } from '../structure.global-id';
import { StructureEditSourceItemParams } from '../../domain/source/origin/edit/structure.edit-source-item.params';
import { ItemEntity } from '../../domain/source/item.entity';


@Injectable()
export class SourceCommandDispatcher extends Reactive {

	constructor(private sourceDispatcher: SourceDispatcher,
				private structureFieldUiRepository: StructureFieldUiRepository,
				private sourceReadModelService: StructureSourceWarehouse) {
		super();
	}

	setOrigin(items: Array<any>, structureId: StructureId = structureGlobalId): void {
		this.sourceDispatcher.setOrigin(structureId, items);
	}

	setLoading(enabled: boolean, structureId: StructureId = structureGlobalId): void {
		this.sourceDispatcher.setLoading(structureId, enabled);
	}

	editItem(params: StructureEditSourceItemParams, structureId: StructureId = structureGlobalId): void {
		this.sourceDispatcher.editItem(structureId, params);
	}

	editItemByIndex(itemIndex: number, fieldIndex: number, value: any, structureId: StructureId = structureGlobalId): void {

		const itemId$ = this.sourceReadModelService
							.onSingleEntities()
							.pipe(
								map((entities: Array<ItemEntity>) => {
									return entities[itemIndex].getId();
								})
							);

		const fieldId$ = this.structureFieldUiRepository
							 .onFields(structureId);

		zip(itemId$, fieldId$)
			.pipe(
				take(1),
				this.takeUntil()
			)
			.subscribe((array: Array<any>) => {

				const itemId = array[0],
					fields = array[1];

				this.editItem(
					new StructureEditSourceItemParams(itemId, fields[fieldIndex], value),
					structureId
				);
			});
	}

}
