import { Optional } from '@generic-ui/hermes';

import { FieldMatcherType } from './field.matcher.type';

export class FieldMatchers {

	constructor(private readonly matcher?: FieldMatcherType,
				private readonly sortMatcher?: FieldMatcherType,
				private readonly searchMatcher?: FieldMatcherType) { // TODO
	}

	getMatcher(): Optional<FieldMatcherType> {
		return Optional.of(this.matcher);
	}

	getSortMatcher(): Optional<FieldMatcherType> {

		let matcher;

		if (this.sortMatcher) {
			matcher = this.sortMatcher;
		}

		if (this.matcher) {
			matcher = this.matcher;
		}

		return Optional.of(matcher);
	}

	getSearchMatcher(): Optional<FieldMatcherType> {

		let matcher;

		if (this.searchMatcher) {
			matcher = this.searchMatcher;
		}

		if (this.matcher) {
			matcher = this.matcher;
		}

		return Optional.of(matcher);
	}
}
