import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { SchemaId } from '../schema.id';
import { SchemaCreatedEvent } from './schema-created.event';

export class SchemaCreatedAggregateEvent extends AggregateEvent<SchemaId> {

	constructor(aggregateId: SchemaId) {
		super(aggregateId, 'SchemaCreatedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<SchemaId> {
		return new SchemaCreatedEvent(this.getAggregateId());
	}
}
