import { ColumnPresentation } from '../column.presentation';
import { ColumnAlign } from '../../column-align';
import { CellView } from '../../cell-view';


export class DateColumnPresentation extends ColumnPresentation {

	static getInstance(): DateColumnPresentation {
		if (!DateColumnPresentation.instance) {
			DateColumnPresentation.instance = new DateColumnPresentation();
		}
		return DateColumnPresentation.instance;
	}

	private static instance: DateColumnPresentation = null;

	private constructor() {
		super();
	}

	getPossibleViews(): Array<CellView> {
		return [
			CellView.DATE,
			CellView.TEXT,
			CellView.ITALIC,
			CellView.BOLD,
			CellView.CHIP
		];
	}

	getDefaultView(): CellView {
		return CellView.DATE;
	}

	getDefaultAlign(): ColumnAlign {
		return ColumnAlign.LEFT;
	}
}
