import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';

import { StructureReadModelRepository } from '../../read/structure.read-model-repository';
import { StructureId } from '../../../domain/structure.id';
import { Formation } from './formation';
import { StructureReadModelRoot } from '../../read/structure.read-model-root';


@Injectable()
export class FormationRepository {

	constructor(private structureRepository: StructureReadModelRepository) {
	}

	onFormation(structureId: StructureId): Observable<Formation> {

		return this.structureRepository
				   .onStructure(structureId)
				   .pipe(
					   filter((structure: StructureReadModelRoot) => structure.getId().toString() === structureId.toString()),
					   map((structure: StructureReadModelRoot) => structure.getFormation())
				   );
	}

}
