import { AggregateEvent } from '@generic-ui/hermes';

import { Logger } from '../../../common/cdk/logger/logger';

import { PagingConfigWithId } from './paging-config-with-id';
import { PagingConfig } from '../domain-api/paging-config';
import { PageChangedAggregateEvent } from './page/page-changed.aggregate-event';
import { StructureId } from '../../../lib/structure/domain/structure.id';
import { PagesizeChangedAggregateEvent } from './pagesize/pagesize-changed.aggregate-event';


export class PagingManager {

	// TODO
	private readonly events: Array<AggregateEvent<StructureId>> = [];

	// TODO Remove
	static default(logger: Logger) {
		return new PagingManager(
			false,
			1,
			25,
			[10, 25, 50, 100],
			false,
			true,
			0,
			logger
		);
	}

	private readonly logger: Logger;

	private constructor(private enabled: boolean,
						private page: number,
						private pageSize: number,
						private pageSizes: Array<number>,
						private pagerTop: boolean,
						private pagerBottom: boolean,
						private sourceSize: number,
						logger: Logger) {
		this.logger = logger;
	}

	// TODO Remove
	static fromConfig(paging: PagingConfigWithId, logger: Logger): PagingManager {

		const defaultPagingDefinition = PagingManager.default(logger),
			enabled = paging.enabled || defaultPagingDefinition.isEnabled(),
			page = paging.page || defaultPagingDefinition.getPage(),
			pageSize = paging.pageSize || defaultPagingDefinition.getPageSize(),
			pageSizes = paging.pageSizes || defaultPagingDefinition.getPageSizes(),
			pagerTop = paging.pagerTop || defaultPagingDefinition.isPagerTop(),
			pagerBottom = paging.pagerBottom || defaultPagingDefinition.isPagerBottom();

		return new PagingManager(
			enabled,
			page,
			pageSize,
			pageSizes,
			pagerTop,
			pagerBottom,
			0,
			logger
		);
	}

	isEnabled(): boolean {
		return this.enabled;
	}

	isDisabled(): boolean {
		return !this.enabled;
	}

	getPage(): number {
		return this.page;
	}

	getPageSize(): number {
		return this.pageSize;
	}

	getPageSizes(): Array<number> {
		return this.pageSizes;
	}

	isPagerTop(): boolean {
		if (this.isDisabled()) {
			return false;
		}

		return this.pagerTop;
	}

	isPagerBottom(): boolean {
		if (this.isDisabled()) {
			return false;
		}

		return this.pagerBottom;
	}

	getSourceSize(): number {
		return this.sourceSize;
	}

	change(pagingConfig: PagingConfig): void {

		if (pagingConfig.enabled !== undefined) {
			this.enabled = pagingConfig.enabled;
		}

		if (pagingConfig.page !== undefined) {
			this.page = pagingConfig.page;

			if (pagingConfig.enabled === undefined) {
				this.enabled = true;
			}
		}

		if (pagingConfig.pageSize !== undefined) {
			this.pageSize = pagingConfig.pageSize;

			if (pagingConfig.enabled === undefined) {
				this.enabled = true;
			}
		}

		if (pagingConfig.pageSizes !== undefined) {
			this.pageSizes = pagingConfig.pageSizes;

			if (pagingConfig.enabled === undefined) {
				this.enabled = true;
			}
		}

		if (pagingConfig.pagerTop !== undefined) {
			this.pagerTop = pagingConfig.pagerTop;

			if (pagingConfig.enabled === undefined) {
				this.enabled = true;
			}
		}

		if (pagingConfig.pagerBottom !== undefined) {
			this.pagerBottom = pagingConfig.pagerBottom;

			if (pagingConfig.enabled === undefined) {
				this.enabled = true;
			}
		}

		if (this.enabled === true &&
			pagingConfig.pagerTop === false &&
			pagingConfig.pagerBottom === false) {
			this.pagerBottom = true;

			this.logger.warn('Pagers cannot be turn off when paging is enabled.');
		}

	}

	setSourceSize(size: number): void {
		this.sourceSize = size;
	}

	nextPage(): void {
		let nextPage = this.page;

		if (!this.isNextPageDisabled()) {
			nextPage += 1;
		}

		this.page = nextPage;
	}

	prevPage(): void {
		let prevPage = this.page;

		if (!this.isPrevPageDisabled()) {
			prevPage -= 1;
		}

		this.page = prevPage;
	}

	changePageSize(pageSize: number): ReadonlyArray<AggregateEvent<StructureId>> {

		if (this.pageSizes.find((ps: number) => ps === pageSize)) {

			this.setPage(1);
			this.setPageSize(pageSize);
		}

		return this.events;
	}

	isNextPageDisabled(): boolean {

		if (this.sourceSize === 0) {
			return true;
		}

		return this.page === Math.ceil(this.sourceSize / this.pageSize);
	}

	isPrevPageDisabled(): boolean {
		return this.page === 1;
	}

	calculateStart(): number {

		const firstItem = 1 + ((this.page - 1) * this.pageSize);

		if (this.sourceSize < firstItem) {
			return 0;
		}

		return firstItem;
	}

	calculateEnd(): number {

		const lastItem = this.page * this.pageSize;

		if (this.sourceSize < lastItem) {
			return this.sourceSize;
		}

		return lastItem;
	}

	sample(source: Array<any>): Array<any> {

		if (this.isDisabled()) {
			return source;
		}

		let start = this.calculateStart();

		if (start !== 0) {
			start -= 1;
		}

		return source.slice(start, this.calculateEnd());
	}

	private setPage(page: number): void {
		this.page = page;
		this.events.push(new PageChangedAggregateEvent(new StructureId('TODO need to be changed')));
	}

	private setPageSize(pageSize: number): void {
		this.pageSize = pageSize;
		this.events.push(new PagesizeChangedAggregateEvent(new StructureId('TODO need to be changed')));
	}

}
