import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { map } from 'rxjs/operators';

import { SchemaId } from '../../domain/schema.id';
import { Reactive } from '../../../common/cdk/reactive';
import { SchemaCssClass } from './schema-css-class';


@Injectable()
export class SchemaCssClassesRepository extends Reactive {

	private theme = new Map<string, SchemaCssClass>();

	private theme$ = new ReplaySubject<Map<string, SchemaCssClass>>();

	constructor() {
		super();
	}

	setTheme(theme: SchemaCssClass, schemaId: SchemaId): void {
		this.next(theme, schemaId);
	}

	on(schemaId: SchemaId): Observable<SchemaCssClass> {

		return this.theme$
				   .asObservable()
				   .pipe(
					   this.takeUntil(),
					   map((map: Map<string, SchemaCssClass>) => {
						   return map.get(schemaId.toString());
					   })
					   // distinctUntilChanged()
				   );
	}

	private next(theme: SchemaCssClass, schemaId: SchemaId): void {
		this.theme.set(schemaId.toString(), theme);
		this.theme$.next(this.theme);
	}

}
