import { ElementRef, Input } from '@angular/core';

import { SmartComponent } from '../../../../../common/cdk/smart-component';

import { ChangedValueEmitter } from './changed-value.emitter';
import { EditEventType } from './state/edit.event-type';


export abstract class EditCommunicationComponent<T> extends SmartComponent {

	@Input()
	value: T;

	@Input()
	valueChanges: ChangedValueEmitter<T>;

	@Input()
	status: ChangedValueEmitter<EditEventType>;

	@Input()
	focus: boolean;

	@Input()
	parent: ElementRef;

	protected readonly ENTER_KEY_CODE = 13;

	protected readonly ESC_KEY_CODE = 27;

	submit(): void {
		this.emitStatus(EditEventType.SUBMIT);
	}

	cancel(): void {
		this.emitStatus(EditEventType.CANCEL);
	}

	private emitStatus(status: EditEventType): void {
		if (this.status) {
			this.status.emit(status);
		}
	}

}
