import { Injectable } from '@angular/core';

import { Logger } from '../../../../../common/cdk/logger/logger';

import { VerticalFormation } from './vertical-formation';


@Injectable()
export class VerticalFormationFactory {

	constructor(private logger: Logger) {
	}

	create(enabled: boolean = false,
		   viewportHeight: number = -1,
		   sourceSize: number = 0,
		   scrollPosition: number = 0,
		   rowHeight?: number,
		   hiddenItemsTop?: number,
		   hiddenItemsBottom?: number): VerticalFormation {

		const vf = new VerticalFormation(
			enabled,
			viewportHeight,
			sourceSize,
			scrollPosition,
			rowHeight,
			hiddenItemsTop,
			hiddenItemsBottom
		);

		vf.setLogger(this.logger);

		return vf;
	}

}
