import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	OnChanges,
	OnInit,
	Renderer2,
	SimpleChanges,
	ViewEncapsulation
} from '@angular/core';
import { timer } from 'rxjs';

import { StructureDefinition } from './structure-definition';
import { StructureIdGenerator } from './structure-id.generator';
import { StructureId } from '../domain/structure.id';
import { CompositionId } from '../../composition/domain/composition.id';
import { localProviders } from './local/local-providers';
import { SchemaCssClassManager } from './schema/schema-css-class.manager';
import { StructureCellEditArchive } from './edit/structure.cell-edit.archive';
import { StructureCellEditStore } from './edit/structure.cell-edit.store';
import { StructureEditModeArchive } from './edit/structure.edit-mode.archive';
import { StructureInfoPanelArchive } from './panel/info/structure.info-panel.archive';
import { StructureSummariesArchive } from './panel/summaries/structure.summaries.archive';
import { StructureSummariesConfigService } from './panel/summaries/structure.summaries-config.service';
import { structureComponentToken } from './structure-component-token';
import { StructureGateway } from './gateway/structure.gateway';
import { StructureReadModelRoot } from '../domain-api/read/structure.read-model-root';
import { PagingCommandDispatcher } from '../../../structure/paging/domain-api/paging.command-dispatcher';
import { PagingEventRepository } from '../../../structure/paging/domain-api/paging.event-repository';
import { SourceCommandDispatcher } from '../domain-api/source/source.command-dispatcher';
import { SourceEventService } from '../domain-api/source/event/source-event.service';
import { CompositionCommandDispatcher } from '../../composition/domain-api/composition.command-dispatcher';
import { CompositionEventRepository } from '../../composition/domain-api/composition.event-repository';
import { FormationEventService } from '../domain-api/formation/formation-event.service';
import { StructureCommandDispatcher } from '../domain-api/structure.command-dispatcher';
import { StructureWarehouse } from '../domain-api/structure.warehouse';
import { CompositionWarehouse } from '../../composition/domain-api/composition.warehouse';
import { StructureColumnMenuConfigArchive } from './header/menu/config/structure.column-menu-config.archive';
import { PagingDisplayModeArchive } from '../../../structure/paging/feature/mode/paging-display-mode.archive';
import { StructureRowSelectEnabledArchive } from './content/row/structure.row-select-enabled.archive';
import { StructureSearchEventService } from '../domain-api/search/structure-search-event.service';
import { StructureCellEditCloseAllService } from './edit/structure.cell-edit-close-all.service';
import { SchemaReadModelRootId } from '../../../schema/domain-api/read/schema.read-model-root-id';
import { SchemaCommandDispatcher } from '../../../schema/domain-api/schema.command-dispatcher';
import { StructureHeaderBottomEnabledArchive } from './header/structure-header-bottom-enabled.archive';
import { StructureHeaderTopEnabledArchive } from './header/structure-header-top-enabled.archive';
import { SchemaEventRepository } from '../../../schema/domain-api/schema.event-repository';
import { StructureRowDetailConfigArchive } from './row-detail/structure.row-detail.config-archive';
import { StructureRowDetailService } from './row-detail/structure.row-detail.service';
import { PagingFeatureModule } from '../../../structure/paging/feature/paging.feature-module';
import { StructureTitlePanelConfigArchive } from './panel/title-panel/structure.title-panel.config-archive';
import { StructureFooterPanelConfigArchive } from './panel/footer-panel/structure.footer-panel.config-archive';
import { StructureInfoPanelConfigService } from './panel/info/structure.info-panel-config.service';


/** @internal */
export function structureIdFactory(generator: StructureIdGenerator): StructureId {
	return new StructureId('gui-' + generator.generateId());
}

/** @internal */
export function compositionIdFactory(generator: StructureIdGenerator): CompositionId {
	return new CompositionId('gui-' + generator.generateId());
}

/** @internal */
export function schemaIdFactory(generator: StructureIdGenerator): SchemaReadModelRootId {
	return new SchemaReadModelRootId('gui-' + generator.generateId());
}

/** @internal */
export const structureComponentSelfProviders = [
	{
		provide: StructureId,
		useFactory: structureIdFactory,
		deps: [
			StructureIdGenerator
		]
	},
	{
		provide: CompositionId,
		useFactory: compositionIdFactory,
		deps: [
			StructureIdGenerator
		]
	},
	{
		provide: SchemaReadModelRootId,
		useFactory: schemaIdFactory,
		deps: [
			StructureIdGenerator
		]
	},
	...localProviders,

	SchemaCssClassManager,
	StructureCellEditArchive,
	StructureCellEditCloseAllService,
	StructureCellEditStore,
	StructureEditModeArchive,
	StructureInfoPanelArchive,
	StructureInfoPanelConfigService,
	StructureSummariesArchive,
	StructureSummariesConfigService,
	StructureColumnMenuConfigArchive,

	PagingFeatureModule.forComponent(),

	StructureRowSelectEnabledArchive,
	StructureHeaderTopEnabledArchive,
	StructureHeaderBottomEnabledArchive,
	StructureRowDetailConfigArchive,
	StructureRowDetailService,
	StructureTitlePanelConfigArchive,
	StructureFooterPanelConfigArchive
];

/** @internal */
@Component({
	selector: 'gui-structure',
	templateUrl: './structure.component.html',
	styleUrls: [
		'./style/styles.ngx.scss',
		'./style/structure.ngx.scss',
		'./style/structure-column-manager.ngx.scss',
		'./style/structure-summaries.ngx.scss',
		'./style/structure-info-modal.ngx.scss',
		// '../../../structure/paging/feature/style/structure-paging.ngx.scss',
		'./style/structure-responsive.ngx.scss',
		'./style/structure-flex.ngx.scss',
		'./style/structure-filter.ngx.scss',
		'../../composition/feature/composition.ngx.scss',
		'./style/themes/fabric.structure.ngx.scss',
		'./style/themes/material.structure.ngx.scss',
		'./style/themes/dark.structure.ngx.scss',
		'./style/themes/light.structure.ngx.scss',
		'./style/themes/generic.structure.ngx.scss'
	],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	host: {
		'[class.gui-structure]': `"true"`,
		'[class.gui-structure-border]': 'isBorderEnabled()',
		'[id]': 'structureId.toString()'
	},
	providers: [
		...structureComponentSelfProviders,
		{
			provide: structureComponentToken,
			useExisting: StructureComponent
		}
	]
})
export class StructureComponent extends StructureGateway implements OnChanges, OnInit, AfterViewInit {

	loaderEnabled: boolean = false;

	circleLoaderEnabled: boolean = true;

	initialLoaderAnimation: boolean = false;

	private structure: StructureReadModelRoot;

	constructor(structureId: StructureId,
				compositionId: CompositionId,
				pagingCommandService: PagingCommandDispatcher,
				pagingEventService: PagingEventRepository,
				sourceCommandService: SourceCommandDispatcher,
				sourceEventService: SourceEventService,
				schemaCommandDispatcher: SchemaCommandDispatcher,
				compositionCommandService: CompositionCommandDispatcher,
				compositionEventService: CompositionEventRepository,
				formationEventService: FormationEventService,
				structureCommandService: StructureCommandDispatcher,
				structureEditModeArchive: StructureEditModeArchive,
				structureCellEditArchive: StructureCellEditArchive,
				structureInfoPanelArchive: StructureInfoPanelArchive,
				structureInfoPanelConfigService: StructureInfoPanelConfigService,
				structureSummariesConfigService: StructureSummariesConfigService,
				structureCellEditStore: StructureCellEditStore,
				structureColumnMenuConfigArchive: StructureColumnMenuConfigArchive,
				structurePagingDisplayModeArchive: PagingDisplayModeArchive,
				structureRowSelectEnabledArchive: StructureRowSelectEnabledArchive,
				structureSearchEventService: StructureSearchEventService,
				structureHeaderTopEnabledArchive: StructureHeaderTopEnabledArchive,
				structureHeaderBottomEnabledArchive: StructureHeaderBottomEnabledArchive,
				structureDetailViewConfigArchive: StructureRowDetailConfigArchive,
				structureTitlePanelConfigArchive: StructureTitlePanelConfigArchive,
				structureFooterPanelConfigArchive: StructureFooterPanelConfigArchive,
				schemaEventRepository: SchemaEventRepository,
				private readonly elementRef: ElementRef,
				private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly renderer: Renderer2,
				private readonly structureDefinition: StructureDefinition,
				private readonly structureReadModelService: StructureWarehouse,
				private readonly compositionReadModelService: CompositionWarehouse,
				private readonly schemaStylesManager: SchemaCssClassManager,
				private readonly schemaReadModelRootId: SchemaReadModelRootId,
				private readonly structureDetailViewService: StructureRowDetailService) {

		super(
			structureId,
			compositionId,
			schemaReadModelRootId,
			structureCommandService,
			pagingCommandService,
			pagingEventService,
			sourceCommandService,
			sourceEventService,
			schemaCommandDispatcher,
			compositionCommandService,
			compositionEventService,
			formationEventService,
			structureEditModeArchive,
			structureCellEditArchive,
			structureInfoPanelArchive,
			structureInfoPanelConfigService,
			structureSummariesConfigService,
			structureCellEditStore,
			structureColumnMenuConfigArchive,
			structurePagingDisplayModeArchive,
			structureRowSelectEnabledArchive,
			structureSearchEventService,
			structureHeaderTopEnabledArchive,
			structureHeaderBottomEnabledArchive,
			structureDetailViewConfigArchive,
			structureTitlePanelConfigArchive,
			structureFooterPanelConfigArchive,
			schemaEventRepository
		);

		structureCommandService.createStructure();
		compositionCommandService.createComposition();
		schemaCommandDispatcher.create();
	}

	ngOnChanges(changes: SimpleChanges) {
		super.ngOnChanges(changes);
	}

	ngOnInit() {
		super.ngOnInit();

		this.structureReadModelService
			.onStructure()
			.pipe(this.takeUntil())
			.subscribe((structureReadModelRoot: StructureReadModelRoot) => {
				this.structure = structureReadModelRoot;

				this.loaderEnabled = structureReadModelRoot.getSource().isLoading();

				this.circleLoaderEnabled = structureReadModelRoot.isLoaderVisible();

				if (this.loaderEnabled && !this.initialLoaderAnimation) {
					this.initialLoaderAnimation = true;
				}
			});

		this.structureDetailViewService.init(this.elementRef);
	}

	ngAfterViewInit() {
		let width = this.elementRef.nativeElement.offsetWidth;

		if (width > 0) {
			this.compositionCommandService.setContainerWidth(width);
		} else {

			/**
			 * When gui-grid is in dynamic container which is created later then grid.
			 */
			timer(0)
				.pipe(
					this.takeUntil()
				)
				.subscribe(() => {

					width = this.elementRef.nativeElement.offsetWidth;

					if (width > 0) {
						this.compositionCommandService.setContainerWidth(width);
					}
				});
		}

		this.schemaStylesManager
			.init(this.elementRef, this.schemaReadModelRootId);

		this.compositionReadModelService
			.onWidth()
			.pipe(
				this.takeUntil()
			)
			.subscribe((width: number) => {
				this.renderer.setStyle(this.elementRef.nativeElement, 'width', width + 'px');
			});
	}

	isBorderEnabled(): boolean {
		return this.structureDefinition.isBorderEnabled();
	}

	getStructureId(): StructureId {
		return this.structureId;
	}

	getElementRef(): ElementRef {
		return this.elementRef
	}

}
