import { FilterId } from './filter.id';
import { FieldId } from '../field/data-type/field.id';
import { OriginItemEntity } from '../../source/origin/origin-item-entity';
import { TypeField } from '../field/data-type/type.field';

export class StructureFilter {

	private readonly filterId: FilterId;

	private field: TypeField;

	private filterValue: any;

	constructor(id: FilterId,
				field: TypeField,
				filterValue: any) { // TODO not any
		this.filterId = id;
		this.field = field;
		this.filterValue = filterValue;
	}

	getFieldId(): FieldId {
		return this.field.getId();
	}

	getFilterValue(): any {
		return this.filterValue;
	}

	setFilterValue(filterValue: any): void {
		this.filterValue = filterValue;
	}

	// Move to container
	filter(entities: Array<OriginItemEntity>): Array<OriginItemEntity> {

		if (entities.length === 0) {
			return entities;
		}

		return entities.filter((item: OriginItemEntity) => {
			return this.field.filter(item, this.filterValue);
		});
	}

}
