import { DomainEvent } from '@generic-ui/hermes';

import { OriginItemEntity } from '../origin-item-entity';
import { StructureId } from '../../../structure.id';


export class StructureSourceItemEditedEvent extends DomainEvent<StructureId> {

	constructor(aggregateId: StructureId,
				private readonly beforeItem: OriginItemEntity,
				private readonly afterItem: OriginItemEntity) {
		super(aggregateId, 'StructureSourceItemEditedEvent');
	}

	getBeforeItem(): OriginItemEntity {
		return this.beforeItem;
	}

	getAfterItem(): OriginItemEntity {
		return this.afterItem;
	}

}
