import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { StructureSetConfigSearchingCommand } from './structure.set-config-searching.command';
import { StructureConfigSearchingSetEvent } from './structure.config-searching-set.event';
import { StructureAggregate } from '../../structure.aggregate';


@Injectable()
export class StructureSetConfigSearchingCommandHandler implements CommandHandler<StructureAggregate, StructureSetConfigSearchingCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<StructureSetConfigSearchingCommand> {
		return StructureSetConfigSearchingCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: StructureSetConfigSearchingCommand): void {

		const searchingConfig = command.getConfig();

		this.domainEventPublisher.publish(new StructureConfigSearchingSetEvent(command.getAggregateId(), searchingConfig.enabled));
	}

	handleAggregate(aggregate: StructureAggregate, command: StructureSetConfigSearchingCommand): void {

		const searchingConfig = command.getConfig();

		aggregate.setSearchingConfig(searchingConfig);
	}

}
