import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { StructureSetHeightCommand } from './structure-set-height.command';
import { StructureHeightSetEvent } from './structure-height-set.event';
import { StructureAggregate } from '../../../structure.aggregate';


@Injectable()
export class StructureSetHeightCommandHandler implements CommandHandler<StructureAggregate, StructureSetHeightCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<StructureSetHeightCommand> {
		return StructureSetHeightCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: StructureSetHeightCommand): void {
		this.domainEventPublisher.publish(new StructureHeightSetEvent(command.getAggregateId()));
	}

	handleAggregate(aggregate: StructureAggregate, command: StructureSetHeightCommand): void {

		const height = command.getHeight();

		aggregate.setHeight(height);
	}

}
