import { Injectable } from '@angular/core';

import { AggregateFactory } from '@generic-ui/hermes';

import { CompositionId } from './composition.id';
import { CompositionAggregate } from './composition.aggregate';
import { ColumnEntityFactory } from './column/column-entity.factory';


@Injectable()
export class CompositionAggregateFactory extends AggregateFactory<CompositionId, CompositionAggregate> {

	constructor(private readonly columnFactory: ColumnEntityFactory) {
		super();
	}

	create(id: CompositionId): CompositionAggregate {
		return new CompositionAggregate(id, this.columnFactory);
	}
}
