import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';
import { NextPageCommand } from './next-page.command';
import { NextPageEvent } from './next-page.event';
import { StructureAggregate } from '../../../../lib/structure/domain/structure/structure.aggregate';


@Injectable()
export class NextPageCommandHandler implements CommandHandler<StructureAggregate, NextPageCommand> {

	constructor(private readonly domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<NextPageCommand> {
		return NextPageCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: NextPageCommand): void {
		this.domainEventPublisher.publish(new NextPageEvent(command.getAggregateId()));
	}

	handleAggregate(aggregate: StructureAggregate, command: NextPageCommand): void {

		// missing implementation
		aggregate.nextPage();
	}

}
