import { Injectable } from '@angular/core';

import { GridComponent } from '../grid.component';

@Injectable()
export class GridRegister {

	private readonly gridMap = new Map();

	// structureId: StructureId
	register(gridId: string, component: GridComponent, structureId: any): void {
		this.gridMap.set(gridId, { component, structureId });
	}

	unregister(gridId: string): void {
		this.gridMap.delete(gridId);
	}

	getValues(gridId: string): any {
		return this.gridMap.get(gridId);
	}

}
