import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { distinctUntilChanged, map } from 'rxjs/operators';

import { SchemaId } from '../../domain/schema.id';
import { Reactive } from '../../../common/cdk/reactive';
import { SchemaTheme } from '../../domain/theme/schema-theme';


@Injectable()
export class SchemaThemeRepository extends Reactive {

	private theme = new Map<string, SchemaTheme>();

	private theme$ = new ReplaySubject<Map<string, SchemaTheme>>();

	constructor() {
		super();
	}

	setTheme(theme: SchemaTheme, schemaId: SchemaId): void {
		this.next(theme, schemaId);
	}

	on(schemaId: SchemaId): Observable<SchemaTheme> {

		return this.theme$
				   .asObservable()
				   .pipe(
					   this.takeUntil(),
					   map((map: Map<string, SchemaTheme>) => {
						   return map.get(schemaId.toString());
					   }),
					   distinctUntilChanged()
				   );
	}

	private next(theme: SchemaTheme, schemaId: SchemaId): void {
		this.theme.set(schemaId.toString(), theme);
		this.theme$.next(this.theme);
	}

}
