import { DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../structure.id';

export const StructureSummariesEnabledSetEventName = 'StructureSummariesEnabledSetEvent';

export class StructureSummariesEnabledSetEvent extends DomainEvent<StructureId> {

	constructor(aggregateId: StructureId,
				private readonly enabled: boolean) {
		super(aggregateId, StructureSummariesEnabledSetEventName);
	}

	isEnabled(): boolean {
		return this.enabled;
	}

}
