import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { FormationRepository } from './read/formation-repository';
import { StructureId } from '../../domain/structure.id';
import { structureGlobalId } from '../structure.global-id';
import { SelectedRowsList } from './read/selected-rows/selected-rows-list';
import { Formation } from './read/formation';


@Injectable()
export class StructureFormationWarehouse {

	constructor(private formationRepository: FormationRepository) {
	}

	onSelectedRows(structureId: StructureId = structureGlobalId): Observable<SelectedRowsList> {
		return this.formationRepository
				   .onFormation(structureId)
				   .pipe(
					   map((formation: Formation) => formation.getSelectedRows())
				   );
	}

}
