import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { take } from 'rxjs/operators';

import { PagingRepository } from './read/paging.repository';
import { StructureId } from '../../../lib/structure/domain/structure.id';
import { structureGlobalId } from '../../../lib/structure/domain-api/structure.global-id';
import { Paging } from './read/paging';


@Injectable()
export class PagingWarehouse {

	constructor(private pagingRepository: PagingRepository) {
	}

	onPaging(structureId: StructureId = structureGlobalId): Observable<Paging> {
		return this.pagingRepository.on(structureId);
	}

	onSinglePaging(structureId: StructureId = structureGlobalId): Observable<Paging> {
		return this.onPaging(structureId)
				   .pipe(
					   take(1)
				   );
	}

}
