import { Entity } from '@generic-ui/hermes';

import { ColumnId } from './column.id';
import { ColumnField } from './field/column-field';
import { ColumnConfig, HeaderTemplate } from './column.config';
import { ColumnAlign } from './column-align';
import { ViewEntity } from './view.entity';
import { DataType } from '../../../structure/domain/structure/field/data-type/data-type';
import { CellView } from './cell-view';
import { ColumnPresentation } from './presentation/column.presentation';

export abstract class AbstractColumnEntity extends Entity<ColumnId> {

	private columnField: ColumnField;

	private header?: string | HeaderTemplate;

	private width?: number;

	protected align: ColumnAlign;

	private columnConfig: ColumnConfig;

	protected view: ViewEntity;

	protected presentation: ColumnPresentation;

	protected constructor(columnId: ColumnId,
						  columnField: ColumnField,
						  columnConfig: ColumnConfig,
						  presentation: ColumnPresentation,
						  header?: string | HeaderTemplate,
						  align?: ColumnAlign,
						  view?: ViewEntity,
						  width?: number) {
		super(columnId);
		this.columnField = columnField;
		this.columnConfig = columnConfig;
		this.presentation = presentation;

		this.view = ViewEntity.fromDataType(columnField.getDataType());

		if (header) {
			this.header = header;
		}

		if (view) {
			this.view = view;
		}

		if (width) {
			this.width = width;
		}

		this.setInitialAlign(columnField, align);
	}

	getPresentation(): ColumnPresentation {
		return this.presentation;
	}

	getColumnConfig(): ColumnConfig {
		return this.columnConfig;
	}

	getField(): ColumnField {
		return this.columnField;
	}

	getHeader(): string | HeaderTemplate {
		return this.header;
	}

	getDataType(): DataType {
		return this.columnField.getDataType();
	}

	getCellView(): CellView {
		return this.view.getCellView();
	}

	getTemplateFunction(): any {
		return this.view.getTemplateFunction();
	}

	setView(view: ViewEntity): void {
		this.view = view;
	}

	setHeader(header: string | HeaderTemplate): void {
		this.header = header;
	}

	getView(): ViewEntity {
		return this.view;
	}

	getAlign(): ColumnAlign {
		return this.align;
	}

	setWidth(width: number): void {
		this.width = width;
	}

	getWidth(): number {
		return this.width;
	}

	getSortingEnabled(): boolean {
		const config = this.columnConfig;

		if (config.sorting !== undefined && config.sorting !== null) {

			if (config.sorting.enabled === undefined || config.sorting.enabled === null) {
				return true;
			}
			return config.sorting.enabled;
		}
	}

	isCellEditingEnabled(): boolean {
		const config = this.columnConfig;

		if (config.cellEditing !== undefined && config.cellEditing !== null) {

			if (config.cellEditing.enabled === undefined || config.cellEditing.enabled === null) {
				return true;
			}
			return config.cellEditing.enabled;
		}

		return true;
	}

	private setInitialAlign(columnField: ColumnField, align?: ColumnAlign): void {

		if (align !== undefined && align !== null) {
			this.align = align;
		} else if (columnField.getDataType() === DataType.NUMBER) {
			this.align = ColumnAlign.RIGHT;
		} else {
			this.align = ColumnAlign.LEFT;
		}
	}

}
