import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';
import { SetSortingCommand } from './set-sorting.command';
import { SortingSetEvent } from './sorting-set.event';
import { StructureAggregate } from '../../structure.aggregate';


@Injectable()
export class SetSortingCommandHandler implements CommandHandler<StructureAggregate, SetSortingCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<SetSortingCommand> {
		return SetSortingCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: SetSortingCommand): void {
		this.domainEventPublisher.publish(new SortingSetEvent(command.getAggregateId()));
	}

	handleAggregate(aggregate: StructureAggregate, command: SetSortingCommand): void {

		const sortingConfig = command.getSortingConfig();

		aggregate.setSortingConfig(sortingConfig);
	}

}
