import { AllFieldTypes } from './all-field-types';
import { DataType } from './data-type';
import { NumberField } from './fields/number-field';
import { OriginItemEntity } from '../../../source/origin/origin-item-entity';
import { BooleanField } from './fields/boolean-field';
import { StringField } from './fields/string-field';
import { DateField } from './fields/date-field';
import { BaseField } from './base.field';


export class TypeField extends BaseField {

	static of(field: AllFieldTypes): TypeField {
		return new TypeField(field);
	}

	// private readonly allDataTypes = [
	// 	// DataType
	// ]

	private readonly field: AllFieldTypes;

	constructor(field: AllFieldTypes) {
		super(
			field.getId(),
			field.getAccessor(),
			field.getDataType(),
			field.getMatchers()
		);
		this.field = field;
	}

	mapNumber<U>(callback: (field: NumberField) => U): U {
		if (this.field.getDataType() === DataType.NUMBER) {
			return callback(this.field as NumberField);
		}
	}

	ifNumber(): NumberField {
		if (this.field.getDataType() === DataType.NUMBER) {
			return (this.field as NumberField);
		}
	}

	/**
	 * @deprecated
	 */
	getField(): AllFieldTypes {
		return this.field;
	}

	search(item: OriginItemEntity, searchPhrase: string): boolean {
		return this.field.search(item, searchPhrase);
	}

	filter(item: OriginItemEntity, filterPhrase: any): boolean {

		const value = this.field.getValue(item);

		if (this.field.getDataType() === DataType.NUMBER) {
			return (this.field as NumberField).filter(value, filterPhrase);
		} else if (this.field.getDataType() === DataType.BOOLEAN) {
			return (this.field as BooleanField).filter(value, filterPhrase);
		} else if (this.field.getDataType() === DataType.STRING) {
			return (this.field as StringField).filter(value, filterPhrase);
		} else if (this.field.getDataType() === DataType.DATE) {
			return (this.field as DateField).filter(value, filterPhrase);
		} else {
			return true;
		}
	}

	sort(entityOne: OriginItemEntity, entityTwo: OriginItemEntity, direction: boolean): number {

		let valueOne = direction ? this.field.getSortValue(entityOne) : this.field.getSortValue(entityTwo),
			valueTwo = direction ? this.field.getSortValue(entityTwo) : this.field.getSortValue(entityOne);

		if (this.field.getDataType() === DataType.NUMBER) {
			return (this.field as NumberField).sort(valueOne, valueTwo);
		} else if (this.field.getDataType() === DataType.BOOLEAN) {
			return (this.field as BooleanField).sort(valueOne, valueTwo);
		} else if (this.field.getDataType() === DataType.STRING) {
			return (this.field as StringField).sort(valueOne, valueTwo);
		} else if (this.field.getDataType() === DataType.DATE) {
			return (this.field as DateField).sort(valueOne, valueTwo);
		} else {
			return 0;
		}
	}

}
