import { FieldId } from './field.id';
import { DataType } from './data-type';
import { OriginItemEntity } from '../../../source/origin/origin-item-entity';
import { SummariesType } from '../../summaries/summaries.type';
import { ColumnSummariesConfig } from '../../../../../composition/domain/column/column-summaries.config';
import { FieldMatchers } from '../matcher/field.matchers';
import { BaseField } from './base.field';

export abstract class Field<T> extends BaseField {

	private summariesEnabled: boolean = true;

	private summariesTypes: SummariesType;

	private readonly possibleSummaries: SummariesType;

	protected constructor(id: FieldId,
						  accessor: any,
						  dataType: DataType,
						  matchers: FieldMatchers,
						  summariesConfig?: ColumnSummariesConfig) {
		super(id,
			accessor,
			dataType,
			matchers);

		this.possibleSummaries = this.assignPossibleSummaries();

		this.assignSummaries(summariesConfig);
	}

	abstract assignDefaultSummaries(): SummariesType;

	abstract assignPossibleSummaries(): SummariesType;

	abstract search(item: OriginItemEntity, searchPhrase: string): boolean;

	abstract sort(one: T, two: T): number;

	abstract filter(value: T, filterPhrase: T): boolean;

	isSummaries(type: SummariesType): boolean {

		if (!this.isSummariesEnabled()) {
			return false;
		}

		return !!(this.summariesTypes & type);
	}

	isSummariesEnabled(): boolean {
		return this.summariesEnabled;
	}

	setSummariesEnabled(enabled: boolean): void {
		this.summariesEnabled = enabled;
	}

	private assignSummaries(summariesConfig: ColumnSummariesConfig): void {

		if (!!summariesConfig && Object.keys(summariesConfig).length !== 0) {

			if (summariesConfig.enabled) {
				this.summariesEnabled = !!summariesConfig.enabled;
			}

			if (summariesConfig.summariesTypes) {

				this.summariesTypes = 0;

				summariesConfig.summariesTypes.forEach((t: any) => {
					this.summariesTypes |= t;
				});
			} else {
				this.summariesTypes = this.assignDefaultSummaries();
			}
		} else {
			this.summariesTypes = this.assignDefaultSummaries();
		}
	}
}
