import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { PrevPageCommand } from './prev-page.command';
import { PrevPageEvent } from './prev-page.event';
import { StructureAggregate } from '../../../../lib/structure/domain/structure/structure.aggregate';


@Injectable()
export class PrevPageCommandHandler implements CommandHandler<StructureAggregate, PrevPageCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<PrevPageCommand> {
		return PrevPageCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: PrevPageCommand): void {
		this.domainEventPublisher.publish(new PrevPageEvent(command.getAggregateId()));
	}

	handleAggregate(structure: StructureAggregate, command: PrevPageCommand): void {

		structure.prevPage();
	}

}
