import { DomainEvent } from '@generic-ui/hermes';

import { OriginItemEntity } from '../origin-item-entity';
import { StructureId } from '../../../structure.id';


export class OriginSetEvent extends DomainEvent<StructureId> {

	constructor(aggregateId: StructureId,
				private readonly origin: Array<OriginItemEntity>) {
		super(aggregateId, 'OriginSetEvent');
	}

	getOrigin(): Array<OriginItemEntity> {
		return this.origin;
	}
}
