import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { CompositionAggregateRepository } from '../../composition.aggregate-repository';
import { SetCompositionWidthCommand } from './set-composition-width.command';
import { CompositionWidthSetEvent } from './composition-width-set.event';
import { CompositionAggregate } from '../../composition.aggregate';

@Injectable()
export class SetCompositionWidthCommandHandler implements CommandHandler<CompositionAggregate, SetCompositionWidthCommand> {

	constructor(compositionAggregateRepository: CompositionAggregateRepository,
				private readonly domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<SetCompositionWidthCommand> {
		return SetCompositionWidthCommand;
	}

	publishDomainEvents(aggregate: CompositionAggregate, command: SetCompositionWidthCommand): void {
		this.domainEventPublisher.publish(new CompositionWidthSetEvent(command.getAggregateId()));
	}

	handleAggregate(composition: CompositionAggregate, command: SetCompositionWidthCommand): void {

		const width = command.getWidth();

		composition.setWidth(width);
	}

}
