import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { map } from 'rxjs/operators';

import { structureGlobalId } from '../../structure.global-id';
import { StructureId } from '../../../domain/structure.id';


@Injectable()
export class StructureSearchHighlightArchive {

	private static readonly HIGHLIGHTING = true;

	private readonly idToHighlight = new Map<string, boolean>();

	private readonly idToHighlight$ = new ReplaySubject<Map<string, boolean>>();

	next(value: boolean, structureId: StructureId = structureGlobalId): void {
		this.idToHighlight.set(structureId.getId(), value);
		this.idToHighlight$.next(this.idToHighlight);
	}

	onValue(structureId: StructureId = structureGlobalId): Observable<boolean> {
		return this.idToHighlight$
				   .asObservable()
				   .pipe(
					   map((idToHighlight: Map<string, boolean>) => {
						   return idToHighlight.get(structureId.getId()) || StructureSearchHighlightArchive.HIGHLIGHTING;
					   })
				   );
	}

}
