import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, switchMap, take } from 'rxjs/operators';

import { DomainEvent, DomainEventBus } from '@generic-ui/hermes';

import { StructureId } from '../../domain/structure.id';
import { StructureSearchPhraseSetDomainEvent } from '../../domain/structure/search/set-phrase/structure.search-phrase-set.domain-event';
import { StructureSearchPhraseRepository } from './phrase/structure.search-phrase.repository';


@Injectable()
export class StructureSearchEventService {

	constructor(private readonly domainEventBus: DomainEventBus,
				private readonly structureSearchPhraseRepository: StructureSearchPhraseRepository) {
	}

	onSearchPhrase(structureId: StructureId): Observable<string> {

		return this.domainEventBus
				   .ofEvent(
					   StructureSearchPhraseSetDomainEvent
				   )
				   .pipe(
					   filter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === structureId.toString()),
					   switchMap((event: DomainEvent<StructureId>) => {

						   return this.structureSearchPhraseRepository
									  .onPhrase(structureId)
									  .pipe(
										  take(1)
									  );
					   })
				   );
	}
}
