import { SummariesValues } from './summaries.values';
import { DataType } from '../../field/data-type/data-type';
import { OriginItemEntity } from '../../../source/origin/origin-item-entity';
import { SummariesType } from '../summaries.type';
import { AllFieldTypes } from '../../field/data-type/all-field-types';


export abstract class SummariesCalculator<T, A extends SummariesValues> {

	abstract forDataType(dataType: DataType): boolean;

	abstract prepare(field: AllFieldTypes): void;

	abstract postCalculate(field: AllFieldTypes, items: Array<OriginItemEntity>): void;

	abstract aggregate(field: AllFieldTypes, value: T): void;

	abstract generateAggregatedValues(field: AllFieldTypes): A;

	calculate(fields: Array<AllFieldTypes>, items: Array<OriginItemEntity>): Map<string, A> {

		const filteredFields = fields.filter((field: AllFieldTypes) => this.forDataType(field.getDataType()));

		if (!filteredFields || filteredFields.length === 0 || items.length === 0) {
			return null;
		}

		let count = new Map<string, number>(),
			distinct = new Map<string, Set<string>>();

		// init
		filteredFields.forEach((field: AllFieldTypes) => {

			const key = field.getKey();

			count.set(key, 0);
			distinct.set(key, new Set());

			this.prepare(field);
		});

		// calculate
		items.forEach((item: OriginItemEntity) => {

			filteredFields.forEach((field: AllFieldTypes) => {

				const key = field.getKey(),
					value = field.getValue(item);

				if (value !== null || value !== undefined || value !== '') {

					if (field.isSummaries(SummariesType.COUNT)) {
						const countForField = count.get(key);
						count.set(key, countForField + 1);
					}

					if (field.isSummaries(SummariesType.DISTINCT)) {
						const distinctSet = distinct.get(key);
						distinctSet.add(value);
					}
				}

				this.aggregate(field, value);
			});
		});

		filteredFields.forEach((field: AllFieldTypes) => {

			this.postCalculate(field, items);

		});

		let summaries = new Map();

		filteredFields.forEach((field: AllFieldTypes) => {

			const key = field.getId().getId();

			let aggregatedValues = this.generateAggregatedValues(field);

			if (field.isSummaries(SummariesType.COUNT)) {
				aggregatedValues.setCount(count.get(key));
			}

			if (field.isSummaries(SummariesType.DISTINCT)) {
				aggregatedValues.setDistinct((distinct.get(key)).size);
			}

			summaries.set(key, aggregatedValues);
		});

		return summaries;
	}

}
