import { ColumnWidth } from './column-width';
import { ColumnConfig } from '../column.config';
import { CompositionContainerWidthCalculator } from '../../width/composition-container-width.calculator';
import { CompositionWidthCalculator } from '../../width/composition-width.calculator';


export class ColumnWidthCollection {

	private baseColumnWidths: Array<ColumnWidth>;

	private columnWidths: Array<ColumnWidth>;

	private width: number;

	constructor(private readonly MIN_COLUMN_WIDTH: number,
				columns: Array<ColumnConfig> = [],
				width: number = 100) {
		this.baseColumnWidths = columns.map((column: ColumnConfig) => new ColumnWidth(column.width));
		this.columnWidths = columns.map((column: ColumnConfig) => new ColumnWidth(column.width));

		this.setContainerWidth(width);

		this.calculate();
	}

	getColumnWidths(): Array<ColumnWidth> {
		return this.baseColumnWidths;
	}

	getColumnsWidth(): number {
		return this.width;
	}

	getWidths(): Array<number> {
		return this.columnWidths.map(c => c.getWidth());
	}

	getMinWidth(): number {
		const calculator = new CompositionContainerWidthCalculator(this.MIN_COLUMN_WIDTH);

		return calculator.calculateMinWidth(this.columnWidths);
	}

	setWidth(width: number): void {

		this.setContainerWidth(width);
		this.calculate();
	}

	setColumns(columnConfigs: Array<ColumnConfig>): void {
		this.baseColumnWidths = columnConfigs.map((column: ColumnConfig) => new ColumnWidth(column.width));
		this.columnWidths = columnConfigs.map((column: ColumnConfig) => new ColumnWidth(column.width));

		this.calculate();
	}

	getMinColumnWidth(): number {
		return this.MIN_COLUMN_WIDTH;
	}

	private calculate(): void {

		if (this.baseColumnWidths && this.width) {

			const calculator = new CompositionWidthCalculator(this.baseColumnWidths, this.width, this.MIN_COLUMN_WIDTH);

			this.columnWidths = calculator.calculate();
		}
	}

	private setContainerWidth(width: number): void {
		if (this.getMinWidth() > width) {
			this.width = this.getMinWidth();
		} else {
			this.width = width;
		}

		this.calculate();
	}
}
