import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { SetVerticalScrollEnabledCommand } from './set-vertical-scroll-enabled.command';
import { VerticalScrollEnabledSetEvent } from './vertical-scroll-enabled-set.event';
import { StructureAggregate } from '../../structure.aggregate';


@Injectable()
export class SetVerticalScrollEnabledCommandHandler implements CommandHandler<StructureAggregate, SetVerticalScrollEnabledCommand> {

	constructor(private readonly domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<SetVerticalScrollEnabledCommand> {
		return SetVerticalScrollEnabledCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: SetVerticalScrollEnabledCommand): void {
		this.domainEventPublisher.publish(new VerticalScrollEnabledSetEvent(command.getAggregateId()));
	}

	handleAggregate(structure: StructureAggregate, command: SetVerticalScrollEnabledCommand): void {

		const enabled = command.isEnabled();

		structure.setVerticalFormationEnabled(enabled);
	}

}
