import { ElementRef, SimpleChanges, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { fromEvent } from 'rxjs';
import { filter } from 'rxjs/operators';

import { EditCommunicationComponent } from '../edit-communication.component';


export abstract class InputEditTemplateComponent<T> extends EditCommunicationComponent<T> {

	@ViewChild('input', { static: true })
	inputRef: ElementRef;

	filterForm: FormGroup;

	readonly filterFieldName = 'phrase';

	protected constructor(private formBuilder: FormBuilder) {
		super();
		this.filterForm = this.formBuilder.group({
			[this.filterFieldName]: ['']
		});
	}

	ngOnChanges(changes: SimpleChanges) {
		if (changes.value !== undefined) {
			this.filterForm.get(this.filterFieldName).setValue(this.value);
		}
	}

	ngOnInit() {
		this.observeChanges();
	}

	ngAfterViewInit() {
		const inputElement = this.inputRef.nativeElement;

		this.focusField(inputElement);

		this.emitValueChange(inputElement.value);

		fromEvent(inputElement, 'blur')
			.pipe(
				this.takeUntil()
			)
			.subscribe(() => {
				this.unsubscribe();
				this.submit();
			});

		const keyup$ = fromEvent(inputElement, 'keyup');

		keyup$
			.pipe(
				filter((e: any) => e.keyCode === this.ENTER_KEY_CODE),
				this.takeUntil()
			)
			.subscribe(() => {
				this.unsubscribe();
				this.submit();
			});

		keyup$
			.pipe(
				filter((e: any) => e.keyCode === this.ESC_KEY_CODE),
				this.takeUntil()
			)
			.subscribe(() => {
				this.unsubscribe();
				this.cancel();
			});
	}

	focusField(inputElement: HTMLElement): void {
		if (this.focus) {
			inputElement.focus();
		}
	}

	private observeChanges() {
		this.filterForm
			.controls[this.filterFieldName]
			.valueChanges
			.pipe(
				this.takeUntil()
			)
			.subscribe((value: string) => {
				this.emitValueChange(value);
			});
	}

	private emitValueChange(value: any): void {
		if (this.valueChanges) {
			this.valueChanges.emit(value);
		}
	}
}
