import { Injectable, Type } from '@angular/core';

import { AggregateEvent, Command, CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { CompositionAggregateRepository } from '../../composition.aggregate-repository';
import { CompositionEventConverter } from '../../composition.event-converter';
import { CompositionAggregate } from '../../composition.aggregate';
import { CompositionSetColumnEnabledCommand } from './composition.set-column-enabled.command';
import { CompositionId } from '../../composition.id';

@Injectable()
export class CompositionSetColumnEnabledCommandHandler implements CommandHandler<CompositionAggregate, CompositionSetColumnEnabledCommand> {

	constructor(private readonly compositionAggregateRepository: CompositionAggregateRepository,
				private readonly domainEventPublisher: DomainEventPublisher,
				private readonly compositionEventConverter: CompositionEventConverter) {
	}

	forCommand(): Type<CompositionSetColumnEnabledCommand> {
		return CompositionSetColumnEnabledCommand;
	}

	publishDomainEvents(aggregate: CompositionAggregate, command: Command): void {
		this.publishEvents(aggregate, command);
	}

	handleAggregate(aggregate: CompositionAggregate, command: CompositionSetColumnEnabledCommand): void {

		const columnId = command.getColumnId(),
			enabled = command.isEnabled();

		aggregate.enableColumn(columnId, enabled);
	}

	private publishEvents(composition: CompositionAggregate, command: Command): void {

		const events = composition.getEvents() as Array<AggregateEvent<CompositionId>>;

		const domainEvents = this.compositionEventConverter.convert(events);

		if (domainEvents && domainEvents.length > 0) {
			this.domainEventPublisher.publish(domainEvents);
		}
	}
}
