import { Injectable } from '@angular/core';

import { DomainEvent, DomainEventHandler } from '@generic-ui/hermes';

import { FieldsInitedEvent } from '../../../../structure/domain/structure/field/init/fields-inited.event';

import { CompositionDispatcher } from '../../composition.dispatcher';
import { ColumnFieldFactory } from '../field/colum-field.factory';
import { ColumnFieldId } from '../field/column-field.id';
import { ColumnConfig } from '../column.config';
import { ColumnParams } from './column.params';
import { TypeField } from '../../../../structure/domain/structure/field/data-type/type.field';
import { CompositionId } from '../../composition.id';


@Injectable()
export class CompositionFieldsInitedEventHandler extends DomainEventHandler<CompositionId, FieldsInitedEvent> {

	constructor(private readonly compositionDispatcher: CompositionDispatcher,
				private readonly columnFieldFactory: ColumnFieldFactory) {
		super();
	}

	handle(event: FieldsInitedEvent): void {

		if (event.ofMessageType('FieldsInitedEvent')) {

			// TODO Move to different layer
			const fields = event.getFields(),
				columnFieldIds = this.convertColumnFieldIds(fields),
				columns = event.columns,
				params = this.convertColumns(columns, fields, columnFieldIds);

			this.compositionDispatcher.setColumns(event.compositionId, params);
		}
	}

	private convertColumnFieldIds(fields: Array<TypeField>): Array<ColumnFieldId> {

		if (!fields) {
			return [];
		}

		return fields.map((field: TypeField) => new ColumnFieldId(field.getId().getId()));
	}

	private convertColumns(columns: Array<ColumnConfig>, fields: Array<TypeField>, columnFieldIds: Array<ColumnFieldId>): Array<ColumnParams> {

		if (!columns) {
			return [];
		}

		return columns.map((column: ColumnConfig, index: number) => {

			const columnFieldId = columnFieldIds[index],
				field = fields[index];

			const columnField = this.columnFieldFactory.create(columnFieldId, field.getAccessorMethod(), field.getDataType(), field.getSearchAccessorMethod());

			return new ColumnParams(column, columnField);
		});

	}
}
