import { ElementRef, Injectable, Renderer2, RendererFactory2 } from '@angular/core';

import { SchemaCssClass } from '../../../../schema/domain-api/css-classes/schema-css-class';
import { SchemaTheme } from '../../../../schema/domain/theme/schema-theme';
import { SchemaRowColoring } from '../../../../schema/domain/coloring/schema-row-coloring';
import { SchemaWarehouse } from '../../../../schema/domain-api/schema.warehouse';
import { SchemaReadModelRootId } from '../../../../schema/domain-api/read/schema.read-model-root-id';
import { Reactive } from '../../../../common/cdk/reactive';


@Injectable()
export class SchemaCssClassManager extends Reactive {

	private readonly VERTICAL_GRID_CLASS_NAME = 'gui-vertical-grid';

	private readonly HORIZONTAL_GRID_CLASS_NAME = 'gui-horizontal-grid';

	private readonly THEME_FABRIC_CLASS_NAME = 'gui-fabric';

	private readonly THEME_MATERIAL_CLASS_NAME = 'gui-material';

	private readonly THEME_LIGHT_CLASS_NAME = 'gui-light';

	private readonly THEME_DARK_CLASS_NAME = 'gui-dark';

	private readonly THEME_GENERIC_CLASS_NAME = 'gui-generic';

	private readonly ROW_COLORING_ODD = 'gui-rows-odd';

	private readonly ROW_COLORING_EVEN = 'gui-rows-even';

	private readonly renderer: Renderer2;

	private cssClass: SchemaCssClass = null;

	private cssHostRef: ElementRef;

	constructor(private readonly rendererFactory2: RendererFactory2,
				private readonly schemaReadModelRepository: SchemaWarehouse) {
		super();
		this.renderer = this.rendererFactory2.createRenderer(null, null);
	}

	init(elementRef: ElementRef, schemaReadModelRootId: SchemaReadModelRootId): void {

		this.cssHostRef = elementRef;

		this.schemaReadModelRepository
			.onCssClasses(schemaReadModelRootId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((classes: SchemaCssClass) => {

				const diff = this.updateState(classes);

				this.renderCssClasses(diff);
			});
	}

	private updateState(css: SchemaCssClass): Partial<SchemaCssClass> {

		if (!this.cssClass) {
			this.cssClass = css;
			return this.cssClass;
		} else {

			let diff = {};

			Object.keys(this.cssClass)
				  .forEach((key: string) => {
					  if (css[key] !== this.cssClass[key]) {
						  diff[key] = css[key];
					  }
				  });

			this.cssClass = css;

			return diff;
		}
	}

	private renderCssClasses(diff: Partial<SchemaCssClass>): void {

		if (diff.hasOwnProperty('verticalGrid')) {
			this.toggleCssClass(diff.verticalGrid, this.VERTICAL_GRID_CLASS_NAME);
		}

		if (diff.hasOwnProperty('horizontalGrid')) {
			this.toggleCssClass(diff.horizontalGrid, this.HORIZONTAL_GRID_CLASS_NAME);
		}

		if (diff.hasOwnProperty('theme')) {
			this.removeThemeCssClasses();
			this.addClass(this.resolveThemeClassName(diff.theme));
		}

		if (diff.hasOwnProperty('rowColoring')) {
			this.removeRowColoringClasses();
			this.addClass(this.resolveRowColoringClassName(diff.rowColoring));
		}
	}

	private toggleCssClass(toggle: boolean, cssClassName: string): void {
		if (toggle) {
			this.addClass(cssClassName);
		} else {
			this.removeClass(cssClassName);
		}
	}

	private removeThemeCssClasses(): void {
		Object.keys(SchemaTheme)
			  .map((key: string) => SchemaTheme[key])
			  .map((theme: SchemaTheme) => this.resolveThemeClassName(theme))
			  .filter((className: string) => !!className)
			  .forEach((className: string) => {
				  this.removeClass(className);
			  });
	}

	private resolveThemeClassName(theme: SchemaTheme | string): string {

		switch (theme) {
			case SchemaTheme.FABRIC:
			case SchemaTheme[SchemaTheme.FABRIC]:
				return this.THEME_FABRIC_CLASS_NAME;

			case SchemaTheme.MATERIAL:
			case SchemaTheme[SchemaTheme.MATERIAL]:
				return this.THEME_MATERIAL_CLASS_NAME;

			case SchemaTheme.LIGHT:
			case SchemaTheme[SchemaTheme.LIGHT]:
				return this.THEME_LIGHT_CLASS_NAME;

			case SchemaTheme.DARK:
			case SchemaTheme[SchemaTheme.DARK]:
				return this.THEME_DARK_CLASS_NAME;

			case SchemaTheme.GENERIC:
			case SchemaTheme[SchemaTheme.GENERIC]:
				return this.THEME_GENERIC_CLASS_NAME;

			default:
				return this.THEME_FABRIC_CLASS_NAME;
		}
	}

	private resolveRowColoringClassName(coloring: SchemaRowColoring | string): string {
		switch (coloring) {
			case SchemaRowColoring.ODD:
			case SchemaRowColoring[SchemaRowColoring.ODD]:
				return this.ROW_COLORING_ODD;

			case SchemaRowColoring.EVEN:
			case SchemaRowColoring[SchemaRowColoring.EVEN]:
				return this.ROW_COLORING_EVEN;

			default:
				return null;
		}
	}

	private removeRowColoringClasses(): void {
		Object.keys(SchemaRowColoring)
			  .map((key: string) => SchemaRowColoring[key])
			  .map((coloring: SchemaRowColoring) => this.resolveRowColoringClassName(coloring))
			  .filter((className: string) => !!className)
			  .forEach((className: string) => {
				  this.removeClass(className);
			  });
	}

	private addClass(cssClassName: string): void {
		if (cssClassName) {
			this.renderer.addClass(this.cssHostRef.nativeElement, cssClassName);
		}
	}

	private removeClass(cssClassName: string): void {
		if (cssClassName) {
			this.renderer.removeClass(this.cssHostRef.nativeElement, cssClassName);
		}
	}

}
