import { ColumnWidth } from '../column/column-width/column-width';

export class CompositionContainerWidthCalculator {

	constructor(private readonly MIN_COLUMN_WIDTH: number) {
	}

	calculateMinWidth(columnWidths: Array<ColumnWidth>): number {

		const { staticColumns, fluidColumns } = this.segregateColumns(columnWidths);

		// TODO reduce
		let sum = 0;
		staticColumns.forEach((c) => {
			sum += c.getWidth();
		});

		sum += fluidColumns.length * this.MIN_COLUMN_WIDTH;

		return sum;
	}

	private segregateColumns(columns: Array<ColumnWidth>): { staticColumns: Array<ColumnWidth>, fluidColumns: Array<ColumnWidth> } {

		let staticColumns = columns.filter((columnWidth: ColumnWidth) => columnWidth.isTypeNumber() && columnWidth.getWidth() > this.MIN_COLUMN_WIDTH),
			fluidColumns = columns.filter((columnWidth: ColumnWidth) => columnWidth.isTypeAuto() || columnWidth.isTypePercentage() || (columnWidth.isTypeNumber() && columnWidth.getWidth() <= this.MIN_COLUMN_WIDTH));

		return {
			staticColumns: staticColumns,
			fluidColumns: fluidColumns
		};
	}
}
