import { Injectable } from '@angular/core';

import { StructureFieldUiModel } from './structure.field.ui-model';
import { TypeField } from '../../domain/structure/field/data-type/type.field';


@Injectable()
export class StructureFieldUiConverter {

	convert(fields: Array<TypeField>): Array<StructureFieldUiModel> {
		return fields.map((field: TypeField) => {
			return this.convertOne(field);
		});
	}

	private convertOne(field: TypeField): StructureFieldUiModel {

		return new StructureFieldUiModel(field.getId(), field.getDataType());
	}
}
