import { TemplateRef } from '@angular/core';

import { ReadModelEntity } from '@generic-ui/hermes';

import { ColumnField } from '../../../domain/column/field/column-field';
import { SortOrder } from '../../../domain/column/sort/sort-order';
import { HeaderTemplate } from '../../../domain/column/column.config';
import { CellTemplateWithContext } from './cell-template-with-context';
import { CellContext } from './cell-context';
import { FieldId } from '../../../../structure/domain/structure/field/data-type/field.id';
import { CellTemplateWithAccessor } from './cell-template-with-accessor';
import { DataType } from '../../../../structure/domain/structure/field/data-type/data-type';
import { ViewEntity } from '../../../domain/column/view.entity';
import { ColumnAlign } from '../../../domain/column/column-align';
import { ColumnDefinitionId } from './column-definition-id';
import { CellValue } from './cell-value';


export class ColumnDefinition extends ReadModelEntity<ColumnDefinitionId> {

	cellTemplate: TemplateRef<any>;

	headerTemplate: TemplateRef<any>;

	editTemplate: TemplateRef<any>;

	width: number;

	private templateFunction: (i: any) => string;

	private readonly columnDefinitionId: ColumnDefinitionId;

	private field: ColumnField;

	private sortStatus: SortOrder;

	private sortable: boolean = true;

	private header: string | HeaderTemplate;

	private enabled: boolean;

	private cellEditingEnabled: boolean;

	constructor(field: ColumnField,
				columnDefinitionId: ColumnDefinitionId, // REFACTOR Id first argument
				enabled: boolean,
				private readonly type: DataType, // REFACTOR
				private readonly view: ViewEntity, // REFACTOR
				private readonly align: ColumnAlign, // REFACTOR
				header: string | HeaderTemplate,
				cellEditingEnabled: boolean,
				sort: SortOrder = SortOrder.NONE,
				sortable = true) {
		super(columnDefinitionId);
		this.field = field;
		this.columnDefinitionId = columnDefinitionId;
		this.enabled = enabled;
		this.header = header;
		this.cellEditingEnabled = cellEditingEnabled;
		this.sortStatus = sort;
		this.sortable = sortable;
	}

	isEnabled(): boolean {
		return this.enabled;
	}

	setHeader(header: string): void {
		this.header = header;
	}

	setField(field: ColumnField): void {
		this.field = field;
	}

	setTemplateFunction(templateFun: (i: any) => string): void {
		this.templateFunction = templateFun;
	}

	getHeaderCellTemplateWithContext(index: number): CellTemplateWithContext {

		let header: any = this.header || '',
			headerCellContext: CellContext;

		if (typeof header === 'string') {
			headerCellContext = new CellContext(CellValue.text(header));
		} else if (typeof header === 'function') {
			headerCellContext = new CellContext(CellValue.text(header(index)));
		}

		const fieldId = new FieldId(this.field.getId().getId());

		return new CellTemplateWithContext(
			this.headerTemplate,
			this.editTemplate,
			headerCellContext,
			this.width,
			fieldId,
			this.columnDefinitionId,
			this.sortStatus,
			this.enabled,
			this.sortable,
			this.align
		);
	}

	getContentCellTemplateWithAccessor(): CellTemplateWithAccessor {

		const accessor = (entity: any) => {
			return this.field.getAccessor()(entity);
		}, searchAccessor = (entity: any) => {
			return this.field.getSearchAccessor()(entity);
		};

		return new CellTemplateWithAccessor(
			this.cellTemplate,
			this.editTemplate,
			true,
			this.templateFunction,
			accessor,
			searchAccessor,
			this.width,
			this.field.getId(),
			this.align,
			this.cellEditingEnabled,
			this.type,
			this.view
		);
	}

}
