import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { SchemaId } from '../schema.id';
import { SchemaTheme } from './schema-theme';
import { SchemaThemeSetEvent } from './schema-theme-set.event';

export class SchemaThemeSetAggregateEvent extends AggregateEvent<SchemaId> {

	constructor(schemaId: SchemaId,
				private readonly theme: SchemaTheme) {
		super(schemaId, 'SchemaThemeSetAggregateEvent');
	}

	toDomainEvent(): DomainEvent<SchemaId> {
		return new SchemaThemeSetEvent(this.getAggregateId(), this.theme);
	}
}
