import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	OnInit,
	Renderer2,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';

import { DomainEventBus } from '@generic-ui/hermes';

import { SmartComponent } from '../../../../common/cdk/smart-component';

import { StructureHeaderColumnsComponent } from './columns/structure-header-columns.component';
import { CellTemplateWithContext } from '../../../composition/domain-api/read/definition/cell-template-with-context';
import { StructureId } from '../../domain/structure.id';
import { StructureFilterWarehouse } from '../../domain-api/filter/structure-filter.warehouse';
import { CompositionWarehouse } from '../../../composition/domain-api/composition.warehouse';
import { StructureHeaderFiltersComponent } from './filters/structure-header-filters.component';
import { StructureWarehouse } from '../../domain-api/structure.warehouse';
import { StructureVerticalFormationWarehouse } from '../../domain-api/vertical-formation/structure-vertical-formation.warehouse';


@Component({
	selector: 'gui-structure-header',
	templateUrl: './structure-header.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureHeaderComponent extends SmartComponent implements OnInit, AfterViewInit {

	@ViewChild(StructureHeaderColumnsComponent, { read: ElementRef, static: true })
	containerRef: ElementRef;

	@ViewChild(StructureHeaderFiltersComponent, { read: ElementRef, static: true })
	filtersRef: ElementRef;

	headerColumns: Array<CellTemplateWithContext> = [];

	filterRowEnabled: boolean = false;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly renderer: Renderer2,
				private readonly eventBus: DomainEventBus,
				private readonly structureId: StructureId,
				private readonly structureFilterReadModelRepository: StructureFilterWarehouse,
				private readonly structureWarehouse: StructureWarehouse,
				private readonly structureVerticalFormationWarehouse: StructureVerticalFormationWarehouse,
				private readonly compositionReadModelService: CompositionWarehouse) {
		super();
	}

	ngOnInit() {

		this.compositionReadModelService
			.onHeaderColumns()
			.pipe(
				this.takeUntil()
			)
			.subscribe((columns: Array<CellTemplateWithContext>) => {
				this.headerColumns = columns;
				this.changeDetectorRef.detectChanges();
			});

		this.compositionReadModelService
			.onContainerWidth()
			.pipe(
				this.takeUntil()
			)
			.subscribe((width: number) => {
				this.renderer.setStyle(this.containerRef.nativeElement, 'width', width + 'px');
			});

		this.structureFilterReadModelRepository
			.onFilteringEnabled(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((enabled: boolean) => {

				this.filterRowEnabled = enabled;
				this.changeDetectorRef.detectChanges();
			});
	}

	ngAfterViewInit() {
		this.structureVerticalFormationWarehouse
			.onRowHeight(this.structureId)
			.pipe(
				this.takeUntil()
			)
			.subscribe((rowHeight: number) => {

				const headerHeight = +(rowHeight) + 2;

				// if (this.containerRef) {
				// 	this.renderer.setStyle(this.containerRef.nativeElement, 'height', headerHeight + 'px');
				// }

				if (this.filtersRef) {
					this.renderer.setStyle(this.filtersRef.nativeElement, 'height', headerHeight + 'px');
				}
			});
	}

}
