import { Injectable } from '@angular/core';

import { CompositionDispatcher } from '../domain/composition.dispatcher';
import { CompositionId } from '../domain/composition.id';
import { compositionGlobalId } from './composition.global-id';
import { ColumnParams } from '../domain/column/set-columns/column.params';
import { ColumnId } from '../domain/column/column.id';
import { ColumnDefinitionId } from './read/definition/column-definition-id';


@Injectable()
export class CompositionCommandDispatcher {

	constructor(private compositionDispatcher: CompositionDispatcher) {
	}

	createComposition(compositionId: CompositionId = compositionGlobalId): void {
		this.compositionDispatcher.createComposition(compositionId);
	}

	setColumns(params: Array<ColumnParams>, compositionId: CompositionId = compositionGlobalId): void {
		this.compositionDispatcher.setColumns(compositionId, params);
	}

	setWidth(width: number, compositionId: CompositionId = compositionGlobalId): void {
		this.compositionDispatcher.setWidth(compositionId, width);
	}

	setContainerWidth(width: number, compositionId: CompositionId = compositionGlobalId): void {
		this.compositionDispatcher.setContainerWidth(compositionId, width);
	}

	setResizeWidth(enabled: boolean, compositionId: CompositionId = compositionGlobalId): void {
		this.compositionDispatcher.setResizeWidth(compositionId, enabled);
	}

	enableColumn(columnDefinitionId: ColumnDefinitionId, compositionId: CompositionId = compositionGlobalId): void {
		this.compositionDispatcher.setColumnEnabled(compositionId, this.toColumnId(columnDefinitionId), true);
	}

	disableColumn(columnDefinitionId: ColumnDefinitionId, compositionId: CompositionId = compositionGlobalId): void {
		this.compositionDispatcher.setColumnEnabled(compositionId, this.toColumnId(columnDefinitionId), false);
	}

	moveLeft(columnDefinitionId: ColumnDefinitionId, compositionId: CompositionId = compositionGlobalId): void {
		this.compositionDispatcher.moveLeft(compositionId, this.toColumnId(columnDefinitionId));
	}

	moveRight(columnDefinitionId: ColumnDefinitionId, compositionId: CompositionId = compositionGlobalId): void {
		this.compositionDispatcher.moveRight(compositionId, this.toColumnId(columnDefinitionId));
	}

	private toColumnId(defId: ColumnDefinitionId): ColumnId {
		return new ColumnId(defId.getId());
	}
}
