import { Injectable } from '@angular/core';

import { FieldIdGenerator } from '../field-id.generator';
import { ColumnConfig } from '../../../../../composition/domain/column/column.config';
import { DataType } from './data-type';
import { UnknownField } from './fields/unknown-field';
import { NumberField } from './fields/number-field';
import { StringField } from './fields/string-field';
import { BooleanField } from './fields/boolean-field';
import { DateField } from './fields/date-field';
import { FieldMatchers } from '../matcher/field.matchers';
import { AllFieldTypes } from './all-field-types';


@Injectable()
export class FieldFactory {

	constructor(private readonly fieldIdGenerator: FieldIdGenerator) {
	}

	create(column: ColumnConfig): AllFieldTypes {

		const fieldMatchers = this.createMatchers(column);

		let columnType = column.type;

		if (columnType === undefined) {
			columnType = DataType.STRING;
		}

		if (columnType === DataType.UNKNOWN) {
			return new UnknownField(this.fieldIdGenerator.generateId(), column.field as string, fieldMatchers, column.summaries);
		}

		if (columnType === DataType.NUMBER) {
			return new NumberField(this.fieldIdGenerator.generateId(), column.field as string, fieldMatchers, column.summaries);
		}

		if (columnType === DataType.STRING) {
			return new StringField(this.fieldIdGenerator.generateId(), column.field as string, fieldMatchers);
		}

		if (columnType === DataType.BOOLEAN) {
			return new BooleanField(this.fieldIdGenerator.generateId(), column.field as string, fieldMatchers);
		}

		if (columnType === DataType.DATE) {
			return new DateField(this.fieldIdGenerator.generateId(), column.field as string, fieldMatchers);
		}

	}

	private createMatchers(column: ColumnConfig): FieldMatchers {

		let matcher = column.matcher,
			sortMatcher;

		if (column.sorting) {
			sortMatcher = column.sorting.matcher;
		}

		return new FieldMatchers(matcher, sortMatcher);
	}


}
