import { Injectable } from '@angular/core';

import { AggregateRepository, Command, CreateAggregateCommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { StructureAggregateRepository } from '../structure-aggregate.repository';
import { CreateStructureCommand } from './create-structure.command';
import { StructureCreatedEvent } from './structure-created.event';
import { StructureAggregate } from '../structure.aggregate';
import { StructureId } from '../../structure.id';


@Injectable()
export class CreateStructureCommandHandler implements CreateAggregateCommandHandler<StructureAggregate, CreateStructureCommand> {

	constructor(private readonly structureAggregateRepository: StructureAggregateRepository,
				private domainEventPublisher: DomainEventPublisher) {
	}

	getAggregateRepository(): AggregateRepository<StructureId, StructureAggregate> {
		return this.structureAggregateRepository;
	}

	forCommand(): Command | any {
		return CreateStructureCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: CreateStructureCommand): void {
		this.domainEventPublisher.publish(new StructureCreatedEvent(command.getAggregateId()));
	}

}
