import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';
import { StructureSetConfigFilterCommand } from './structure.set-config-filter.command';
import { StructureConfigFilterSetEvent } from './structure.config-filter-set.event';
import { StructureAggregate } from '../../structure.aggregate';


@Injectable()
export class StructureSetConfigFilterCommandHandler implements CommandHandler<StructureAggregate, StructureSetConfigFilterCommand> {

	constructor(private readonly domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<StructureSetConfigFilterCommand> {
		return StructureSetConfigFilterCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: StructureSetConfigFilterCommand): void {

		const filterConfig = command.getConfig();

		this.domainEventPublisher.publish(new StructureConfigFilterSetEvent(command.getAggregateId(), filterConfig.enabled));
	}

	handleAggregate(aggregate: StructureAggregate, command: StructureSetConfigFilterCommand): void {

		const filterConfig = command.getConfig();

		aggregate.setFilterConfig(filterConfig);
	}

}
