import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { SetCompositionResizeWidthCommand } from './set-composition-resize-width.command';
import { CompositionResizeWidthSetEvent } from './composition-resize-width-set.event';
import { CompositionAggregate } from '../../composition.aggregate';


@Injectable()
export class SetCompositionResizeWidthCommandHandler implements CommandHandler<CompositionAggregate, SetCompositionResizeWidthCommand> {

	constructor(private readonly domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<SetCompositionResizeWidthCommand> {
		return SetCompositionResizeWidthCommand;
	}

	publishDomainEvents(aggregate: CompositionAggregate, command: SetCompositionResizeWidthCommand): void {
		this.domainEventPublisher.publish(new CompositionResizeWidthSetEvent(command.getAggregateId()));
	}

	handleAggregate(composition: CompositionAggregate, command: SetCompositionResizeWidthCommand): void {

		const enabled = command.getEnabled();

		composition.setResizeWidth(enabled);
	}

}
