import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { distinctUntilChanged, map } from 'rxjs/operators';

import { Reactive } from '../../../../../common/cdk/reactive';
import { StructureId } from '../../../domain/structure.id';


@Injectable()
export class StructureSearchPhraseRepository extends Reactive {

	private phrase = new Map<string, string>();

	private phrase$ = new ReplaySubject<Map<string, string>>();

	constructor() {
		super();
	}

	setPhrase(phrase: string, structureId: StructureId): void {
		this.phrase.set(structureId.toString(), phrase);
		this.phrase$.next(this.phrase);
	}

	onPhrase(structureId: StructureId): Observable<string> {

		return this.observePhrases(structureId)
				   .pipe(
					   distinctUntilChanged()
				   );
	}

	private observePhrases(structureId: StructureId): Observable<string> {
		return this.phrase$
				   .asObservable()
				   .pipe(
					   map((map: Map<string, string>) => {
						   return map.get(structureId.toString());
					   })
				   );
	}

}
