import { Injectable } from '@angular/core';
import { SummariesCalculator } from '../summaries.calculator';
import { NumberSummarizedValues } from './number-summarized.values';
import { DataType } from '../../../field/data-type/data-type';
import { Field } from '../../../field/data-type/field';
import { SummariesType } from '../../summaries.type';
import { OriginItemEntity } from '../../../../source/origin/origin-item-entity';


@Injectable()
export class NumberSummariesCalculator extends SummariesCalculator<number, NumberSummarizedValues> {

	private readonly sum = new Map<string, number>();
	private readonly min = new Map<string, number>();
	private readonly max = new Map<string, number>();
	private readonly average = new Map<string, number>();
	private readonly median = new Map<string, number>();

	constructor() {
		super();
	}

	forDataType(dataType: DataType): boolean {
		return dataType === DataType.NUMBER;
	}

	prepare(field: Field<number>): void {

		const key = field.getKey();

		this.sum.set(key, 0);
		this.min.set(key, Number.MAX_SAFE_INTEGER);
		this.max.set(key, 0);
	}

	postCalculate(field: Field<number>, items: Array<OriginItemEntity>): void {

		const key = field.getKey();

		if (field.isSummaries(SummariesType.AVERAGE)) {
			this.average.set(key, this.sum.get(key) / items.length);
		}

		if (field.isSummaries(SummariesType.MEDIAN)) {
			this.median.set(key, field.getValue(items[Math.floor(items.length / 2)]));
		}
	}

	aggregate(field: Field<number>, value: number): void {

		const numberValue = +value,
			key = field.getKey(),
			sumForField = this.sum.get(key),
			minForField = this.min.get(key),
			maxForField = this.max.get(key);

		if (field.isSummaries(SummariesType.SUM) || field.isSummaries(SummariesType.AVERAGE)) {
			this.sum.set(key, sumForField + numberValue);
		}

		if (field.isSummaries(SummariesType.MIN)) {
			if (minForField > numberValue) {
				this.min.set(key, numberValue);
			}
		}

		if (field.isSummaries(SummariesType.MAX)) {
			if (maxForField < numberValue) {
				this.max.set(key, numberValue);
			}
		}

	}

	generateAggregatedValues(field: Field<number>): NumberSummarizedValues {

		const key = field.getKey(),
			sum = field.isSummaries(SummariesType.SUM) ? this.sum.get(key) : undefined,
			min = field.isSummaries(SummariesType.MIN) ? this.min.get(key) : undefined,
			max = field.isSummaries(SummariesType.MAX) ? this.max.get(key) : undefined,
			average = field.isSummaries(SummariesType.AVERAGE) ? this.average.get(key) : undefined,
			median = field.isSummaries(SummariesType.MEDIAN) ? this.median.get(key) : undefined;

		return new NumberSummarizedValues(
			sum,
			min,
			max,
			average,
			median
		);
	}

}
