import { Inject, Injectable } from '@angular/core';

import { SUMMARIES_CALCULATORS } from './calculation/summaries.calculators.token';
import { SummariesCalculator } from './calculation/summaries.calculator';
import { StructureId } from '../../structure.id';
import { SummariesManager } from './summaries.manager';


@Injectable()
export class SummariesManagerFactory {

	constructor(@Inject(SUMMARIES_CALCULATORS) private calculators: Array<SummariesCalculator<any, any>>) {
	}

	create(structureId: StructureId): SummariesManager {
		return new SummariesManager(structureId, this.calculators);
	}

}
