import { Command } from '@generic-ui/hermes';

import { CompositionId } from '../../composition.id';
import { ChangeSortParams } from './change-sort.params';


export class CompositionChangeSortStatusCommand extends Command {

	constructor(readonly compositionId: CompositionId,
				private readonly sortParams: Array<ChangeSortParams>) {
		super(compositionId, 'CompositionChangeSortStatusCommand');
	}

	getCompositionId(): CompositionId {
		return this.compositionId;
	}

	getSortParams(): Array<ChangeSortParams> {
		return this.sortParams;
	}

}
