import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';

import { Paging } from '../../domain-api/read/paging';


@Component({
	selector: 'gui-paging-select',
	templateUrl: `paging-select.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	host: {
		'[class.gui-paging-select]': `"true"`
	}
})
export class PagingSelectComponent {

	@Input()
	paging: Paging;

	@Output()
	pageSizeChanged = new EventEmitter();

	changePageSize(pageSize: number): void {
		this.pageSizeChanged.emit(pageSize);
	}
}
