import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { SetFormationCommand } from './set-formation.command';
import { FormationSetEvent } from './formation-set.event';
import { StructureAggregate } from '../../structure/structure.aggregate';


@Injectable()
export class SetFormationCommandHandler implements CommandHandler<StructureAggregate, SetFormationCommand> {

	constructor(private readonly domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<SetFormationCommand> {
		return SetFormationCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: SetFormationCommand): void {
		this.domainEventPublisher.publish(new FormationSetEvent(command.getAggregateId()));
	}

	handleAggregate(aggregate: StructureAggregate, command: SetFormationCommand): void {

		// missing implementation
	}

}
