import { OriginItemEntity } from '../../source/origin/origin-item-entity';
import { DataType } from '../field/data-type/data-type';
import { TypeField } from '../field/data-type/type.field';


export class SearchManager {

	private searchFields: Array<TypeField> = [];

	private searchPhrase: string;

	private enabledDataTypes: Array<DataType> = [
		DataType.STRING
	];

	addSearchPhrase(fields: Array<TypeField>, searchPhrase: string): void {

		if (!searchPhrase) {
			this.searchFields = [];
			return;
		}

		let searchableFields =
			fields.filter((field: TypeField) => {
				return this.enabledDataTypes.some((dt: DataType) => dt === field.getDataType());
			});

		if (searchableFields.length === 0) {
			return;
		}

		this.searchFields = searchableFields;

		this.searchPhrase = searchPhrase;
	}

	removeSearchFilters(): void {
		this.searchFields = [];
	}

	search(entities: Array<OriginItemEntity>): Array<OriginItemEntity> {

		if (entities.length === 0 || this.searchFields.length === 0) {
			return entities;
		}

		let searchedItems = new Set<OriginItemEntity>();

		for (let i = 0; i < entities.length; i += 1) {

			this.searchFields
				.filter((field: TypeField) => field.search(entities[i], this.searchPhrase))
				.forEach(() => {
					searchedItems.add(entities[i]);
				});
		}

		return Array.from(searchedItems);
	}

}
