import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { Override } from '../../../../../common/cdk/decorators';

import { CompositionWarehouse } from '../../../../composition/domain-api/composition.warehouse';
import { CompositionId } from '../../../../composition/domain/composition.id';
import { CompositionReadModelRootRepository } from '../../../../composition/domain-api/read/composition.read-model-root-repository';
import { CellTemplateWithContext } from '../../../../composition/domain-api/read/definition/cell-template-with-context';
import { CellTemplateWithAccessor } from '../../../../composition/domain-api/read/definition/cell-template-with-accessor';
import { compositionGlobalId } from '../../../../composition/domain-api/composition.global-id';
import { FieldId } from '../../../domain/structure/field/data-type/field.id';
import { SortOrder } from '../../../../composition/domain/column/sort/sort-order';


@Injectable()
export class LocalCompositionWarehouse extends CompositionWarehouse {

	constructor(private readonly compositionId: CompositionId,
				compositionRepository: CompositionReadModelRootRepository) {
		super(compositionRepository);
	}

	@Override
	onWidth(): Observable<number> {
		return super.onWidth(this.compositionId);
	}

	@Override
	onContainerWidth(compositionId: CompositionId = compositionGlobalId): Observable<number> {
		return super.onContainerWidth(this.compositionId);
	}

	@Override
	onWidthForEachColumn(): Observable<Array<number>> {
		return super.onWidthForEachColumn(this.compositionId);
	}

	@Override
	onHeaderColumns(): Observable<Array<CellTemplateWithContext>> {
		return super.onHeaderColumns(this.compositionId);
	}

	@Override
	onAllColumns(compositionId: CompositionId = compositionGlobalId): Observable<Array<CellTemplateWithContext>> {
		return super.onAllColumns(this.compositionId);
	}

	@Override
	onSortOrder(fieldId: FieldId): Observable<SortOrder> {
		return super.onSortOrder(fieldId, this.compositionId);
	}

	@Override
	onTemplateColumns(): Observable<Array<CellTemplateWithAccessor>> {
		return super.onTemplateColumns(this.compositionId);
	}

	@Override
	onResizeWidth(): Observable<boolean> {
		return super.onResizeWidth(this.compositionId);
	}

}
