import { DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../structure.id';
import { CompositionId } from '../../../../../composition/domain/composition.id';
import { ColumnConfig } from '../../../../../composition/domain/column/column.config';
import { TypeField } from '../data-type/type.field';


export class FieldsInitedEvent extends DomainEvent<StructureId> {

	constructor(aggregateId: StructureId,
				public readonly compositionId: CompositionId,
				public readonly columns: Array<ColumnConfig>, // TODO merge
				private readonly fields: Array<TypeField>) {
		super(aggregateId, 'FieldsInitedEvent');
	}

	getFields(): Array<TypeField> {
		return this.fields;
	}
}
