import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';
import { StructureReadModelRepository } from '../../../../lib/structure/domain-api/read/structure.read-model-repository';
import { StructureId } from '../../../../lib/structure/domain/structure.id';
import { Paging } from './paging';
import { StructureReadModelRoot } from '../../../../lib/structure/domain-api/read/structure.read-model-root';


@Injectable()
export class PagingRepository {

	constructor(private structureRepository: StructureReadModelRepository) {
	}

	on(structureId: StructureId): Observable<Paging> {

		return this.structureRepository
				   .onStructure(structureId)
				   .pipe(
					   filter((structure: StructureReadModelRoot) => structure.getId().toString() === structureId.toString()),
					   map((structure: StructureReadModelRoot) => structure.getPaging())
				   );
	}

}
