import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { SchemaId } from '../../schema.id';
import { SchemaHorizontalGridSetEvent } from './schema-horizontal-grid-set.event';


export class SchemaHorizontalGridSetAggregateEvent extends AggregateEvent<SchemaId> {

	constructor(schemaId: SchemaId,
				private readonly horizontalGrid: boolean) {
		super(schemaId, 'SchemaHorizontalGridSetEvent');
	}

	toDomainEvent(): DomainEvent<SchemaId> {
		return new SchemaHorizontalGridSetEvent(this.getAggregateId(), this.horizontalGrid);
	}
}
