import { Injectable, Type } from '@angular/core';

import { AggregateId, CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { StructureAggregateRepository } from '../../structure-aggregate.repository';
import { SetSortOrderCommand } from './set-sort-order.command';
import { SortOrderSetEvent } from './sort-order-set.event';
import { CompositionId } from '../../../../../composition/domain/composition.id';
import { FieldId } from '../../field/data-type/field.id';
import { StructureSorter } from '../structure.sorter';
import { StructureAggregate } from '../../structure.aggregate';


@Injectable()
export class SetSortOrderCommandHandler implements CommandHandler<StructureAggregate, SetSortOrderCommand> {

	constructor(private readonly structureAggregateRepository: StructureAggregateRepository,
				private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<SetSortOrderCommand> {
		return SetSortOrderCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: SetSortOrderCommand): void {
	}

	handleAggregate(structure: StructureAggregate, command: SetSortOrderCommand): void {

		const structureId = command.getAggregateId(),
			compositionId = command.getCompositionId(),
			fieldId = command.getFieldId(),
			sortOrder = command.getSortOrder();

		const sorters = structure.setSortOrder(fieldId, sortOrder);

		this.publishSortEvent(structureId, compositionId, fieldId, sorters);
	}

	private publishSortEvent(aggregateId: AggregateId, compositionId: CompositionId, fieldId: FieldId, sorters: Array<StructureSorter>): void {

		const directions = sorters.map((sorter: StructureSorter) => {
			return {
				direction: sorter.hasDirection(),
				fieldId: sorter.getField().getId()
			};
		});

		this.domainEventPublisher.publish(new SortOrderSetEvent(aggregateId, compositionId, directions));
	}

}
