import { DomainEvent } from '@generic-ui/hermes';

import { SchemaId } from '../schema.id';
import { SchemaRowColoring } from './schema-row-coloring';


export class RowColoringSetEvent extends DomainEvent<SchemaId> {

	constructor(schemaId: SchemaId,
				private readonly rowColoring: SchemaRowColoring) {
		super(schemaId, 'RowColoringSetEvent');
	}

	getRowColoring(): SchemaRowColoring {
		return this.rowColoring;
	}
}
