import { Command } from '@generic-ui/hermes';

import { StructureId } from '../../../structure.id';
import { SortOrder } from '../../../../../composition/domain/column/sort/sort-order';
import { FieldId } from '../../field/data-type/field.id';
import { CompositionId } from '../../../../../composition/domain/composition.id';


export class SetSortOrderCommand extends Command {

	constructor(structureId: StructureId,
				private readonly compositionId: CompositionId,
				private readonly fieldId: FieldId,
				private readonly sortOrder: SortOrder) {
		super(structureId, 'SetSortOrderCommand');
	}

	getCompositionId(): CompositionId {
		return this.compositionId;
	}

	getFieldId(): FieldId {
		return this.fieldId;
	}

	getSortOrder(): SortOrder {
		return this.sortOrder;
	}
}
