import { Injectable, Type } from '@angular/core';

import { AggregateEvent, CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { CompositionAggregateRepository } from '../../../composition.aggregate-repository';
import { CompositionEventConverter } from '../../../composition.event-converter';
import { CompositionAggregate } from '../../../composition.aggregate';
import { CompositionMoveLeftColumnCommand } from './composition.move-left-column.command';
import { CompositionId } from '../../../composition.id';

@Injectable()
export class CompositionMoveLeftColumnCommandHandler implements CommandHandler<CompositionAggregate, CompositionMoveLeftColumnCommand> {

	constructor(private readonly compositionAggregateRepository: CompositionAggregateRepository,
				protected domainEventPublisher: DomainEventPublisher,
				private compositionEventConverter: CompositionEventConverter) {
	}

	forCommand(): Type<CompositionMoveLeftColumnCommand> {
		return CompositionMoveLeftColumnCommand;
	}

	publishDomainEvents(aggregate: CompositionAggregate, command: CompositionMoveLeftColumnCommand): void {
		this.publishEvents(aggregate, command);
	}

	handleAggregate(aggregate: CompositionAggregate, command: CompositionMoveLeftColumnCommand): void {

		const columnId = command.getColumnId();

		aggregate.moveLeft(columnId);
	}

	private publishEvents(composition: CompositionAggregate, command: CompositionMoveLeftColumnCommand): void {

		const events = composition.getEvents() as Array<AggregateEvent<CompositionId>>;

		const domainEvents = this.compositionEventConverter.convert(events);

		if (domainEvents && domainEvents.length > 0) {
			this.domainEventPublisher.publish(domainEvents);
		}
	}
}
