import { GenericBuilder } from '../../../common/cdk/generic-builder';

export class StructurePagingDefinitionBuilder extends GenericBuilder<PagingDefinition> {

	private enabled: boolean;

	constructor(enabled: boolean) {
		super();
		this.enabled = enabled;
	}

	protected buildObject(): PagingDefinition {
		return new PagingDefinition(this.enabled);
	}

	withEnabled(enabled: boolean): StructurePagingDefinitionBuilder {
		this.enabled = enabled;
		return this;
	}
}

export class StructurePagingDefinitionDefaultBuilder extends StructurePagingDefinitionBuilder {

	private static readonly defaultEnabled = true;

	constructor() {
		super(StructurePagingDefinitionDefaultBuilder.defaultEnabled);
	}
}

export class PagingDefinition {

	private readonly enabled: boolean;

	constructor(enabled: boolean) {
		this.enabled = enabled;
	}

	static Builder = StructurePagingDefinitionBuilder;

	static DefaultBuilder = StructurePagingDefinitionDefaultBuilder;

	isEnabled(): boolean {
		return this.enabled;
	}

}
