import { Injectable } from '@angular/core';

import { DomainEventHandler } from '@generic-ui/hermes';

import { SchemaCssClassesRepository } from './schema.css-classes.repository';
import { SchemaId } from '../../domain/schema.id';
import { SchemaThemeSetEvent } from '../../domain/theme/schema-theme-set.event';
import { RowColoringSetEvent } from '../../domain/coloring/row-coloring-set.event';
import { SchemaHorizontalGridSetEvent } from '../../domain/grid/horizontal/schema-horizontal-grid-set.event';
import { SchemaVerticalGridSetEvent } from '../../domain/grid/vertical/schema-vertical-grid-set.event';
import { SchemaCssClass } from './schema-css-class';
import { SchemaRowColoring } from '../../domain/coloring/schema-row-coloring';
import { SchemaTheme } from '../../domain/theme/schema-theme';

export type SchemaCssClassesEventType = SchemaThemeSetEvent | RowColoringSetEvent | SchemaHorizontalGridSetEvent | SchemaVerticalGridSetEvent;

@Injectable()
export class SchemaCssClassesEventHandler extends DomainEventHandler<SchemaId, SchemaCssClassesEventType> {

	rowColoring: SchemaRowColoring;

	horizontalGrid: boolean;

	verticalGrid: boolean;

	schemaTheme: SchemaTheme;

	constructor(private schemaCssClassesRepository: SchemaCssClassesRepository) {
		super();
	}

	handle(event: SchemaCssClassesEventType): void {

		if (event.ofMessageType('RowColoringSetEvent')) {
			this.rowColoring = (event as RowColoringSetEvent).getRowColoring();
		}

		if (event.ofMessageType('SchemaHorizontalGridSetEvent')) {
			this.horizontalGrid = (event as SchemaHorizontalGridSetEvent).getHorizontalGrid();
		}

		if (event.ofMessageType('SchemaVerticalGridSetEvent')) {
			this.verticalGrid = (event as SchemaVerticalGridSetEvent).getVerticalGrid();
		}

		if (event.ofMessageType('SchemaThemeSetEvent')) {
			this.schemaTheme = (event as SchemaThemeSetEvent).getTheme();
		}

		this.publish(event.getAggregateId());

	}

	private publish(aggregateId: SchemaId): void {
		if (this.rowColoring !== undefined && this.horizontalGrid !== undefined && this.verticalGrid !== undefined && this.schemaTheme !== undefined) {
			this.schemaCssClassesRepository.setTheme(new SchemaCssClass(this.verticalGrid, this.horizontalGrid, this.schemaTheme, this.rowColoring), aggregateId);
		}
	}

}
