import { DomainEvent } from '@generic-ui/hermes';

import { SchemaId } from '../../schema.id';

export class SchemaHorizontalGridSetEvent extends DomainEvent<SchemaId> {

	constructor(schemaId: SchemaId,
				private readonly horizontalGrid: boolean) {
		super(schemaId, 'SchemaHorizontalGridSetEvent');
	}

	getHorizontalGrid(): boolean {
		return this.horizontalGrid;
	}
}
