import { AggregateEvent } from '@generic-ui/hermes';

import { StructureId } from '../../structure.id';
import { FieldId } from '../field/data-type/field.id';
import { SummariesCalculator } from './calculation/summaries.calculator';
import { OriginItemEntity } from '../../source/origin/origin-item-entity';
import { StructureSummariesChangedAggregateEvent } from './structure.summaries-changed.aggregate-event';
import { TypeField } from '../field/data-type/type.field';


export class SummariesManager {

	static DEFAULT_ENABLED = false;

	private readonly structureId: StructureId;

	private enabled: boolean;

	private readonly values = new Map<FieldId, any>();

	constructor(structureId: StructureId,
				private readonly calculators: Array<SummariesCalculator<any, any>>) {
		this.structureId = structureId;
		this.enabled = SummariesManager.DEFAULT_ENABLED;
	}

	calculate(fields: Array<TypeField>, entities: Array<OriginItemEntity>): Array<AggregateEvent<StructureId>> {

		if (!this.enabled) {
			return [];
		}

		const calculations = new Map();

		this.calculators.forEach((calc: SummariesCalculator<any, any>) => {

			const allFields = fields.map((typeField: TypeField) => typeField.getField());

			const aggregatedValues = calc.calculate(allFields, entities);

			if (aggregatedValues) {
				Array.from(aggregatedValues.keys())
					 .forEach((key: any) => {
						 calculations.set(key, aggregatedValues.get(key));
					 });
			}
		});

		if (calculations.size > 0) {
			return [
				new StructureSummariesChangedAggregateEvent(this.structureId, calculations)
			];
		} else {
			return [];
		}

	}

	setEnabled(enabled: boolean): void {
		this.enabled = enabled;
	}

	add() {
	}

	remove() {
	}

	update() {
	}

}
