import {
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	Input,
	OnInit,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';

import { CellTemplateWithContext } from '../../../../composition/domain-api/read/definition/cell-template-with-context';
import { StructureCommandDispatcher } from '../../../domain-api/structure.command-dispatcher';
import { StructureColumnConfigService } from './structure.column-config.service';
import { StructureColumnMenuConfig } from './config/structure.column-menu-config';
import { SmartComponent } from '../../../../../common/cdk/smart-component';
import { StructureColumnMenuConfigArchive } from './config/structure.column-menu-config.archive';


@Component({
	selector: 'gui-structure-column-config-trigger',
	template: `
		<div #headerDialogContainer
			 *ngIf="isEnabled()"
			 class="gui-header-menu-icon-wrapper"
			 (click)="openConfigDialog()">

			<gui-structure-column-menu-icon [ngClass]="'gui-header-menu-icon'"></gui-structure-column-menu-icon>

		</div>
	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureColumnConfigTriggerComponent extends SmartComponent implements OnInit {

	@ViewChild('headerDialogContainer', { static: false })
	headerDialogContainer: ElementRef;

	@Input()
	column: CellTemplateWithContext;

	config: StructureColumnMenuConfig;

	constructor(private elementRef: ElementRef,
				private changeDetectorRef: ChangeDetectorRef,
				private structureCommandService: StructureCommandDispatcher,
				private structureColumnMenuConfigArchive: StructureColumnMenuConfigArchive,
				private structureColumnConfigService: StructureColumnConfigService) {
		super();
	}

	ngOnInit() {
		this.structureColumnMenuConfigArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((config: StructureColumnMenuConfig) => {
				this.config = config;
				this.changeDetectorRef.detectChanges();
			});
	}

	openConfigDialog(): void {
		this.structureColumnConfigService.open(this.headerDialogContainer, this.column);
	}

	closeConfigDialog(): void {
		this.structureColumnConfigService.close();
	}

	isEnabled(): boolean {
		return this.config && this.config.isEnabled();
	}

}
