import { Command } from '@generic-ui/hermes';

import { StructureId } from '../../../../lib/structure/domain/structure.id';
import { PagingConfig } from '../../domain-api/paging-config';

export class SetPagingCommand extends Command {

	constructor(structureId: StructureId,
				private readonly config: PagingConfig) {
		super(structureId, 'SetPagingCommand');
	}

	getPagingConfig(): PagingConfig {
		return this.config;
	}

}
