import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter } from 'rxjs/operators';

import { DomainEvent, DomainEventBus } from '@generic-ui/hermes';

import { StructureId } from '../../domain/structure.id';
import { StructureSummariesChangedEvent } from '../../domain/structure/summaries/structure.summaries-changed.event';


@Injectable()
export class StructureSummariesUiEventsRepository {

	constructor(private readonly domainEventBus: DomainEventBus) {
	}

	onSummariesChanged(structureId: StructureId): Observable<DomainEvent<StructureId>> {

		return this.domainEventBus
				   .pipe(
					   filter((event: DomainEvent<StructureId>) => event.ofMessageType('StructureSummariesChangedEvent')),
					   filter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === structureId.toString())
				   );
	}
}
