import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';
import { SetScrollPositionCommand } from './set-scroll-position.command';
import { ScrollPositionSetEvent } from './scroll-position-set.event';
import { StructureAggregate } from '../../structure.aggregate';


@Injectable()
export class SetScrollPositionCommandHandler implements CommandHandler<StructureAggregate, SetScrollPositionCommand> {

	constructor(private readonly domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<SetScrollPositionCommand> {
		return SetScrollPositionCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: SetScrollPositionCommand): void {
		this.domainEventPublisher.publish(new ScrollPositionSetEvent(command.getAggregateId()));
	}

	handleAggregate(structure: StructureAggregate, command: SetScrollPositionCommand): void {

		const position = command.getPosition();

		structure.setScrollPosition(position);
	}

}
