import { AggregateEvent, DomainEvent } from '@generic-ui/hermes';

import { CompositionId } from '../../composition.id';
import { CompositionColumnsSetEvent } from './composition.columns-set.event';


export class ColumnsSetAggregateEvent extends AggregateEvent<CompositionId> {

	constructor(compositionId: CompositionId) {
		super(compositionId, 'ColumnsSetAggregateEvent');
	}

	toDomainEvent(): DomainEvent<CompositionId> {
		return new CompositionColumnsSetEvent(this.getAggregateId());
	}

}
