import { Entity } from '@generic-ui/hermes';

import { OriginId } from './origin-id';


export class OriginItemEntity extends Entity<OriginId> {

	rawData: any; // tODO

	private version: number;

	private position: number;

	constructor(id: OriginId,
				rawData: any,
				position: number,
				version: number = 0) {
		super(id);
		this.rawData = rawData;
		this.position = position;
		this.version = version;
	}

	getData(): any {
		return this.rawData;
	}

	getVersion(): number {
		return this.version;
	}

	getPosition(): number {
		return this.position;
	}

	bumpVersion(): void {
		this.version += 1;
	}

	setPosition(position: number): void {
		this.position = position;
	}

	clone(): OriginItemEntity {

		const rawData = { ...this.rawData };

		return new OriginItemEntity(this.getId(), rawData, this.version);
	}

}
