import { ComponentFactoryResolver, ComponentRef, Injector, TemplateRef, Type } from '@angular/core';
import { TemplatesComponent } from './templates.component';
import { DataType } from '../../../../structure/domain/structure/field/data-type/data-type';
import { CellView } from '../../../domain/column/cell-view';


export abstract class TemplateFactory<T extends TemplatesComponent> {

	private templates: Map<DataType | CellView, TemplateRef<any>> = new Map<DataType | CellView, TemplateRef<any>>();

	private templatesComponent: ComponentRef<T> = null;

	protected constructor(private readonly templatesComponentDefinition: Type<T>,
						  private readonly componentFactoryResolver: ComponentFactoryResolver) {
		this.createTemplatesComponent();
	}

	abstract generateMapKeys(): Array<DataType | CellView>;

	getTemplates(): Map<DataType | CellView, TemplateRef<any>> {
		return this.templates;
	}

	destroy(): void {
		if (this.templatesComponent) {
			this.templatesComponent.destroy();
			this.templatesComponent = null;
		}
	}

	private createTemplatesComponent(): void {

		const componentFactory = this.componentFactoryResolver.resolveComponentFactory(this.templatesComponentDefinition);

		const injector: Injector = Injector.create({ providers: [] });

		this.templatesComponent = componentFactory.create(injector);

		this.generateMap();
	}

	private generateMap(): void {
		this.generateMapKeys()
			.forEach((type: DataType | CellView) => {
				this.findAndSetTemplate(type);
			});
	};

	private findAndSetTemplate(dt: DataType | CellView): void {

		const template = this.templatesComponent.instance.getTemplate(dt);

		this.templates.set(dt, template);
	}

}
